                  'current': (currentTab === 'security'),
                  'complete': securityComplete
                }"
                @click="tabClick('security')"
              >
                <div class="account-icon">
                  <span class="si si-lock-f"></span>
                  <span class="success-check">
                    <!-- Checkmark -->
                    <svg class="icon-check animated" width="6" height="5" viewBox="0 0 6 5" xmlns="http://www.w3.org/2000/svg">
                      <path d="M1 2.09l1.546 1.606L5.43 1" stroke="#fff" fill="none" fill-rule="evenodd"/>
                    </svg>
                  </span>
                </div>
                <div class="set-up-label">Update Your Security Question</div>
              </div>

              <div
                class="set-up-item"
                :class="{
                  'current': (currentTab === 'paperless'),
                  'complete': paperlessComplete
                }"
                @click="tabClick('paperless')"
              >
                <div class="account-icon">
                  <span class="si si-leaf"></span>
                  <span class="success-check">
                    <!-- Checkmark -->
                    <svg class="icon-check animated" width="6" height="5" viewBox="0 0 6 5" xmlns="http://www.w3.org/2000/svg">
                      <path d="M1 2.09l1.546 1.606L5.43 1" stroke="#fff" fill="none" fill-rule="evenodd"/>
                    </svg>
                  </span>
                </div>
                <div class="set-up-label">Reduce Clutter, Go Paperless</div>
              </div>

            </div>

            <transition name="fade-collapse-btn">
              <kite-button
                v-if="!inModalView"
                type="outline"
                size="full"
                :onClick="() => (canShowPostSuccess) ? dismissSetUp() : openModal()"
              >
                <template v-if="!canShowPostSuccess">
                  Update All
                </template>
                <template v-else>
                  Dismiss
                </template>
              </kite-button>
            </transition>

<!-- SUCCESS -->
            <transition name="fade-slower">
              <div
                v-if="canShowSuccess && !canShowPostSuccess"
                class="setup-success"
              >
                <div class="check-icon">
                  <!-- Checkmark -->
                  <svg class="icon-check animated" viewBox="0 0 24 19" xmlns="http://www.w3.org/2000/svg">
                    <path d="M1.64 10.414L7.438 16 22.364 1.495" fill-rule="nonzero" stroke="#fff" stroke-width="4" fill="none"/>
                  </svg>
                </div>
              </div>
            </transition>
          </div>
        </div> <!-- end .account-setup-body -->


<!-- TABS -->
        <transition name="fade" mode="out-in">
          <div
            key="contact"
            v-if="currentTab === 'contact' && inModalView"
            class="setup-tab contact-tab"
          >
            <div class="setup-title">
              <h2>Update Your Primary Contact Info</h2>
              <p>Please confirm that the following contact info is correct.</p>
            </div>
            <div class="setup-form">
              <div class="setup-form-section">
                <kite-formatted-input
                  formatType="phone"
                  id="contactPhone"
                  label="Phone Number"
                  :value="contactPhone"
                  v-on:cleave-input="onPhoneInputChange"
                />
              </div>
              <div
                class="setup-form-section action-row-trigger skinny-section"
                @click="showPhoneTypeActionList"
              >
                <div class="label">
                  Phone Type
                </div>
                <div class="value">
                  {{contactPhoneType || 'Select a phone type...'}}
                </div>
              </div>
              <div class="setup-form-section">
                <kite-text-input
                  id="contactEmail"
                  label="Email Address"
                  type="email"
                  :value="contactEmail"
                  :onChange="onInputChange"
                />
              </div>
            </div>
          </div>

<!--  -->

          <div
