              <p>Your security question is used to verify your identity when recovering or changing account information, like your password.</p>
            </div>
            <div class="setup-form">
              <div
                class="setup-form-section action-row-trigger skinny-section"
                @click="showSecurityQuestionActionList"
              >
                <div class="label">
                  Security Question
                </div>
                <div class="value">
                  {{securityQuestion || 'Select a security question...'}}
                </div>
              </div>
