              <p>Reduce clutter and keep your personal info more secure by enrolling in paperless billing. You’ll automatically get a notification when your bill is ready to view online. You’ll also no longer receive a paper bill.</p>
            </div>
            <div class="setup-form">
              <div class="setup-form-section skinny-section">
                <kite-radio
                  label="Yes - Enroll in Paperless Billing"
                  :checked="usePaperless"
                  id="paperlessEnroll"
                  name="usePaperless"
                  value="true"
                  :onChange="onPaperlessChange"
                />
              </div>
              <div class="setup-form-section skinny-section">
                <kite-radio
                  label="No - Do not enroll in Paperless Billing"
                  :checked="!usePaperless"
