              size="full"
              :onClick="saveAndContinue"
            >
              <template v-if="currentTab !== 'paperless'">
                Save and Continue
              </template>
              <template v-else>
                Save
              </template>
            </kite-button>
            <kite-button
              v-if="currentTab !== 'paperless'"
              type="outline"
              size="full"
              :onClick="nextStep"
            >
              Skip This
            </kite-button>
            <kite-button
              v-if="currentTab !== 'contact'"
              type="standalone-link"
              size="full"
