<script>

import { 
  KiteButton,
  KiteTextInput,
  KiteSelect,
  KiteRadio,
  KiteFormattedInput,
} from '@kite/vue-kite';
import InputPasswordToggle from './input-password-toggle.vue';
import { mapState } from 'vuex';

const tabs = [
  'contact',
  'security',
  'paperless',
];


export default {
  name: 'account-set-up',
  components: {
    KiteButton,
    KiteTextInput,
    KiteSelect,
    KiteRadio,
    KiteFormattedInput,
    InputPasswordToggle,
  },

//-----

  data: function() {
    return {

      // General
      currentTab: tabs[0],
      scrollElement: null,
      canShowSuccess: false,
      canShowPostSuccess: false,
      canHideSetUp: false,

      // Animations
      elHeight: 'auto',
      elWidth: 'auto',

      cardPosition: 'relative',
      cardTop: 0,
      cardLeft: 0,
      cardHeight: 'auto',
      cardWidth: 'auto',

      inModalView: false,
      isClosing: false,

      // Form
      contactPhone: '',
      contactPhoneType: '',
      contactEmail: '',
      contactComplete: false,

      securityQuestion: '',
      securityAnswer: '',
      securityComplete: false,

      usePaperless: false,
      paperlessComplete: false,

      phoneTypeOptions: [
        {
          value: 'Mobile',
          selected: false,
        },
        {
          value: 'Non-Mobile',
          selected: false,
        }
      ],
      securityQuestionOptions: [
        {
          value: 'What is the name of your first pet?',
          selected: false,
        },
        {
          value: 'In what city were you born?',
          selected: false,
        },
        {
          value: 'What is your mother\'s maiden name?',
          selected: false,
        },
        {
          value: 'What street did you grow up on?',
          selected: false,
        }
      ],
    }
  },

//-----

  computed: {
  },

//-----

  methods: {
    saveElRefs: function() {
      this.scrollElement = this.$el.querySelector('.content-scroll');
    },

    // Animations
    //--------------
    setBaseSize: function() {
      if (this.$el) {
        const elBox = this.$el.querySelector('.content').getBoundingClientRect();

        this.elHeight = elBox.height + 'px';
        this.elWidth = elBox.width + 'px';
        this.cardPosition = 'absolute';
      }
    },
    fixCardInPlace: function() {
      const elBox = this.$el.getBoundingClientRect();

      this.cardPosition = 'fixed';
      this.cardTop = elBox.top + 'px';
      this.cardLeft = this.$el.offsetLeft + 'px';
      this.cardHeight = elBox.height + 'px';
      this.cardWidth = elBox.width + 'px';
    },
    animateCardFullscreen: function() { // Set's CSS on .content element
      window.setTimeout(() => {
        this.inModalView = true;
        this.$store.commit('lockScroll');

        this.cardTop = 0;
        this.cardLeft = 0;
        this.cardWidth = '100%';
        this.cardHeight = '100%';
      }, 200);
    },
    resetCard: function() {
      this.inModalView = false;
      this.isClosing = true;
      this.fixCardInPlace();

      window.setTimeout(() => {
        this.cardPosition = 'absolute';
        this.cardTop = 0;
        this.cardLeft = 0;
        this.cardWidth = '100%';
        this.cardHeight = '100%';

        this.$store.commit('unlockScroll');
        this.isClosing = false;
      }, 700);
    },
    openModal: function() {
      this.fixCardInPlace();

      this.$nextTick(() => {
        this.animateCardFullscreen();
      });
    },
    showSuccess: function() {
      this.canShowSuccess = true;
      this.resetCard();

      window.setTimeout(() => {
        if (this.contactComplete && this.securityComplete && this.paperlessComplete) {
          this.canShowPostSuccess = true;
        }
        this.currentTab = tabs[0];
        this.canShowSuccess = false;
      }, 2500);
    },
    dismissSetUp: function() {
      this.canHideSetUp = true;
      this.elHeight = '0px';
    },

    // Tabs
    //--------------
    tabClick: function(tab) {
      this.currentTab = tab;

      if (!this.inModalView) {
        // show modal
        this.openModal();
      }
    },

    // Forms
    //--------------
    onInputChange: function(e) {
      this[e.target.name] = e.target.value;
    },
    onPhoneInputChange: function(val) {
      this.contactPhone = val;
    },
    onPaperlessChange: function(e) {
      this.usePaperless = JSON.parse(e.target.value);
    },
    getCurrentStep: function() {
      return tabs.indexOf(this.currentTab)
    },
    previousStep: function() {
      const step = this.getCurrentStep();
      this.currentTab = tabs[step - 1];
      this.scrollFormToTop();
    },
    nextStep: function() {
      this.scrollFormToTop();
      const step = this.getCurrentStep();
      this.currentTab = tabs[step + 1];
    },
    saveAndContinue: function() {
      this[this.currentTab + 'Complete'] = true;

      if (this.currentTab !== 'paperless') {
        this.nextStep();
      } else {
        // hide modal and show success
        this.showSuccess();
      }
    },
    scrollFormToTop: function() {
      this.scrollElement.scrollTo({top: 0, behavior: 'smooth'});
    },

    onPhoneTypeSelect: function(item) {
      this.contactPhoneType = item.value;
      this.phoneTypeOptions = this.setSelectedOption(this.phoneTypeOptions, item);
    },
    showPhoneTypeActionList: function() {
      this.$store.commit('showActionSheet', {
        title: 'Select Phone Type',
        items: this.phoneTypeOptions,
        onSelect: this.onPhoneTypeSelect.bind(this),
      });
    },

    onSecurityQuestionSelect: function(item) {
      this.securityQuestion = item.value;
      this.securityQuestionOptions = this.setSelectedOption(this.securityQuestionOptions, item);
    },
    showSecurityQuestionActionList: function() {
      this.$store.commit('showActionSheet', {
        title: 'Select Security Question',
        items: this.securityQuestionOptions,
        onSelect: this.onSecurityQuestionSelect.bind(this),
      });
    },

    setSelectedOption: function(options, selected) {
      return options.map((option) => {
        option.selected = (option.value === selected.value);
        return option;
      });
    },
  },

  mounted: function() {
    this.saveElRefs();

    window.setTimeout(() => {
      this.setBaseSize();
    }, 200);
  },
}

</script>



