
<template>

  <div class="view view-wifi-network">

    <section class="section-recessed-card">
      <div class="recessed-card">
        <h2>Your Spectrum Network Info</h2>
        <p>{{ user.wifi.networkName }}</p>
        <p class="small">{{ user.wifi.password }}</p>
      </div>
    </section>

    <div class="link-card">
      <div
        class="link-card-item"
        @click="showEditModal"
      >
        Edit Network Info
      </div>
    </div>


    <modal-full-page
      modalTitle="Edit Network"
      :canShow="canShowEditModal"
      :onBack="hideEditModal"
      :onSave="saveNetworkInfo"
      newClass="edit-network-modal"
    >
      <p>If you change your WiFi network info, make sure to also update your WiFi settings on any connected devices. The WiFi info on your Spectrum-supplied router will no longer to accurate.</p>

      <kite-text-input
        label="WiFi Network Name (SSID)"
        id="tempNetworkName"
        :value="tempNetworkName"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />

      <kite-text-input
        label="Password"
        id="tempNetworkPassword"
        :value="tempNetworkPassword"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />
    </modal-full-page>

  </div>

</template>



<script>

import ModalFullPage from '../components/modal-full-page.vue';
import { KiteTextInput } from '@kite/vue-kite';
import { mapState } from 'vuex';


function clone(x) {
  return JSON.parse(JSON.stringify(x));
}


export default {
  name: 'wifi-network',
  components: {
    ModalFullPage,
    KiteTextInput,
  },

//-----

  data: () => ({
    canShowEditModal: false,
    tempNetworkName: '',
    tempNetworkPassword: '',
  }),

//-----

  computed: {
    ...mapState([
      'user',
    ]),
  },

//-----

  methods: {
    setTempValues() {
      this.tempNetworkName = clone(this.user.wifi.networkName);
      this.tempNetworkPassword = clone(this.user.wifi.password);
    },
    showEditModal() {
      this.setTempValues();
      this.canShowEditModal = true;
    },
    hideEditModal() {
      this.canShowEditModal = false;
    },
    onInputChange(e) {
      this[e.target.name] = e.target.value;
    },
    saveNetworkInfo() {
      this.$store.commit('saveNetworkInfo', {
        networkName: this.tempNetworkName,
        password: this.tempNetworkPassword,
      });
      this.hideEditModal();
    },
  },

}

</script>



<style lang="scss">

.view-wifi-network {
  padding-top: 40px;

  .recessed-card {
    p {
      margin: 4px;

      &:last-child {
        margin: 0;
      }
    }
  }

  .edit-network-modal {

  }
}

</style>
