
<template>

  <div class="view view-outage">

    <span class="si si-circle-caution-f outage-icon"></span>

    <h1 class="outage-title">Spectrum Internet Outage Reported</h1>

    <p class="outage-copy">We're working to get things up and running as soon as possible. Would you like us to call you when you the issue is resolved?</p>

    <kite-button
      type="primary"
      size="full"
      :onClick="() => $router.push('/outage/callback')"
    >
      Call Me
    </kite-button>

    <kite-button
      type="outline"
      size="full"
      :onClick="goBack"
    >
      Cancel
    </kite-button>

    <kite-button
      type="standalone-link"
      size="full"
      :onClick="() => canShowDismissModal = true"
    >
      Dismiss Outage
    </kite-button>


    <transition name="fade">
      <modal
        v-if="canShowDismissModal"
        title="Dismiss Outage Alert?"
        content="This alert provides the easiest way to view updates on the current outage. If you dismiss this alert, you can still view the status of this outage in the Support section of the app."
        primaryActionTitle="Dismiss"
        :onPrimaryActionClick="dismissOutageAlert"
        :onCancelClick="() => canShowDismissModal = false"
      />
    </transition>

  </div>

</template>



<script>

import { KiteButton } from '@kite/vue-kite';
import Modal from '../components/modal.vue';
import { mapState } from 'vuex';



export default {
  name: 'outage',
  components: {
    KiteButton,
    Modal,
  },

//-----

  data: function() {
    return {
      canShowDismissModal: false,
    }
  },

//-----

  computed: {
    ...mapState([
      'user'
    ]),
  },

//-----

  methods: {
    goBack: function() {
      this.$router.go(-1);
    },
    dismissOutageAlert: function() {
      this.$store.commit('cancelOutageNotification');
      this.goBack();
    },
  },
}

</script>



