      </button>
    </div>
    <div class="action-sheet-body">

      <slot></slot>

    </div>
  </div>

</template>



<script>

export default {
  name: 'make-payment-base',

  props: [
    'title',
    'show',
    'onClose',
    'onSubmit',
  ],

  methods: {
    submit: function() {
      this.onSubmit();
      this.onClose();
    }
  }
}

</script>



