
<template>

  <div
    class="loading-screen"
    :class="{
      'phase-one': phaseOne,
      'phase-two': phaseTwo,
      'phase-three': phaseThree,
      'start-tour': canStartTour,
    }"
  >

    <transition name="fade-slow">
      <div
        v-if="!canStartTour"
        class="loading-copy-container"
      >
        <h1 class="loading-title">Welcome to My&nbsp;Spectrum</h1>

        <div class="spinner-container">
          <kite-spinner />
        </div>

        <p class="loading-copy">Getting your account info...</p>
      </div>

      <button
        v-else
        class="si si-x tutorial-close"
        @click="onClose"
      ></button>
    </transition>


    <div
      class="tour-img-window"
      :class="{
        'pre-tour': !canStartTour
      }"
    >
      <div class="lottie-container">
        <!-- Lottie animation here -->
      </div>
    </div>


  <!-- Loading screen logo -->

    <transition name="fade-slow" mode="out-in">
      <div
        v-if="!canStartTour"
        class="bottom-imgs"
      >
        <div class="logo-container">
          <img 
            :src="require('../assets/images/spectrum-logo.svg')"
            alt="Spectrum Logo"
            class="loading-logo"
          />
        </div>
      </div>
    </transition>

  <!-- Copy Slider -->

    <div class="tour-copy-window">
      <div
        class="tour-copy-slider"
        @touchstart="onTouchStart"
        :style="{'transform': `translateX(${copyTranslate}px)`}"
        :class="{'no-transition': swipeDistance}"
      >
        <div
          v-for="slide in slides"
          class="tour-copy-slide"
        >
          <div v-html="slide.copy"></div>
        </div>
      </div>
    </div>

  <!-- Actions -->

    <div class="tour-slider-actions">
      <button
        class="slider-btn slider-back"
        :class="{'hide': (!currentSlide)}"
        @click="prevSlide"
      >
        Back
      </button>
      <div class="slider-indicators">
        <span
          v-for="n in slides.length"
          class="indicator"
          :class="{'active': (n-1 === currentSlide)}"
        ></span>
      </div>

      <transition name="fade" mode="out-in">
        <button
          v-if="(currentSlide+1) < slides.length"
          class="slider-btn slider-next"
          @click="nextSlide"
        >
          Next
        </button>
        <button
          v-else
          class="slider-btn slider-next"
          @click="onClose"
        >
          Done
        </button>
      </transition>
    </div>

  </div>

</template>



<script>

import TourPhone from './tour-phone.vue';
import { KiteSpinner } from '@kite/vue-kite';
import lottie from 'lottie-web';

const slides = [
  {
    copy: '<h2>Hi, Jasmine</h2><p class="intro-slide">My Spectrum makes it easy to manage your account and services, no matter where you go. Here are a few things you can&nbsp;do.</p>',
    frames: [30, 65],
  },
  {
    copy: '<p>Make a one-time payment, or save time and never miss a payment by enrolling in Auto&nbsp;Pay.</p>',
    frames: [90, 119],
    loopFrames: [120, 180]
  },
  {
    copy: '<p>Troubleshoot equipment issues, and get alerts if there’s a service outage in your&nbsp;area.</p>',
    frames: [180, 215],
    loopFrames: [216, 326]
  },
  {
    copy: '<p>Add people to your account and manage the info they can&nbsp;access.</p>',
    frames: [360, 391],
    postTransitionFrames: [392, 493]
  },
  {
    copy: '<p>Find answers to common questions, right in the&nbsp;app.</p>',
    frames: [495, 533],
    postTransitionFrames: [534, 660]
  },
];

const SWIPE_MINIMUM = 160;


export default {
  name: 'loading',
  components: {
    KiteSpinner,
    TourPhone,
  },

//------

  props: [
    'onComplete'
  ],

//------

  data: () => ({
    phaseOne: false,
    phaseTwo: false,
    phaseThree: false,
    canStartTour: false,

    slideWidth: 0,
    currentSlide: 0,

    slides,
    animation: null,
    isBack: false,

    // Touch/Swipe
    noTransitionDelay: false,
    swipeDistance: 0,
    copySliderEl: null,
    touchStartX: null,
    touchStartY: null,
  }),

//------

  computed: {
    copyTranslate: function() {
      const slideTranslate = (this.currentSlide) ? -(this.slideWidth * (this.currentSlide)) : 0;
      return (slideTranslate + this.swipeDistance);
    },
  },

//------

  methods: {

    // Initial Loader/Welcome screen
    //--------
    firstTimer: function() {
      window.setTimeout(() => {
        this.phaseOne = true;
        this.secondTimer();
      }, 800);
    },
    secondTimer: function() {
      window.setTimeout(() => {
        this.phaseTwo = true;
        this.thirdTimer();
      }, 2000);
    },
    thirdTimer: function() {
      window.setTimeout(() => {
        this.phaseThree = true;
        this.fourthTimer();
      }, 2000);
    },
    fourthTimer: function() {
      window.setTimeout(() => {
        this.canStartTour = true;

        window.setTimeout(() => {
          this.lottieNext();
        }, 1000)
      }, 1000);
    },

    // Feature Tour
    //--------
    initializeSlider: function() {
      this.slideWidth = this.$el.offsetWidth;
    },
    nextSlide: function() {
      if (this.currentSlide+1 < this.slides.length) {
        this.currentSlide++;
        this.lottieNext();
      }
    },
    prevSlide: function() {
      if (this.currentSlide) {
        this.lottieBack();
        this.currentSlide--;
      }
    },
    onClose: function() {
      this.$router.push('/home');
    },

    // Lottie Controls
    //----------
    initializeLottie: function() {
      this.animation = lottie.loadAnimation({
        container: this.$el.querySelector('.lottie-container'),
        renderer: 'svg',
        loop: false,
        autoplay: false,
        path: '/bodymovin.json',
      });
    },
    lottieNext() {
      this.isBack = false;
      this.playSegment();
    },
    lottieBack() {
      this.isBack = true;
      this.playSegment();
    },
    playSegment() {
      this.animation.onComplete = null;

      const segment = slides[this.currentSlide];
      this.animation.playSegments(this.isBack ? [...segment.frames].reverse() : segment.frames, true);

      // If need to loop or show post-transition animation...
      if (
        (this.isBack && slides[this.currentSlide-1] && slides[this.currentSlide-1].loopFrames) ||
        (!this.isBack && segment.loopFrames)
      ) {
        this.animation.onComplete = this.playLoopSegment;
      } else if (segment.postTransitionFrames) {
        this.animation.onComplete = this.playPostTransition;
      }
    },
    playLoopSegment() {
      this.animation.playSegments(slides[this.currentSlide].loopFrames, true);
    },
    playPostTransition() {
      this.animation.playSegments(slides[this.currentSlide].postTransitionFrames, true);
      this.animation.onComplete = null;
    },

    // Swipe/Touch
    //-------------
    cacheSlider() {
      this.copySliderEl = this.$el.querySelector('.tour-copy-slider');
    },
    onTouchStart: function(e) {
      this.copySliderEl.addEventListener('touchmove', this.onTouchMove);
      this.copySliderEl.addEventListener('touchend', this.onTouchEnd);
      this.copySliderEl.addEventListener('touchcancel', this.onTouchCancel);

      this.initializeTouch(e);
    },
    initializeTouch: function(e) {
      const touchE = e.touches[0];
      this.touchStartX = touchE.clientX;
      this.touchStartY = touchE.clientY;
    },
    onTouchMove: function(e) {
      const touchE = e.touches[0];
      const xDelta = touchE.clientX - this.touchStartX;
      const yDelta = touchE.clientY - this.touchStartY;
      const swipingLeft = (xDelta < 0) && (Math.abs(yDelta) < Math.abs(xDelta));

      if (Math.abs(xDelta) < SWIPE_MINIMUM) {
        this.swipeDistance = xDelta;
      } else {
        const overswipe = Math.abs(xDelta) - SWIPE_MINIMUM;
        const extra = (Math.log(overswipe)/Math.log(1.2));
        this.swipeDistance = (swipingLeft) ? -SWIPE_MINIMUM - extra : SWIPE_MINIMUM + extra;
      }
    },
    onTouchEnd: function(e) {
      this.removeTouchEvents();

      if (Math.abs(this.swipeDistance) >= SWIPE_MINIMUM/2) {
        if (this.swipeDistance > 0) {
          this.prevSlide();
        } else {
          this.nextSlide();
        }
      }

      this.swipeDistance = 0;
    },
    onTouchCancel: function(e) {
      this.removeTouchEvents();
      this.swipeDistance = 0;
    },
    removeTouchEvents: function() {
      this.copySliderEl.removeEventListener('touchmove', this.onTouchMove);
      this.copySliderEl.removeEventListener('touchend', this.onTouchEnd);
      this.copySliderEl.removeEventListener('touchcancel', this.onTouchCancel);
    }
  },

//------

  mounted: function() {
    this.firstTimer();
    this.initializeSlider();
    this.initializeLottie();
    this.cacheSlider();
  }

}

</script>



