
<template>

  <div
    class="voicemail-accordion"
    :class="{
      'unread': unread,
      'open': isOpen
    }"
  >

    <div class="swipe-row">

      <div class="swipe-actions">
        <button
          class="swipe-btn make-btn"
          @click="toggleRead"
        >
          <template v-if="rawMessage.unread">
            Mark as Read
          </template>
          <template v-else>
            Mark as Unread
          </template>
        </button>
        <button
          class="swipe-btn delete-btn"
          @click="deleteMe"
        >
          Delete
        </button>
      </div>

      <div
        class="swipe-row-top"
        @touchstart="onTouchStart"
        :class="{'use-transition': useTransition}"
        :style="{'transform': `translateX(${swipePosition}px)`}"
      >
        <div class="swipe-left">
          <div class="voicemail-number swipe-row-title">
            <span class="unread-indicator"></span>
            {{number}}
          </div>
          <div class="voicemail-time small swipe-row-detail">
            {{date}} at {{time}}
          </div>
        </div>

        <div class="swipe-right">
          {{convertedDuration}}
        </div>

        <span
          class="si si-chevron-down swipe-row-action-icon"
          @click="openDrawer"
        ></span>

      </div>
    </div>

<!-- Open Content -->
    <div
      class="accordion-drawer"
      :style="{height: currentHeight+'px'}"
    >
      <div class="accordion-content">

        <h1 class="call-phone">
          {{number}}
        </h1>
        <p class="call-date">
          {{date}} at {{time}}
        </p>

      <!--  -->

        <div class="scrubber-row">

          <button
            class="play-button"
            @click="togglePlay"
          >
            <span
              v-if="audioPlaying"
              class="si si-pause-f"
            ></span>
            <span
              v-else
              class="si si-play-f"
            ></span>
          </button>

          <div class="call-scrubber">
            <div class="scrubber-progress-time scrubber-time">
              {{audioPosition}}
            </div>
            <div class="scrubber-track">
              <div
                class="scrubber-progress-track"
                :style="{'width': audioProgress+'%'}"
              >
                <div class="scrubber-bar"></div>
              </div>
            </div>
            <div class="scrubber-time-left scrubber-time">
              -{{audioRemaining}}
            </div>
          </div>

          <audio
            src="/jm-didyouknow.mp3"
            type="audio/mp3"
            @timeupdate="onTimeUpdate"
            @loadedmetadata="onLoadedData"
            @ended="onEnd"
          ></audio>

        </div>

      <!--  -->
  
        <div class="call-action-row">
          <button class="share-btn">
            Share
          </button>
          <div class="center-buttons">
            <button
              class="circle-button"
              :class="{active: bluetoothOn}"
              @click="() => bluetoothOn = !bluetoothOn"
            >
              <svg xmlns="http://www.w3.org/2000/svg" width="14" height="23" viewBox="0 0 14 23">
                <path fill="#FFF" fill-rule="nonzero" d="M6.246 0h1.21l5.872 5.871L7.71 11.5l5.617 5.629L7.457 23h-1.21V12.965L.858 18.352 0 17.492 5.992 11.5 0 5.508l.86-.86 5.386 5.387V0zm1.21 1.707v8.328l4.165-4.164-4.164-4.164zm0 19.586l4.165-4.164-4.164-4.164v8.328z"/>
              </svg>
            </button>
            <button
              class="circle-button"
              :class="{active: speakerOn}"
              @click="() => speakerOn = !speakerOn"
            >
              <span class="si si-speaker-sound-f"></span>
            </button>
          </div>
          <button
            class="delete-btn"
            @click="deleteMe"
          >
            Delete
          </button>
        </div>

        <div
          class="loader-overlay"
          :class="{'show': canShowLoader}"
        >
          <div class="spinner-container">
            <kite-spinner />
          </div>
          <p>Loading Voicemail...</p>
        </div>

      </div> <!-- end .accordion-content -->
    </div>

  </div>

</template>



<script>

import { KiteSpinner } from '@kite/vue-kite';


export default {
  name: 'voicemail-accordion',
  components: {
    KiteSpinner,
  },

//------

  props: [
    'unread',
    'number',
    'date',
    'time',
    'duration', // in seconds

    'onOpen', // fn
    'anotherMessageIsOpen', // bool
    'forceAllMessagesClose', // fn
    'forceClose', // bool

    'rawMessage',
    'onDelete',
    'onToggleRead',
  ],

//------

  data: function() {
    return {
      isOpen: false,
      openHeight: 0,
      canShowLoader: false,

      bluetoothOn: false,
      speakerOn: false,

      // Sound player
      audioPlaying: false,
      audioEl: null,
      audioPosition: '00:00',
      audioRemaining: '00:00',
      audioProgress: 0,

      // Touches
      touchStartX: null,
      swipePosition: 0,
      useTransition: false,
    };
  },

//------

  computed: {
    currentHeight: function() {
      return (this.isOpen) ? this.openHeight : 0;
    },
    convertedDuration: function() {
      return this.getDuration(this.duration);
    }
  },

//------

  watch: {
    audioPlaying: function(newVal, oldVal) {
      if (newVal) {
        this.audioEl.play();
      } else {
        this.audioEl.pause();
      }
    },
    forceClose: function(newVal, oldVal) {
      if (newVal && !oldVal && this.isOpen) {
        this.closeDrawer();
      }
    },
  },

//------

  methods: {
    setHeights: function() {
      const contentEl = this.$el.querySelector('.accordion-content');
      const actionRowEl = this.$el.querySelector('.swipe-row');
      const actionRowHeight = actionRowEl.getBoundingClientRect().height;
      this.openHeight = contentEl.getBoundingClientRect().height - actionRowHeight;
    },

    openDrawer: function() {
      if (!this.swipeLocked) {
        this.forceAllMessagesClose();

        this.$nextTick(() => {
          this.onOpen(this.rawMessage);
          this.isOpen = true;
          this.canShowLoader = true;

          window.setTimeout(() => {
            this.canShowLoader = false;
          }, 1800);
        });
      }
    },
    closeDrawer: function() {
      this.isOpen = false;
    },

    getDuration: function(time) {
      const min = Math.round(time/60);
      const sec = Math.round(time % 60);

      return `${(min < 10) ? '0'+min : min}:${(sec < 10) ? '0'+sec : sec}`;
    },
    deleteMe: function() {
      this.onDelete(this.rawMessage);
    },
    toggleRead: function() {
      this.onToggleRead(this.rawMessage);
    },


    // Sound player
    //---------------
    initializePlayer: function() {
      this.audioEl = this.$el.querySelector('audio');
    },
    togglePlay: function() {
      this.audioPlaying = !this.audioPlaying;
    },
    onLoadedData: function() {
      this.onTimeUpdate();
    },
    onTimeUpdate: function() {
      this.audioProgress = Math.floor(this.audioEl.currentTime/this.audioEl.duration * 100);
      this.audioPosition = this.getDuration(this.audioEl.currentTime);
      this.audioRemaining = this.getDuration(this.audioEl.duration - this.audioEl.currentTime);
    },
    onEnd: function() {
      this.togglePlay();
      this.audioEl.currentTime = 0;
    },

    // Touch/Swipe
    //---------------
    onTouchStart: function(e) {
      this.$el.addEventListener('touchmove', this.onTouchMove);
      this.$el.addEventListener('touchend', this.onTouchEnd);
      this.$el.addEventListener('touchcancel', this.onTouchCancel);

      this.initializeTouch(e);
    },
    initializeTouch: function(e) {
      const touchE = e.touches[0];
      this.touchStartX = touchE.clientX;
      this.touchStartY = touchE.clientY;
    },
    onTouchMove: function(e) {
      const touchE = e.touches[0];
      const xDelta = touchE.clientX - this.touchStartX;
      const yDelta = touchE.clientY - this.touchStartY;
      const swipingLeft = (xDelta < 0) && (Math.abs(yDelta) < Math.abs(xDelta));
      const swipingRight = (xDelta > 0) && (Math.abs(yDelta) < Math.abs(xDelta));

      if (!this.swipeLocked) {
        if (swipingLeft) {
          // Move accordion over
          if (xDelta > -160) {
            this.swipePosition = xDelta;
          } else {
            const overswipe = Math.abs(xDelta) - 160;
            const extra = (Math.log(overswipe)/Math.log(1.2));
            this.swipePosition = -160 - extra;
          }
        }
      } else {
        if (swipingLeft) {
          const extra = (Math.log(xDelta)/Math.log(1.2));
          this.swipePosition = -160 - extra;
        } else if (swipingRight) {
          this.swipePosition = -160 + xDelta;
        }
      }
    },
    onTouchEnd: function(e) {
      this.removeTouchEvents();
      this.useTransition = true;

      this.$nextTick(() => {
        if (Math.abs(this.swipePosition) >= 160) {
          this.swipePosition = -160;
          this.swipeLocked = true;
        } else {
          this.swipePosition = 0;
          this.swipeLocked = false;
        }
      });

      window.setTimeout(() => {
        this.useTransition = false;
      }, 300)
    },
    onTouchCancel: function(e) {
      this.removeTouchEvents();

      this.useTransition = true;
      this.$nextTick(() => {
        this.swipePosition = 0;
        this.swipeLocked = false;
      });

      window.setTimeout(() => {
        this.useTransition = false;
      }, 300);
    },
    removeTouchEvents: function() {
      this.$el.removeEventListener('touchmove', this.onTouchMove);
      this.$el.removeEventListener('touchend', this.onTouchEnd);
      this.$el.removeEventListener('touchcancel', this.onTouchCancel);
    }
  },

//------

  mounted: function() {
    this.setHeights();
    this.initializePlayer();
  }

}

</script>



