
<template>

  <div class="view view-outage-callback">

    <div class="intro-card">
      <p>To receive a call back about the current outage, please select a saved contact from your account or add a new one for us to call.</p>
    </div>

    <div class="contact-card link-card">
      <div
        v-for="contact in contacts"
        class="link-card-item callback-contact"
        @click="() => setCallback(contact)"
      >
        {{contact.name}} {{contact.number}}
      </div>
    </div>

    <div class="action-container">
      <kite-button
        type="outline"
        size="full"
        :onClick="() => canShowAddModal = true"
      >
        Add Contact
      </kite-button>
    </div>


    <transition name="fade">
      <modal
        v-if="canShowCallbackModal"
        title="Confirm Call Back Number"
        :content="`We'll call you at ${callbackContact.number} when the outage is resolved.`"
        primaryActionTitle="OK"
        :onPrimaryActionClick="confirmCallback"
        :onCancelClick="cancelCallback"
      />
    </transition>

    <modal-full-page
      :canShow="canShowAddModal"
      modalTitle="Add Contact"
      :onBack="cancelAddContact"
      :onSave="saveAddContact"
    >
      <kite-formatted-input
        formatType="phone"
        label="Phone Number"
        :value="newPhoneNumber"
        v-on:cleave-input="phoneInput"
      />
    </modal-full-page>

  </div>

</template>



<script>

import {
  KiteButton,
  KiteFormattedInput,
} from '@kite/vue-kite';
import Modal from '../components/modal.vue';
import ModalFullPage from '../components/modal-full-page.vue';
import { mapState } from 'vuex';

const contacts = [
  {
    name: 'Alice',
    number: '(123) 345-5673'
  },{
    name: 'Amanda',
    number: '(120) 727-1938'
  },{
    name: 'Bobby',
    number: '(419) 100-7660'
  },{
    name: 'Samantha',
    number: '(767) 963-8517'
  },
];


export default {
  name: 'outage-callback',
  components: {
    KiteButton,
    Modal,
    ModalFullPage,
    KiteFormattedInput,
  },

  data: function() {
    return {
      contacts,
      canShowCallbackModal: false,
      callbackContact: null,

      canShowAddModal: false,
      newPhoneNumber: '',
    }
  },

//-----

  computed: {
    ...mapState([
      'user'
    ]),
  },

//-----

  methods: {
    setCallback: function(contact) {
      this.callbackContact = contact;
      this.canShowCallbackModal = true;
    },
    cancelCallback: function() {
      this.callbackContact = null;
      this.canShowCallbackModal = false;
    },
    confirmCallback: function() {
      this.$router.go(-1);
    },

    phoneInput: function(val) {
      this.newPhoneNumber = val;
    },
    cancelAddContact: function() {
      this.canShowAddModal = false;
      this.newPhoneNumber = '';
    },
    saveAddContact: function() {
      this.canShowAddModal = false;
      this.contacts.push({
        name: '',
        number: this.newPhoneNumber,
      });
    },
  },
}

</script>



