
<template>

  <div class="view view-details">

    <div class="title-area">
      <div class="due-subtitle">Amount Due</div>
      <div class="amount-due">
        <sup>$</sup>{{latestBill.balance.toFixed(2)}}
      </div>
      <div class="due-subtitle">Due by {{latestBill.dueDateFull}}</div>
    </div>

    <div class="tabs-area">
      
      <TabControls
        :selected="selectedTab"
        :onTabChange="onTabChange"
        :messageNumber="latestBill.messages.length"
      />


      <div
        class="tabs-window"
        :class="{'messages': selectedTab === 'messages'}"
        :style="{height: tabHeight+'px'}"
      >
        <div class="tabs-slider">


          <div class="tab-page bill-details-tab">
            
            <div class="bill-details">
              <div class="bill-section">
                <h4>Previous Account Activity</h4>

                <DetailRow
                  v-for="(item, i) in latestBill.details.prevActivity"
                  :key="i"
                  :detail="item"
                />

              </div>
              <div class="bill-section">
                <h4>Current Charges
                  <span> - Service from {{latestBill.serviceRangeFull}}</span>
                </h4>

                <DetailRow
                  v-for="(item, i) in latestBill.details.serviceDetails"
                  :key="i"
                  :detail="item"
                />

              </div>
            </div>

            <div class="bill-card-total-bar">
              <div class="total-left">
                <div class="due-title">Amount Due by {{latestBill.dueDateFull}}</div>
              </div>
              <div class="total-right">
                <div class="due-value">${{latestBill.balance}}</div>
              </div>
            </div>

            <div class="bill-card-download-bar">
              <a class="download-link" :href="latestBill.downloadLink" target="_blank">
                <span class="si si-pdf"></span>
                <span class="download-label">View PDF Statement</span>
              </a>
            </div>

          </div>


          <div class="tab-page bill-messages-tab">
            <div
              v-for="message in latestBill.messages"
              class="message"
            >
              <span class="title">{{message.title}}</span>
              <span class="body">{{message.body}}</span>
            </div>
          </div>


        </div>
      </div> <!-- end .tab-window -->

    </div>


    <CompareCard />

  </div>

</template>



<script>

import CompareCard from '../components/compare-card.vue';
import TabControls from '../components/tab-controls.vue';
import DetailRow from '../components/detail-row.vue';
import Bills from '../data/bills';

import { mapState } from 'vuex';


export default {
  name: 'bill-details',
  components: {
    CompareCard,
    TabControls,
    DetailRow,
  },

//-----

  data: function() {
    return {
      bills: Bills,
      latestBill: Bills[0],
      selectedTab: 'details', // || 'messages'

      detailsHeight: 0,
      messagesHeight: 0,
      tabHeight: 0,
    }
  },

//-----

  // computed: {
  //   ...mapState([
  //     'currentBill',
  //   ]),
  // },

//-----

  methods: {
    onTabChange: function(val) {
      this.selectedTab = val;
      this.tabHeight = (val === 'details') ? this.detailsHeight : this.messagesHeight;
    }
  },

//-----

  mounted: function() {
    this.$store.commit('setCurrentBill', this.latestBill);

    // Get heights of tabs
    this.$nextTick(() => {
      const billTabEl = this.$el.querySelector('.bill-details-tab');
      this.detailsHeight = billTabEl.scrollHeight;
      // console.log('detailsHeight: ', billTabEl.scrollHeight);

      const messagesTabEl = this.$el.querySelector('.bill-messages-tab');
      this.messagesHeight = messagesTabEl.scrollHeight;
      // console.log('detailsHeight: ', messagesTabEl.scrollHeight);

      this.onTabChange('details');
    });
  },

}

</script>



