<template>
  <div class="view view-tv-upgrade-purchase">
    <div class="full-card intro-card">
      <h2>Complete Your Purchase</h2>
      <p>
        Please accept the purchase agreement to complete your upgrade. This
        charge will be included on your next bill, and you can  cancel anytime.
      </p>
    </div>

    <div class="full-card price-card">
      <div class="label">Total</div>
      <div class="values">
        <div class="price">$24.99/mo</div>
        <div class="price-footnote">Excludes Taxes and Fees</div>
      </div>
    </div>

    <div class="actions-container">
      <p>
        By selecting Purchase, you agree to Spectrum’s
        <a href>Terms & Conditions</a> and <a href>Privacy Policy</a>.
      </p>
      <kite-button type="primary" size="full" :onClick="onPurchase">
        Purchase
      </kite-button>
      <kite-button type="standalone-link" size="full" :onClick="onCancelClick">
        Cancel
      </kite-button>
    </div>

    <!-- Loader -->

    <transition name="fade">
      <div v-if="submittingPurchase" class="loader-spinner">
        <div class="spinner-container">
          <kite-spinner />
        </div>
      </div>
    </transition>

    <!-- Success Modal -->

    <modal-full-page
      class="success-modal"
      :canShow="canShowSuccess"
      modalTitle="Purchase Complete"
      :onSave="onDone"
      saveBtnCopy="Done"
    >
      <section class="section-recessed-card">
        <div class="recessed-card">
          <div class="success-check">
            <!-- Checkmark -->
            <svg
              class="icon-check animated"
              width="6"
              height="5"
              viewBox="0 0 6 5"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M1 2.09l1.546 1.606L5.43 1"
                stroke="#fff"
                fill="none"
                fill-rule="evenodd"
              />
            </svg>
          </div>

          <h2 class="subsection-title">Welcome to Spectrum  TV Stream</h2>
          <p>
            Your upgrade is complete! Your new TV experience is ready and
            available on all your favorite devices.
          </p>

