              target="_blank"
              >Watch Now</a
            >
          </p>
        </div>
      </section>
    </modal-full-page>

    <!-- Cancel Modal -->

    <transition name="fade">
      <modal
        v-if="canShowCancelModal"
        title="Cancel Order?"
        content="You’re just one step away from watching TV. Are you sure you want to cancel?"
        primaryActionTitle="Complete Order"
        cancelActionTitle="Cancel Order"
        :onPrimaryActionClick="onCancelCancel"
        :onCancelClick="onCancelProceed"
      ></modal>
    </transition>
  </div>
</template>

<script>
import { KiteButton, KiteLinkButton, KiteSpinner } from '@kite/vue-kite';
import ModalFullPage from '../components/modal-full-page.vue';
import Modal from '../components/modal.vue';

export default {
  name: 'tv-upgrade-purchase',
  components: {
    KiteButton,
    KiteLinkButton,
    KiteSpinner,
    ModalFullPage,
    Modal
  },

  data: () => ({
    canShowSuccess: false,
    submittingPurchase: false,
    canShowCancelModal: false
  }),

  //-----

  methods: {
    onPurchase() {
      this.submittingPurchase = true;

      this.$store.commit('purchaseTV');

      window.setTimeout(() => {
        this.submittingPurchase = false;
        this.canShowSuccess = true;
      }, 3000);
    },
    onDone() {
      this.$router.push('/home');
    },
    onCancelClick() {
      this.canShowCancelModal = true;
    },
    onCancelCancel() {
      this.canShowCancelModal = false;
    },
    onCancelProceed() {
      this.onDone();
    }
  }
};
</script>

