
<template>

  <div class="swipe-row">

    <!-- Hidden Buttons -->
    <div class="swipe-actions">
      <button
        class="swipe-btn make-btn"
        @click="primaryBtnAction"
        :style="{'background-color': primaryBtnColor}"
      >
        {{ primaryBtnLabel }}
      </button>
      <button
        class="swipe-btn delete-btn"
        @click="secondaryBtnAction"
        :style="{'background-color': secondaryBtnColor}"
      >
        {{ secondaryBtnLabel }}
      </button>
    </div>


    <!-- Top Swipe Row -->
    <div
      class="swipe-row-top"
      @touchstart="onTouchStart"
      @click="onClick"
      :class="{
        'use-transition': useTransition,
        'animate-swipe': canShowSwipeAffordance,
      }"
      :style="{'transform': `translateX(${swipePosition}px)`}"
    >

      <div
        v-if="rowIcon"
        class="row-icon"
      >
        <img :src="rowIcon" alt="device icon">
      </div>
      <div class="swipe-left">
        <div class="swipe-row-title">
          {{ title }}
        </div>
        <div class="small swipe-row-detail">
          {{ details }}
        </div>
      </div>

      <div
        class="swipe-right"
        :class="[rightValueClass]"
      >
        {{ rightValue }}
      </div>

      <span class="si si-chevron-right swipe-row-action-icon"></span>
    </div>

  </div>

</template>



<script>

export default {
  name: 'swipe-row',
  components: {
  },

//------

  props: [
    'title',
    'details',
    'rowIcon',
    'rightValue',
    'rightValueClass',
    'primaryBtnLabel',
    'primaryBtnAction',
    'primaryBtnColor',
    'secondaryBtnLabel',
    'secondaryBtnAction',
    'secondaryBtnColor',
    'onClick',
  ],

//------

  data: () => ({
    canShowSwipeAffordance: false,

    // Touches
    touchStartX: null,
    touchStartY: null,
    swipePosition: 0,
    useTransition: false,
    swipeLocked: false,
  }),

//------

  methods: {
    checkToShowSwipeAffordance() {
      if (!this.$el.previousElementSibling) {
        // Is first child...
        this.canShowSwipeAffordance = true;
      }
    },

    // Touch/Swipe
    //---------------
    onTouchStart: function(e) {
      e.target.addEventListener('touchmove', this.onTouchMove);
      e.target.addEventListener('touchend', this.onTouchEnd);
      e.target.addEventListener('touchcancel', this.onTouchCancel);

      this.initializeTouch(e);
    },
    initializeTouch: function(e) {
      const touchE = e.touches[0];
      this.touchStartX = touchE.clientX;
      this.touchStartY = touchE.clientY;
    },
    onTouchMove: function(e) {
      const touchE = e.touches[0];
      const xDelta = touchE.clientX - this.touchStartX;
      const yDelta = touchE.clientY - this.touchStartY;
      const swipingLeft = (xDelta < 0) && (Math.abs(yDelta) < Math.abs(xDelta));
      const swipingRight = (xDelta > 0) && (Math.abs(yDelta) < Math.abs(xDelta));

      if (!this.swipeLocked) {
        if (swipingLeft) {
          // Move accordion over
          if (xDelta > -160) {
            this.swipePosition = xDelta;
          } else {
            const overswipe = Math.abs(xDelta) - 160;
            const extra = (Math.log(overswipe)/Math.log(1.2));
            this.swipePosition = -160 - extra;
          }
        }
      } else {
        if (swipingLeft) {
          const extra = (Math.log(xDelta)/Math.log(1.2));
          this.swipePosition = -160 - extra;
        } else if (swipingRight) {
          this.swipePosition = -160 + xDelta;
        }
      }
    },
    onTouchEnd: function(e) {
      this.removeTouchEvents(e);
      this.useTransition = true;

      this.$nextTick(() => {
        if (Math.abs(this.swipePosition) >= 160) {
          this.swipePosition = -160;
          this.swipeLocked = true;
        } else {
          this.swipePosition = 0;
          this.swipeLocked = false;
        }
      });

      window.setTimeout(() => {
        this.useTransition = false;
      }, 300)
    },
    onTouchCancel: function(e) {
      this.removeTouchEvents(e);

      this.useTransition = true;
      this.$nextTick(() => {
        this.swipePosition = 0;
        this.swipeLocked = false;
      });

      window.setTimeout(() => {
        this.useTransition = false;
      }, 300);
    },
    removeTouchEvents: function(e) {
      e.target.removeEventListener('touchmove', this.onTouchMove);
      e.target.removeEventListener('touchend', this.onTouchEnd);
      e.target.removeEventListener('touchcancel', this.onTouchCancel);
    }
  },

// //------

  mounted: function() {
    this.checkToShowSwipeAffordance();
  }
}
  
</script>



