
<template>

  <div class="view view-make-payment">
    
<!--     <div class="step-counter">
      Step {{currentStep+1}} of 4
      <div
        class="step-counter-progress-bar"
        :style="{width: `${(currentStep+1) * 25}%`}"
      ></div>
    </div> -->

    <div class="step-slider-window">
      <div
        class="step-slider"
        :style="{transform: `translateX(${stepSliderTranslate}px)`}"
      >

        <!-- 1. Amount -->

        <div class="step step1">
          <div class="full-card step-title-card">
            <p class="small step-count">Step 1 of 4</p>
            <h2>Payment Amount</h2>
          </div>

          <div class="link-card select-card">
            <div
              class="link-card-item no-link"
              :class="{'selected': paymentAmountType === 'amountDue'}"
              @click="setPaymentAmountType('amountDue')"
            >
              Amount Due ${{user.billing.amountDue.toFixed(2)}}
            </div>
            <div
              v-if="user.billing.pastDueAmount"
              class="link-card-item no-link"
              :class="{'selected': paymentAmountType === 'pastDueAmount'}"
              @click="setPaymentAmountType('pastDueAmount')"
            >
              Past Due ${{user.billing.pastDueAmount.toFixed(2)}}
            </div>
            <div
              class="link-card-item no-link other-amount-item"
              :class="{'selected': paymentAmountType === 'otherAmount'}"
              @click="onSetToOtherAmount()"
            >
              <kite-text-input
                id="otherAmount"
                label="Pay Other Amount"
                type="number"
                :value="otherAmount"
                :onChange="onOtherAmountEdit"
                :onKeyup="onOtherAmountEdit"
              />
            </div>
          </div>

          <div class="progress-buttons">
            <kite-button
              size="full"
              :onClick="nextStep"
            >
              Continue
            </kite-button>
            <kite-button
              size="full"
              type="standalone-link"
              :onClick="cancelMakePayment"
            >
              Cancel
            </kite-button>
          </div>
        </div>

        <!-- 2. Method -->

        <div
          class="step step2"
          :class="{'current': (makePaymentStep === 1)}"
        >
          <div class="full-card step-title-card">
            <p class="small step-count">Step 2 of 4</p>
            <h2>Payment Method</h2>
          </div>

          <div
            v-if="user.billing.savedMethods.length || newPaymentMethods.length"
            class="link-card select-card payment-method-card"
          >
            <transition-group tag="div" name="row-list">
              <make-payment-method-row
                v-for="method in user.billing.savedMethods"
                :key="method.id"
                :isSelected="paymentMethod.id === method.id"
                :onSelect="() => setPaymentMethod(method)"
                :onDelete="() => deletePaymentMethod(method)"
                :paymentMethod="method"
              />
              <make-payment-method-row
                v-for="method in newPaymentMethods"
                :key="method.id"
                :isSelected="paymentMethod.id === method.id"
                :onSelect="() => setPaymentMethod(method)"
                :onDelete="() => deleteTempPaymentMethod(method)"
                :paymentMethod="method"
              />
            </transition-group>
          </div>

          <div
            v-if="(newPaymentMethods.length + user.billing.savedMethods.length < 3)"
            class="link-card"
          >
            <div
              class="link-card-item"
              @click="() => canShowPaymentMethod = true"
            >
              Use Another Payment Method
            </div>
          </div>

          <div class="progress-buttons">
            <kite-button
              size="full"
              :onClick="nextStep"
            >
              Continue
            </kite-button>
            <kite-button
              size="full"
              type="standalone-link"
              :onClick="cancelMakePayment"
            >
              Cancel
            </kite-button>
          </div>

          <div class="full-card step-title-card">
            <h2>Expired Payment Methods</h2>
            <p>We'll automatically delete expired cards after 180 days.</p>
          </div>

          <div class="link-card expired-card">
            <div
              v-for="expiredMethod in expiredMethods"
              class="link-card-item no-link"
            >
              <div class="label-cell">
                <payment-method
                  :method="expiredMethod"
                  :isShortened="true"
                />
              </div>
              <div class="value-cell red">
                Expired {{expiredMethod.cardExp}}
              </div>
            </div>
          </div>

        </div>

        <!-- 3. Date -->

        <div class="step step3">
          <div class="full-card step-title-card">
            <p class="small step-count">Step 3 of 4</p>
            <h2>Payment Date</h2>
            <p>Due Date: {{nextDueDate}}</p>
          </div>

          <datepicker
            v-model="paymentDate"
            :inline="true"
            :disabledDates="disabledDates"
            :highlighted="highlighted"
          />

          <div class="progress-buttons">
            <kite-button
              size="full"
              :onClick="nextStep"
            >
              Continue
            </kite-button>
            <kite-button
              size="full"
              type="standalone-link"
              :onClick="cancelMakePayment"
            >
              Cancel
            </kite-button>
          </div>
        </div>

        <!-- 4. Review -->

        <div class="step step4">
          <div class="full-card step-title-card">
            <p class="small step-count">Step 4 of 4</p>
            <h2>Review and Complete Payment</h2>
          </div>

          <div class="full-card">
            <div class="card-item split-card-item">
              <div class="split-label">Payment Amount</div>
              <div class="split-value">${{paymentAmount}}</div>
            </div>
            <div class="card-item split-card-item">
              <div class="split-label">Payment Method</div>
              <div class="split-value">
                <payment-method
                  :method="paymentMethod"
                  :isShortened="true"
                />
              </div>
            </div>
            <div class="card-item split-card-item">
              <div class="split-label">Payment Date</div>
              <div class="split-value">{{mdyPaymentDate}}</div>
            </div>
          </div>

          <div class="link-card">
            <div class="link-card-item no-link switch-item">
              <span class="label-cell">
                Enroll In Auto Pay
              </span>
              <span class="value-cell">
                <kite-switch
                  id="enrollAutopay"
                  name="enrollAutopay"
                  :isOn="enrollAutopay"
                  :onChange="onEnrollAutopayToggle"
                />
              </span>
            </div>
          </div>

          <div class="progress-buttons">
            <p>By making this payment, you are confirming your agreement to the <a href="#">Terms & Conditions</a>.</p>
            <kite-button
              size="full"
              :onClick="submitPayment"
            >
              Complete Payment
            </kite-button>
            <kite-button
              size="full"
              type="standalone-link"
              :onClick="cancelMakePayment"
            >
              Cancel
            </kite-button>
          </div>
        </div>

      </div>
    </div>


<!-- Modals -->

    <make-payment-method
      :show="canShowPaymentMethod"
      :onClose="() => this.canShowPaymentMethod = false"
      :onSubmit="onAddNewPaymentMethod"
    />
    

<!-- Submitting Spinner -->

    <transition name="fade">
      <div
        v-if="submitting"
        class="payment-loader"
      >
        <div class="payment-spinner-container">
          <kite-spinner />
        </div>
      </div>
    </transition>

    <transition name="fade-slow">
      <make-payment-success
        v-if="showSuccess"
        :paymentAmount="paymentAmount"
        :paymentDate="paymentDate"
        :paymentMethod="paymentMethod"
        :contentTitle="successTitle"
        :contentCopy="successCopy"
      />
    </transition>

  </div>

</template>



<script>

import {
  KiteButton,
  KiteSpinner,
  KiteSwitch,
  KiteTextInput,
} from '@kite/vue-kite';
import MakePaymentMethod from '../components/make-payment-method.vue';
import MakePaymentMethodRow from '../components/make-payment-method-row.vue';
import MakePaymentSuccess from '../components/make-payment-success.vue';
import Datepicker from 'vuejs-datepicker';
import PaymentMethod from '../components/payment-method.vue';

import { mapState } from 'vuex';
import {
  getMDY,
  getNextBillDateUTC,
  getNextBillDueDateUTC,
  getNextAutoPayDateUTC,
  isToday
} from '../utils/time';

const today = new Date();
let yesterday = new Date();
yesterday.setDate(yesterday.getDate() - 1);


export default {
  name: 'make-payment',
  components: {
    KiteButton,
    KiteSpinner,
    KiteSwitch,
    KiteTextInput,
    MakePaymentMethod,
    MakePaymentSuccess,
    Datepicker,
    PaymentMethod,
    MakePaymentMethodRow,
  },

  data: function() {
    return {
      sliderWidth: 0,
      submitting: false,

      paymentAmount: '',
      paymentAmountType: '',
      paymentMethod: '',
      paymentDate: today,
      enrollAutopay: false,

      // Temp values
      otherAmount: '',
      newPaymentMethods: [],

      // View controls
      showSuccess: false,
      canShowPaymentAmount: false,
      canShowPaymentMethod: false,

      // Calendar data
      disabledDates: {
        to: yesterday,
        from: getNextAutoPayDateUTC(),
      },
      highlighted: {
        dates: [
          getNextBillDueDateUTC(),
        ]
      },
      nextDueDate: getMDY(getNextBillDueDateUTC()),

      // Expired cards
      expiredMethods: [
        {
          id: 1,
          paymentType: 'card',
          cardType: 'visa',
          cardVal: '4342 1234 1232 0091',
          cardExp: '02/19',
        },
        {
          id: 2,
          paymentType: 'card',
          cardType: 'mastercard',
          cardVal: '5533 3423 5534 3478',
          cardExp: '12/18',
        },
      ],
    }
  },

//-----

  computed: {
    ...mapState([
      'user',
      'makePaymentStep',
    ]),
    mdyPaymentDate() {
      return getMDY(this.paymentDate);
    },
    stepSliderTranslate() {
      return -(this.sliderWidth * this.makePaymentStep);
    },
    successTitle() {
      if (this.enrollAutopay) {
        return (isToday(this.paymentDate)) ? 'Thanks for your payment and auto pay enrollment.' : 'Lorem ipsum payment scheduled and auto pay enrollment.';
      } else {
        return (isToday(this.paymentDate)) ? 'Thanks for your payment.' : 'Payment Scheduled';
      }
    },
    successCopy() {
      return (this.enrollAutopay) ? 'You\'ll receive a confirmation message shortly. Payments may take up to 24 hours to post to your account.' : 'Payments may take up to 24 hours to post to your account.';
    },
  },

//-----

  methods: {
    initValues() {
      if (this.user) {
        this.paymentAmount = this.user.billing.amountDue.toFixed(2);
        this.paymentAmountType = 'amountDue';
        this.paymentMethod = this.user.billing.savedMethods ? this.user.billing.savedMethods[0] : '';
      }
    },
    initSlider() {
      this.sliderWidth = this.$el.offsetWidth;
    },
    nextStep() {
      this.$store.commit('nextMakePaymentStep');

      if (this.$el.scrollTop) {
        window.setTimeout(() => {
          this.$el.scroll({
            top: 0,
            behavior: 'smooth',
          });
        }, 50);
      }
    },

    // Payment Amount
    setPaymentAmount(val) {
      this.paymentAmount = val;
    },
    onOtherAmountEdit(e) {
      this.otherAmount = e.target.value;
      this.paymentAmount = this.otherAmount;
    },
    onSetToOtherAmount() {
      this.paymentAmountType = 'otherAmount';
    },
    setPaymentAmountType(type) {
      this.paymentAmountType = type;
      this.setPaymentAmount(this.user.billing[type].toFixed(2));
    },

    // Payment Method
    setPaymentMethod(method) {
      this.paymentMethod = method;
    },
    getNextPaymentID() {
      let nextId = 0;

      this.user.billing.savedMethods.forEach((method) => {
        if (method.id >= nextId) {
          nextId = method.id + 1;
        }
      });
      this.newPaymentMethods.forEach((method) => {
        if (method.id >= nextId) {
          nextId = method.id + 1;
        }
      });

      return nextId;
    },
    onAddNewPaymentMethod(method) {
      method.id = this.getNextPaymentID();

      if (method.saveMethod) {
        this.$store.commit('savePaymentMethod', method);
      } else {
        this.newPaymentMethods.push(method);
      }
      this.paymentMethod = method;
    },
    deletePaymentMethod(method) {
      this.$store.commit('deletePaymentMethod', method);
      this.checkIfDeleteIsSelected(method);
    },
    deleteTempPaymentMethod(deletedMethod) {
      this.newPaymentMethods = this.newPaymentMethods.filter((method) => !(method.id === deletedMethod.id));
      this.checkIfDeleteIsSelected(deletedMethod);
    },
    checkIfDeleteIsSelected(method) {
      if (this.paymentMethod.id === method.id) {
        this.paymentMethod = this.user.billing.savedMethods ? this.user.billing.savedMethods[0] : '';
      }
    },

    // Submit
    onEnrollAutopayToggle(e) {
      this.enrollAutopay = e.target.checked
    },
    submitPayment: function() {
      const payment = {
        amount: this.paymentAmount,
        date: this.paymentDate,
        enrollInAutopay: this.enrollAutopay,
        paymentMethod: this.paymentMethod,
      };

      this.$store.commit('submitPayment', payment);

      this.submitting = true;

      window.setTimeout(() => {
        this.showSuccess = true;
        this.submitting = false;
      }, 2000);
    },

    cancelMakePayment() {
      this.$store.commit('setMakePaymentStep', 0);
      this.$router.push('/billing');
    },
  },

//-----

  mounted: function() {
    this.initValues();
    this.initSlider();
  }

}

</script>



