<template>

  <div class="tab-controls">
    <div
      class="tab"
      :class="{'selected': selected === 'details'}"
      @click="onTabSelect('details')"
    >
      Bill Details
    </div>

    <div
      class="tab"
      :class="{'selected': selected === 'messages'}"
      @click="onTabSelect('messages')"
    >
      Bill Messages ({{messageNumber}})
    </div>
    
    <div
      class="tab-highlight-bar"
      :style="{left: tabHighlightLeft+'px', width: tabHighlightWidth+'px'}"
    ></div>
  </div>

</template>



<script>

export default {
  name: 'tabControls',
  components: {
  },
  props: [
    'selected',
    'onTabChange',
    'messageNumber',
  ],

//-----

  data: function() {
    return {
      tabHighlightLeft: 0,
      tabHighlightWidth: 150,
    }
  },

//-----

  methods: {
    onTabSelect: function(val) {
      this.onTabChange(val);
      this.positionLine();
    },

//-----

    positionLine: function() {
      this.$nextTick(() => {
        const selectedEl = this.$el.querySelector('.selected');

        if (selectedEl) {
          this.tabHighlightLeft = selectedEl.offsetLeft;
          this.tabHighlightWidth = selectedEl.offsetWidth;
        }
      });
    },
  }, // end methods

//-----

  mounted: function() {
    this.positionLine();
  },
}

</script>



<style lang="scss">

@import '~@kite/colors/variables';


.tab-controls {
  display: flex;
  position: relative;
  padding: 12px 0 0;
  border-bottom: 1px solid $kite-gray-2;

  .tab {
    flex: 1 1 auto;
    display: inline-block;
    padding: 12px 24px 12px;
    min-width: 120px;
    font-weight: 500;
    cursor: pointer;
    text-align: center;

    &:focus {
      outline: none;
    }
  }

  .tab-highlight-bar {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 150px;
    height: 4px;
    background-color: $kite-blue-2;
    transition:
      left 0.4s ease-in-out,
      width 0.3s;
  }
}

</style>
