
<template>

  <div class="view view-autopay-enroll">
    
    <div class="payment-card">
      <div class="payment-card-row disabled">
        <div class="row-title">Payment Amount</div>
        <div class="row-value">${{paymentAmount}} (Total Balance)</div>
      </div>
      <div class="payment-card-row disabled">
        <div class="row-title">First Payment Date</div>
        <div class="row-value">{{firstPaymentDate}} (Due Date)</div>
      </div>
      <div class="payment-card-row disabled">
        <div class="row-title">Next Auto Pay Date</div>
        <div class="row-value">{{nextPaymentDate}} (On or Around)</div>
      </div>
      <div
        class="payment-card-row"
        @click="canShowPaymentMethod = true"
      >
        <div class="row-title">Payment Method</div>
        <div
          class="row-value"
          :class="{'card-value': (paymentMethod.paymentType === 'card')}"
        >
          <span
            v-if="paymentMethod.paymentType === 'card'"
            class="card-logo"
          >
            <card-logos
              :cardType="paymentMethod.cardType"
              :cardValid="true"
              :useGrey="true"
            />
          </span>
          {{methodValue}}
        </div>
      </div>
    </div>

    <div class="make-payment-actions">
      <kite-button
        size="full"
        :onClick="submitEnroll"
        :disabled="!canSubmit"
      >
        Enroll in Autopay
      </kite-button>
    </div>


<!-- Modals -->

    <make-payment-method
      :show="canShowPaymentMethod"
      :onClose="() => this.canShowPaymentMethod = false"
      :onSubmit="(val) => this.paymentMethod = val"
    />
    
    <transition name="fade">
      <div
        v-if="submitting"
        class="payment-loader"
      >
        <div class="payment-spinner-container">
          <kite-spinner />
        </div>
      </div>
    </transition>

    <transition name="fade-slow">
      <make-payment-success
        v-if="showSuccess"
        :isAutopay="true"
        headerTitle="Enroll in Autopay"
        contentTitle="Thanks for your Auto Pay Enrollment"
        contentCopy="You'll receive a confirmation message shortly. Payments may take up to 24 hours to post to your account."
        :paymentAmount="paymentAmount"
        :paymentMethod="paymentMethod"
      />
    </transition>

  </div>

</template>



<script>

import {
  KiteButton,
  KiteSpinner,
} from '@kite/vue-kite';
import MakePaymentMethod from '../components/make-payment-method.vue';
import MakePaymentSuccess from '../components/make-payment-success.vue';
import CardLogos from '../components/card-logos.vue';

import { mapState } from 'vuex';
import {
  getMDY,
  getNextBillDueDateUTC,
  getNextAutoPayDateUTC,
} from '../utils/time';

// TODO:
// - Dates to UTC
// - Payment amount from user
// - 


export default {
  name: 'autopay-enroll',
  components: {
    KiteButton,
    KiteSpinner,
    MakePaymentMethod,
    MakePaymentSuccess,
    CardLogos
  },

  data: function() {
    return {
      submitting: false,
      showSuccess: false,
      canShowPaymentMethod: false,

      paymentMethod: '',
      paymentAmount: '',
      firstPaymentDate: '11/27/18',
      nextPaymentDate: '12/26/18',
    }
  },

//-----

  computed: {
    ...mapState([
      'user'
    ]),
    canSubmit: function() {
      return (
        this.paymentMethod !== ''
      );
    },

    methodValue: function() {
      if (this.paymentMethod) {
        const {
          paymentType,
          accountNumber,
        } = this.paymentMethod;

        if (paymentType === 'card') {
          const {cardVal} = this.paymentMethod;
          return cardVal.substring(cardVal.length-4);

        } else {
          return `${paymentType.charAt(0).toUpperCase() + paymentType.slice(1)} x${accountNumber.substring(accountNumber.length-4)}`;
        }
      } else {
        return 'Select Payment Method';
      }
    },
  },

//-----

  methods: {
    submitEnroll: function() {
      this.$store.commit('autopayEnroll', this.paymentMethod);

      this.submitting = true;

      window.setTimeout(() => {
        this.showSuccess = true;
        this.submitting = false;
      }, 2000);
    }
  },

//-----

  mounted: function() {
    if (this.user) {
      const { billing } = this.user;

      this.paymentAmount = billing.amountDue.toFixed(2);
      this.firstPaymentDate = getMDY(getNextBillDueDateUTC());
      this.nextPaymentDate = getMDY(getNextAutoPayDateUTC());
      this.paymentAmount = billing.currentBalance.toFixed(2);
    }
  }

}

</script>



