
<template>

  <div class="action-sheet make-payment-modal make-payment-success">
    <div class="action-sheet-header app-header">
      <div class="title show">{{headerTitle || 'Payment Success'}}</div>
      <button
        class="header-submit"
        @click="close"
      >
        Done
      </button>
    </div>

    <div
      class="action-sheet-body"
      :class="{'has-content': contentCopy}"
    >

      <div class="section-recessed-card status-card">
        <div class="recessed-card">
          <span class="si si-checkmark-f"></span>
          <h2 class="subsection-title">{{contentTitle || title}}</h2>
          <p>Payments may take up to 24 hours to post to your account.</p>
        </div>
      </div>

      <summary-card-autopay
        v-if="isAutopay && !hideSummary"
        :paymentAmount="paymentAmount"
        :paymentMethod="paymentMethod"
      />
      <summary-card
        v-else-if="!hideSummary"
        :paymentAmount="paymentAmount"
        :paymentDate="paymentDate"
        :paymentMethod="paymentMethod"
      />

      <div
        v-if="paymentIsToday"
        class="screenshot-action"
      >
        <kite-button
          size="full"
          type="outline"
          :onClick="() => {}"
        >
          Take Screenshot
        </kite-button>
      </div>

    </div>
  </div>

</template>



<script>

import SummaryCard from './summary-card.vue';
import SummaryCardAutopay from './summary-card-autopay.vue';
import {
  KiteButton,
} from '@kite/vue-kite';
import { mapState } from 'vuex';
import {
  isToday,
} from '../utils/time';


export default {
  name: 'make-payment-success',
  components: {
    SummaryCard,
    SummaryCardAutopay,
    KiteButton,
  },

//-----

  props: [
    'paymentAmount',
    'paymentDate',
    'paymentMethod',
    'headerTitle',
    'contentTitle',
    'contentCopy',
    'isAutopay',
    'hideSummary',
  ],

//-----

  data: function() {
    return {
      title: 'Payment Scheduled', // Set in mounted()
      paymentIsToday: (isToday(this.paymentDate)),
    }
  },

//-----

  computed: {
    ...mapState([
      'user'
    ]),
  },

//-----

  methods: {
    close() {
      this.$router.push('/billing');
      this.$store.commit('setMakePaymentStep', 0);
    },
    setTitle() {
      this.title = this.paymentIsToday ? 'Thanks for your payment.' : 'Payment Scheduled';
    },
  },

//-----

  mounted: function() {
    this.setTitle();
  }

}

</script>



