  left: 0;
  opacity: 1;

  .action-sheet-body {
    padding: 32px 0;
    text-align: center;

    .si {
      display: inline-block;
      margin-bottom: 24px;
      font-size: 44px;
      line-height: 1em;
      color: $kite-dark-green;
    }

    .status-card {
      h2 {
        margin-bottom: 8px;

        &:last-child {
          margin: 0;
        }
      }
      p {
        &:last-child {
          margin: 0;
        }
      }
    }

    .summary-card {
      padding: 24px 16px;
      font-size: 1rem;
      font-weight: 400;
      border-radius: 0;
      border-right: 0;
      border-left: 0;

      .success-row {
        margin-bottom: 24px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &.has-content {
      .success-title {
        margin-bottom: 16px;
      }
      .success-copy {
        margin-bottom: 16px;
      }
    }

    .screenshot-action {
      padding: 40px 16px 24px;

      .kite-btn {
        margin: 0;
      }
    }
  }
}

</style>
