
<template>

  <div class="payment-method">
    <card-logos
      :card-type="(method.paymentType === 'card') ? method.cardType : 'bank'"
    />
    {{ accountNumber }}
  </div>

</template>



<script>

import CardLogos from '../components/card-logos.vue';
import {mapState} from 'vuex';


export default {
  name: 'payment-method',
  components: {
    CardLogos,
  },

//----

  props: [
    'method',
    'isShortened',
  ],

//-----

  computed: {
    accountNumber() {
      if (this.method) {
        const account = (this.method.paymentType === 'card') ? this.method.cardVal : this.method.accountNumber;
        return (this.isShortened) ? account.slice(-4) : account;
      }
    },
  },

}

</script>



