.payment-method {
  display: flex;
  align-items: center;
  flex: 1 0 0;

  .billing-card-logos {
    display: inline-block;
    flex: 0 0 40px;
    height: 22px;
    margin-right: 12px;
    box-shadow: 0 2px 3px $kite-gray-2;

    .card-logo {
      font-size: 23px;
    }
  }
}

  
</style>
