
<template>

  <div class="view view-scp-device-details">

    <transition name="app-banner">
      <div
        v-if="checkIfDevicePaused(scpFocusedDevice)" 
        class="app-alert-notification info-alert"
      >
        <span class="si si-circle-info-f"></span>
        <div class="alert-copy">
          This Device is Currently Paused
        </div>
      </div>
    </transition>

    <div class="section-recessed-card device-detail-card">
      <div class="recessed-card">
        <div class="scp-device-badge">
          <img :src="scpFocusedDevice.whiteicon" alt="device icon">
          <div
            v-if="checkIfDevicePaused(scpFocusedDevice)"
            class="device-badge-pause"
          ></div>
        </div>
        <h2 class="subsection-title">
          {{ scpFocusedDevice.name }}
        </h2>

        <!-- Paused Intro Card -->
        <template v-if="checkIfDevicePaused(scpFocusedDevice)">
          <div class="value-detail">
            <template v-if="scpFocusedDevice.tempPause && (scpFocusedDevice.tempPause === 1)">
              Paused: Unpause Device to Connect
            </template>
            <template v-else>
              Paused For: {{ getTimeRemaining(scpFocusedDevice) }}
            </template>
          </div>
          <kite-button
            size="full"
            type="outline"
            :onClick="unpauseDevice"
          >
            Unpause Device
          </kite-button>
        </template>

        <!-- Unpaused Intro Card -->
        <template v-else>
          <div class="value-detail">
            Signal Strength: <span class="green">Excellent</span>
          </div>
          <small class="value-detail">
            Connected to Office
          </small>
          <kite-button
            size="full"
            :onClick="showActionSheet"
          >
            Pause Device
          </kite-button>  
        </template>
        
      </div>
    </div>

    <div class="full-card section-title-card">
      <h2>{{ (scpFocusedDevice.schedule) ? 'Device Pause Scheduled' : 'Scheduled Pause' }}</h2>
    </div>
    
    <!-- SCHEDULE CARD -->
    <!-- No schedule set... -->
    <div
      v-if="!scpFocusedDevice.schedule"
      class="full-card"
    >
      <p>Set up a recurring pause for this device. Select the days and times you want to pause the network connection.</p>
      <kite-button
        type="outline"
        size="full"
        :onClick="showScheduler"
      >
        Create Pause Schedule
      </kite-button>
    </div>

    <!-- With schedule set... -->
    <div
      v-else
      class="link-card set-schedule-card"
    >
      <div class="link-card-item no-link switch-item">
        <div class="label-cell">
          {{ scpFocusedDevice.schedule.name }}
        </div>
        <div class="value-cell">
<!--
          <kite-switch
            id="enableSchedule"
            name="customDays"
            :isOn="scpFocusedDevice.schedule.enabled"
            :onChange="onScheduleToggle"
          />
-->
        </div>
      </div>

      <div
        class="schedule-card-accordion"
        :class="{'collapse': !scpFocusedDevice.schedule.enabled}"
      >
        <div class="link-card-item no-link schedule-row">
          <div
            v-for="scheduleDay in scpFocusedDevice.schedule.days"
            class="schedule-row-item"
          >
            <div class="schedule-row-day">{{ scheduleDay.name }}</div>
            <div class="schedule-row-times">{{ scheduleDay.start }} - {{ scheduleDay.end }}</div>
          </div>
        </div>
        <div class="link-card-item no-link edit-schedule-row">
          <kite-button
            type="standalone-link"
            size="full"
            :onClick="showScheduler"
          >
            Edit Schedule
          </kite-button>
        </div>
      </div>

    </div>

    <div class="link-card device-link-card">
      <div
        class="link-card-item"
        @click="onShowEditName"
      >
        Edit Device Name
      </div>
      <div class="link-card-item">
        View Device Info
      </div>
    </div>

    <div class="full-card">
      <h2>Need Help</h2>
      <a tabindex>View Troubleshooting Article</a>
    </div>


    <!-- Pause Action Sheet -->
    <!-- ---- -->

    <div
      class="pop-up-action-sheet"
      :class="{'show': canShowPauseActions}"
      @click="hideActionSheet"
    >
      <div class="pu-all-actions-container">
        <div class="pu-actions">
          <button
            class="pu-action-btn"
            @click="() => setDevicePause(0.5)"
          >
            For 30 Minutes
          </button>
          <button
            class="pu-action-btn"
            @click="() => setDevicePause(2)"
          >
            For 2 Hours
          </button>
          <button
            class="pu-action-btn"
            @click="() => setDevicePause(24)"
          >
            Until End of Day
          </button>
          <button
            class="pu-action-btn"
            @click="() => setDevicePause(0)"
          >
            Until I Unpause
          </button>
          <button
            class="pu-action-btn"
            @click="showScheduler"
          >
            Create a Pause Schedule
          </button>
        </div>
        <div class="pu-cancel">
          <button
            class="pu-action-btn pu-cancel"
            @click="hideActionSheet"
          >
            Cancel
          </button>
        </div>
      </div>
    </div>


    <!-- Scheduling Modal -->
    <!-- ---- -->

    <modal-full-page
      :modalTitle="scpFocusedDevice.name"
      :canShow="canShowScheduler"
      :onBack="hideScheduler"
      :onSave="saveSchedule"
      newClass="schedule-modal"
    >
      <div class="full-card schedule-name-card">
        <kite-text-input
          id="newScheduleName"
          label="Schedule Name"
          :onChange="onInputChange"
          :onKeyup="onInputChange"
          :value="newScheduleName"
        />
      </div>

      <div class="full-card schedule-days-card">
        <div class="schedule-days-title">
          Select Days
        </div>
        <div class="schedule-days-row">
          <label
            v-for="day in refDays"
            class="day-option"
            :key="day"
          >
            <input
              type="radio"
              name="currentDay"
              v-model="currentDay"
              :value="day"
            />
            <span
              class="day-option-button"
              :class="{'active': (scheduleDays.find((scheduleDay) => scheduleDay.name === day).enabled)}"
            >
              {{ day.slice(0, 1) }}
            </span>
          </label>
        </div>
      </div>

      <div
        v-if="currentDayObj"
        class="full-card schedule-details-card"
      >
        <div
          class="schedule-details-fade-box"
          :class="{'hide': canHideScheduleDetails}"
        >

          <div class="schedule-day-toggle-row">
            <div class="day-toggle-name">
              {{ currentDayObj.name }}
            </div>
            <!-- <div class="day-switch">
              <kite-switch
                :id="`customUse${currentDayObj.name}`"
                name="customDays"
                :isOn="currentDayObj.enabled"
                :onChange="onScheduleDayToggle"
              />
            </div> -->
          </div>
          <kite-select
            label="Start Time (MST)"
            id="startTime"
            :onChange="setScheduleStartTime"
            :value="currentDayObj.start"
          >
            <option
              v-for="time in refTimes"
              :key="time"
              :value="time"
            >
              {{ time }}
            </option>
          </kite-select>

          <kite-select
            label="Stop Time (MST)"
            id="endTime"
            :onChange="setScheduleEndTime"
            :value="currentDayObj.end"
          >
            <option
              v-for="time in currentDayObj.endTimes"
              :key="time"
              :value="time"
            >
              {{ time }}
            </option>
          </kite-select>

          <kite-button
            :onClick="onClearDaySchedule"
            type="standalone-link"
            size="full"
            className="btn-clear-day-schedule"
          >
            Clear Schedule
          </kite-button>

        </div>
      </div>

      <div
        v-if="scheduleDays.find(day => day.enabled)"
        class="schedule-delete-action-row"
      >
        <kite-button
          :onClick="onClearWeekSchedule"
          type="outline"
          size="full"
          className="btn-clear-week-schedule"
        >
          Delete Schedule
        </kite-button>
      </div>

    </modal-full-page>


    <!-- Edit Name Modal -->
    <!-- ---- -->

    <modal-full-page
      modalTitle="Edit Name"
      :canShow="canShowEditName"
      :onBack="onHideEditName"
      :onSave="onSaveNewName"
      newClass="device-edit-name-modal"
    >
      <kite-text-input
        id="newDeviceName"
        label="Device Name"
        :value="newDeviceName"
        :onChange="onInputEdit"
        :onKeyup="onInputEdit"
      />
    </modal-full-page>

  </div>

</template>



<script>

import {
  KiteButton,
  KiteTextInput,
  KiteSelect,
  KiteSwitch,
} from '@kite/vue-kite';
import ModalFullPage from '../components/modal-full-page.vue';
import {
  mapState,
  mapGetters,
} from 'vuex';
import { days as UTCDays } from '../utils/time';


const refDays = [
  'sunday',
  'monday',
  'tuesday',
  'wednesday',
  'thursday',
  'friday',
  'saturday',
];

const refTimes = [
  '12:00 AM',
  '1:00 AM',
  '2:00 AM',
  '3:00 AM',
  '4:00 AM',
  '5:00 AM',
  '6:00 AM',
  '7:00 AM',
  '8:00 AM',
  '9:00 AM',
  '10:00 AM',
  '11:00 AM',
  '12:00 PM',
  '1:00 PM',
  '2:00 PM',
  '3:00 PM',
  '4:00 PM',
  '5:00 PM',
  '6:00 PM',
  '7:00 PM',
  '8:00 PM',
  '9:00 PM',
  '10:00 PM',
  '11:00 PM',
];

function clone(obj) {
  return JSON.parse(JSON.stringify(obj));
}

const now = new Date();


export default {
  name: 'scp-device-details',
  components: {
    KiteButton,
    KiteTextInput,
    KiteSelect,
    KiteSwitch,
    ModalFullPage,
  },

  data: () => ({
    canShowScheduler: false,

    // Scheduler
    refDays,
    refTimes,

    newScheduleName: '',
    scheduleDays: [],
    currentDay: null, // The day "tab" that's focused
    currentDayObj: null,
    canHideScheduleDetails: false, // for the switch day animation

    // Action Sheet
    canShowPauseActions: false,

    // Edit Name
    canShowEditName: false,
    newDeviceName: '',
  }),

//-----

  computed: {
    ...mapState([
      'connectedDevices',
    ]),
    ...mapGetters([
      'scpFocusedDevice',
    ]),
  },

//-----

  watch: {
    currentDay(newVal, oldVal) {
      if (!oldVal) {
        this.setScheduleDay();

      } else {
         // fade in and out the schedule details card
        this.canHideScheduleDetails = true;

        window.setTimeout(() => {
          this.setScheduleDay();
          this.canHideScheduleDetails = false;
        }, 300);
      }
       
    },
  },

//-----

  methods: {

    // General Interactions
    //-----

    showScheduler() {
      this.canShowScheduler = true;
      this.$el.querySelector('#newScheduleName').focus();
    },
    hideScheduler() {
      this.canShowScheduler = false;
    },
    onScheduleToggle(e) {
      const newSchedule = clone(this.scpFocusedDevice.schedule);
      newSchedule.enabled = e.target.checked;
      this.$store.commit('setDeviceSchedule', newSchedule);
    },

    // Utils
    //----

    onInputChange(e) {
      this[e.target.name] = e.target.value;
    },
    checkIfDevicePaused(deviceObj) {
      if (deviceObj.tempPause) {
        return true;

      } else if (deviceObj.schedule && deviceObj.schedule.enabled && deviceObj.schedule.days) {
        const todaySchedule = this.getTodaysSchedule(deviceObj);
        const currentHour = now.getHours();

        if (todaySchedule) {
          // convert times 0-23 time
          const startInt = this.convertTimeToInt(todaySchedule.start);
          const endInt = this.convertTimeToInt(todaySchedule.end);

          return ( (currentHour >= startInt) && (currentHour < endInt) );
        } else {
          return false;
        }

      } else {
        return false;
      }
    },
    setScheduleDay() {
      this.currentDayObj = this.scheduleDays.find((day) => day.name === this.currentDay);
    },

    // Time 
    //-----

    getTodaysSchedule(deviceObj) {
      const currentDay = UTCDays[now.getDay()].toLowerCase();
      return deviceObj.schedule.days.find((day) => day.name === currentDay);
    },
    convertTimeToInt(timeString) {
      const isPM = timeString.includes('PM');
      const splitString = timeString.split(':');
      const rawInt = parseInt(splitString[0]);

      if (rawInt === 12) {
        return (isPM) ? 12 : 0;
      } else {
        return (isPM) ? (rawInt + 12) : rawInt;
      }
    },
    getTimeRemaining(deviceObj) {
      if (deviceObj.tempPause) {
        return deviceObj.tempPause;

      } else {
        const todaySchedule = this.getTodaysSchedule(deviceObj);
        const endInt = this.convertTimeToInt(todaySchedule.end);

        const hoursLeft = endInt - 1 - now.getHours();
        const minutesLeft = 60 - now.getMinutes();

        return this.formatTimeString(hoursLeft, minutesLeft);
      }
    },
    unpauseDevice() {
      if (this.scpFocusedDevice.tempPause) {
        this.$store.commit('unpauseDevice', this.scpFocusedDevice.id);
      } else {
        const newSchedule = clone(this.scpFocusedDevice.schedule);
        newSchedule.enabled = false;
        this.$store.commit('setDeviceSchedule', newSchedule);
      }
    },


    // Action Sheet
    //----

    showActionSheet() {
      this.canShowPauseActions = true;
    },
    hideActionSheet() {
      this.canShowPauseActions = false;
    },
    setDevicePause(time) {
      let pauseLength;

      switch (time) {
        case 0.5:
          pauseLength = '30 minutes';
          break;
        case 2:
          pauseLength = '2 hours';
          break;
        case 24:
          pauseLength = this.getRestOfTodayTime();
          break;
        case 0:
        default:
          pauseLength = 1;
          break;
      }
      
      const pausePayload = {
        id: this.scpFocusedDevice.id,
        length: pauseLength,
      };

      this.$store.commit('pauseDevice', pausePayload);
    },
    getRestOfTodayTime() {
      const hoursLeft = 23 - now.getHours();
      const minutesLeft = 60 - now.getMinutes();

      return this.formatTimeString(hoursLeft, minutesLeft);
    },
    formatTimeString(hoursLeft, minutesLeft) {
      let returnString = '';

      if (hoursLeft) {
        returnString += hoursLeft;
        
        if (hoursLeft > 1) {
          returnString += ' hours ';
        } else {
          returnString += ' hour ';
        }
      }

      if (minutesLeft) {
        returnString += minutesLeft;

        if (minutesLeft > 1) {
          returnString += ' minutes';
        } else {
          returnString += ' minute';
        }
      }

      return returnString;
    },


    // Scheduler General
    //----

    checkToShowScheduler() {
      if (this.$route.query.schedule) {
        this.canShowScheduler = true;
      }
    },
    getNextTime(timeString) {
      const currentIndex = refTimes.indexOf(timeString);
      return refTimes[currentIndex+1];
    },
    cancelSchedule() {
      if (this.scpFocusedDevice.schedule) {
        this.loadSavedSchedule();
      } else {
        // reset back to blank
        this.initCustomSchedules();
        this.resetStandardSchedule();
      }
    },
    initSchedule() {
      if (this.scpFocusedDevice.schedule) {
        this.loadSavedSchedule();
      } else {
        this.initScheduleDays();
      }
    },
    // initCustomSchedules() {
    initScheduleDays() {

      // build array of days
      this.scheduleDays = refDays.map((day) => {
        return {
          name: day,
          start: refTimes[0],
          end: refTimes[1],
          enabled: false,
          endTimes: clone(refTimes),
        };
      });
    },
    loadSavedSchedule() {
      const schedule = clone(this.scpFocusedDevice.schedule);
      this.newScheduleName = schedule.name;

      // Create days objects for every day, even if only a few were saved
      const savedDays = refDays.map(dayName => {
        let baseDay = {
          name: dayName,
        };
        const daySaved = schedule.days.find(savedDay => savedDay.name === dayName);

        if (daySaved) {
          const newTimes = clone(refTimes);
          const startIndex = newTimes.indexOf(daySaved.start);

          return Object.assign(baseDay, {
            start: daySaved.start,
            end: daySaved.end,
            enabled: true,
            endTimes: newTimes.slice(startIndex + 1),
          });

        } else {
          return Object.assign(baseDay, {
            start: refTimes[0],
            end: refTimes[1],
            enabled: false,
            endTimes: clone(refTimes),
          });
        }

      });

      this.scheduleDays = savedDays;
    },

    onScheduleDayToggle(e) {
      this.scheduleDays = this.scheduleDays.map((customDay) => {
        if (customDay.name === this.currentDayObj.name) {
          customDay.enabled = e.target.checked;
        }
        return customDay;
      });
    },
    setScheduleStartTime(e) {
      this.scheduleDays = this.scheduleDays.map((customDay) => {
        if (customDay.name === this.currentDayObj.name) {
          customDay.start = e.target.value;
          customDay.enabled = true;

          const newTimes = clone(refTimes);
          const startIndex = newTimes.indexOf(e.target.value);
          customDay.endTimes = newTimes.slice(startIndex + 1);
          customDay.end = customDay.endTimes[0];
        }
        return customDay;
      });
    },
    setScheduleEndTime(e) {
      this.scheduleDays = this.scheduleDays.map((customDay) => {
        if (customDay.name === this.currentDayObj.name) {
          customDay.end = e.target.value;
          customDay.enabled = true;
        }
        return customDay;
      });
    },
    onClearDaySchedule() {
      console.log('clearing day schedule...');

      this.scheduleDays = this.scheduleDays.map((customDay) => {
        return (customDay.name === this.currentDayObj.name) ?
          {
            name: customDay.name,
            start: refTimes[0],
            end: refTimes[1],
            enabled: false,
            endTimes: clone(refTimes),
          }
          :
          customDay;
      });

      this.setScheduleDay();
    },
    onClearWeekSchedule() {
      this.hideScheduler();
      this.$store.commit('clearDeviceSchedule');

      this.initScheduleDays();
      this.currentDay = null;
      this.newScheduleName = '';
    },
    saveSchedule() {
      const activeDays = this.scheduleDays.filter(day => day.enabled);

      const newSchedule = {
        enabled: !!(activeDays.length),
        name: this.newScheduleName,
        days: activeDays.map((day) => ({
          name: day.name,
          start: day.start,
          end: day.end || this.getNextTime(day.start),
        })),
      };

      // Sort days in correct order
      newSchedule.days.sort((day1, day2) => {
        return refDays.indexOf(day1.name) - refDays.indexOf(day2.name);
      });

      this.$store.commit('setDeviceSchedule', newSchedule)
      this.hideScheduler();
    },


    // Edit Device Name
    //-----

    onShowEditName() {
      this.newDeviceName = this.scpFocusedDevice.name;
      this.canShowEditName = true;
    },
    onHideEditName() {
      this.canShowEditName = false;
    },
    onInputEdit(e) {
      this[e.target.name] = e.target.value;
    },
    onSaveNewName() {
      this.$store.commit('setDeviceName', this.newDeviceName);
      this.$router.replace({
        path: '/services/scp/device',
        query: {
          title: this.newDeviceName
        },
      });
      this.canShowEditName = false;
    },
  },

//-----

  created() {
    if (!this.scpFocusedDevice || !this.scpFocusedDevice.name) {
      this.$router.push('/services/scp/devices');
    }
  },
  beforeMount() {
    this.initSchedule();
    this.checkToShowScheduler();
  }
}

</script>



