
<template>

  <div class="kite-password-toggle">

    <kite-text-input
      :id="id"
      :name="id"
      :label="label"
      :type="(showPassword) ? 'text' : 'password'"
      :value="value"
      :onChange="onChange"
      :error="error"
      :forceError="forceError"
    />

    <button
      class="password-toggle-btn"
      type="button"
      @click.prevent.stop="toggleType"
    >
      <transition name="fade" mode="out-in">
        <span v-if="!showPassword" key="show">
          Show
        </span>
        <span v-else key="hide">
          Hide
        </span>
      </transition>
    </button>
  </div>

</template>



<script>

import { 
  KiteTextInput,
} from '@kite/vue-kite';


export default {
  name: 'input-password-toggle',
  components: {
    KiteTextInput
  },
  props: [
    'id',
    'value',
    'onChange',
    'label',
    'error',
    'forceError',
  ],

//-----

  data: function() {
    return {
      showPassword: false,
    }
  },

//-----

  computed: {
  },

//-----

  methods: {
    toggleType: function() {
      this.showPassword = !this.showPassword;
    },
  },
}

</script>



<style lang="scss">

@import '~@kite/shared/variables';

.kite-password-toggle {
  position: relative;

  .kite-form-control[type='password'] {
    background: none;
    background-color: $kite-white;
  }

  .password-toggle-btn {
    position: absolute;
    right: 8px;
    top: 27px;
    padding: 4px;
    font-size: 1rem;
    font-weight: 500;
    color: $kite-blue-2;

    background-color: transparent;
    border: 0;
    border-radius: 0;
  }
}

</style>
