
<template>

  <div class="loader-countdown">
    <div class="progress-container">
      <svg class="circular" viewBox="25 25 50 50">
        <circle :stroke-dashoffset="dashOffset" stroke="#0073d1" class="path" cx="50" cy="50" r="20" fill="none" stroke-width="2"/>
      </svg>

      <span class="progress-copy">
        <span class="remaining-val">
          {{ remainingString }}
        </span>
        <span class="remaining-label">
          Remaining
        </span>
      </span>
    </div>
  </div>

</template>



<script>

  export default {
    name: 'loader-countdown',
    props: {
      percent: {
        type: Number,
        required: true,
        validator: function(val) {
          return (
            (val >= 0) &&
            (val <= 100)
          );
        }
      },
      remainingString: {
        type: String,
        required: true,
      },
    },

  //-----

    computed: {
      dashOffset: function() {
        const fullVal = 123;
        return fullVal - (this.filteredPercent/100 * fullVal);
      },
      filteredPercent: function() {
        if (this.percent > 100) {
          return 100;
        } else if (this.percent < 0) {
          return 0;
        } else {
          return this.percent;
        }
      }
    },
  };

</script>



<style lang="scss">

.loader-countdown {
  position: relative;
  display: inline-block;
  width: 144px;
  height: 144px;
  padding: 24px 12px;

  .progress-container {
    position: relative;
    height: 100%;
    width: 100%;
  }

  .progress-copy {
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    transform: translate(-50%, -50%);

    .remaining-val {
      font-size: 1.25rem;
      font-weight: 700;
      display: block;
    }
    .remaining-label {
      font-size: 1rem;
    }
  }

  .circular {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    transform-origin: center center;
    transform: rotate(-90deg);
  }

  .path {
    stroke-dasharray: 124, 125; // full circle = 123
    stroke-linecap: round;
  }
}

</style>
