
<template>

  <div
    class="make-payment-method-row"
    :class="{'show-shadow': (swipePosition)}"
  >

    <div class="swipe-actions">
      <button
        class="swipe-btn delete-btn"
        @click="deleteMe"
      >
        Delete
      </button>
    </div>

    <div 
      class="link-card-item no-link method-action-row"
      :class="{
        'use-transition': useTransition,
        'selected': isSelected,
      }"
      @click="onClick"
      @touchstart="onTouchStart"
      :style="{'transform': `translateX(${swipePosition}px)`}"
    >
      <payment-method
        :method="paymentMethod"
        :isShortened="true"
      />
    </div>

  </div>

</template>



<script>

import PaymentMethod from '../components/payment-method.vue';

const MIN_SWIPE = 74;
const LOG_ARG = 1.2;


export default {
  name: 'make-payment-method-row',
  components: {
    PaymentMethod,
  },

//-----

  props: [
    'isSelected',
    'onSelect',
    'onDelete',
    'paymentMethod',
  ],

//-----

  data: () => ({

    // Touches
    touchStartX: null,
    swipePosition: 0,
    useTransition: false,
    swipeLocked: false,
  }),

//-----

  computed: {
  },

//-----

  methods: {
    onClick() {
      if (!this.swipeLocked) {
        this.onSelect(this.paymentMethod);
      } else {
        this.swipePosition = MIN_SWIPE - 2;
        this.onTouchEnd();
      }
    },
    deleteMe() {
      this.onDelete(this.paymentMethod);
    },

    // Touch/Swipe
    //---------------
    onTouchStart: function(e) {
      this.$el.addEventListener('touchmove', this.onTouchMove);
      this.$el.addEventListener('touchend', this.onTouchEnd);
      this.$el.addEventListener('touchcancel', this.onTouchCancel);

      this.initializeTouch(e);
    },
    initializeTouch: function(e) {
      const touchE = e.touches[0];
      this.touchStartX = touchE.clientX;
      this.touchStartY = touchE.clientY;
    },
    onTouchMove: function(e) {
      const touchE = e.touches[0];
      const xDelta = touchE.clientX - this.touchStartX;
      const yDelta = touchE.clientY - this.touchStartY;
      const swipingLeft = (xDelta < 0) && (Math.abs(yDelta) < Math.abs(xDelta));
      const swipingRight = (xDelta > 0) && (Math.abs(yDelta) < Math.abs(xDelta));

      if (!this.swipeLocked) {
        if (swipingLeft) {
          // Move accordion over
          if (xDelta > -MIN_SWIPE) {
            this.swipePosition = xDelta;
          } else {
            const overswipe = Math.abs(xDelta) - MIN_SWIPE;
            const extra = (Math.log(overswipe)/Math.log(LOG_ARG));
            this.swipePosition = -MIN_SWIPE - extra;
          }
        }
      } else {
        if (swipingLeft) {
          const extra = (Math.log(Math.abs(xDelta))/Math.log(LOG_ARG));
          this.swipePosition = -MIN_SWIPE - extra;
        } else if (swipingRight) {
          this.swipePosition = -MIN_SWIPE + xDelta;
        }
      }
    },
    onTouchEnd: function(e) {
      this.removeTouchEvents();
      this.useTransition = true;

      this.$nextTick(() => {
        if (Math.abs(this.swipePosition) >= MIN_SWIPE) {
          this.swipePosition = -MIN_SWIPE;
          this.swipeLocked = true;
        } else {
          this.swipePosition = 0;
          this.swipeLocked = false;
        }
      });

      window.setTimeout(() => {
        this.useTransition = false;
      }, 300)
    },
    onTouchCancel: function(e) {
      this.removeTouchEvents();

      this.useTransition = true;
      this.$nextTick(() => {
        this.swipePosition = 0;
        this.swipeLocked = false;
      });

      window.setTimeout(() => {
        this.useTransition = false;
      }, 300);
    },
    removeTouchEvents: function() {
      this.$el.removeEventListener('touchmove', this.onTouchMove);
      this.$el.removeEventListener('touchend', this.onTouchEnd);
      this.$el.removeEventListener('touchcancel', this.onTouchCancel);
    }

  },

//-----

  mounted: function() {
  }

}

</script>



<style lang="scss">

.make-payment-method-row {
  position: relative;
  border-bottom: 1px solid $kite-gray-2;

  &:last-child {
    border-bottom: 0;
  }

  &.show-shadow {
    .method-action-row {
      box-shadow: 2px 0 5px -2px $kite-gray-3;
    }
  }

  .method-action-row {
    position: relative;
    z-index: 2;
    background-color: $kite-white;

    &.use-transition {
      transition: transform 0.2s ease-out;
    }
  }

  .swipe-actions {
    position: absolute;
    z-index: 1;
    right: 0;
    top: 0;
    height: 100%;
    width: auto;
    display: flex;

    .swipe-btn {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 auto;
      padding: 11px;
      text-align: center;
      color: $kite-white;
      font-weight: 700;
      font-size: 1rem;
      background-color: $kite-gray-3;

      &.delete-btn {
        background-color: $kite-dark-red;
      }
    }
  }

}
</style>
