
<template>

  <div
    class="quick-tutorial"
    :class="{'past-welcome': !isOnWelcome}"
  >
    <button
      class="si si-x tutorial-close"
      @click="onClose"
    ></button>

    <div class="tour-welcome">
      <div class="welcome-copy">
        <h2>Welcome to My&nbsp;Spectrum</h2>
        <p>Lorem ipsum a few ways we can make your life easier.</p>
      </div>

     <!--  <img
        :src="require('../assets/images/tour-giant-check.svg')"
        alt="check-mark icon"
        class="tour-welcome-bg bg-check"
      />

      <img
        :src="require('../assets/images/tour-bill-doc.svg')"
        alt="bill illustration icon"
        class="tour-welcome-bg bg-bill"
      /> -->
    </div>
    
  <!-- Phone -->

    <tour-phone
      :showSlide="currentSlide-1"
    />

  <!-- IMG Track -->

    <div class="tour-img-window">
      <div class="tour-img-track">
        <img
          :src="require('../assets/images/tour-far-bg.png')"
          alt="far background image"
          class="tour-far-bg-img"
          :style="{'transform': `translateX(${-farBgTranslate}%)`}"
        />
        <img
          :src="require('../assets/images/tour-near-bg.png')"
          alt="near background image"
          class="tour-near-bg-img"
          :style="{'transform': `translateX(${-nearBgTranslate}%)`}"
        />
      </div>
    </div>

  <!-- Copy Slider -->

    <div class="tour-copy-window">
      <div
        class="tour-copy-slider"
        :style="{'transform': `translateX(${copyTranslate}px)`}"
      >
        <div
          v-for="slide in slides"
          class="tour-copy-slide"
        >
          <p v-html="slide.copy"></p>
        </div>
      </div>
    </div>

  <!-- Actions -->

    <div class="tour-slider-actions">
      <button
        class="slider-btn slider-back"
        :class="{'hide': (!currentSlide)}"
        @click="prevSlide"
      >
        Back
      </button>
      <div class="slider-indicators">
        <span
          v-for="n in slides.length"
          class="indicator"
          :class="{'active': (n-1 === currentSlide)}"
        ></span>
      </div>

      <transition name="fade" mode="out-in">
        <button
          v-if="(currentSlide+1) < slides.length"
          class="slider-btn slider-next"
          @click="nextSlide"
        >
          Next
        </button>
        <button
          v-else
          class="slider-btn slider-next"
          @click="onClose"
        >
          Done
        </button>
      </transition>
    </div>

  </div>

</template>



<script>

import TourPhone from './tour-phone.vue';

const slides = [
  {
    copy: '',
  },
  {
    copy: 'View and update your scheduled appointment on the&nbsp;Dashboard.',
  },
  {
    copy: 'Pay your bill, set up Auto Pay, view payment history and&nbsp;more.',
  },
  {
    copy: 'Troubleshoot your equipment or check for service interruptions in your&nbsp;area.',
  },
];


export default {
  name: 'quick-tutorial',
  components: {
    TourPhone,
  },

//-----

  props: [
    'onClose',
  ],

//-----

  data: () => ({
    slideWidth: 0,
    currentSlide: 0,

    slides,
  }),

//-----

  computed: {
    isOnWelcome() {
      return (this.currentSlide === 0);
    },
    copyTranslate: function() {
      return (this.currentSlide) ? -(this.slideWidth * (this.currentSlide)) : -this.slideWidth;
    },
    nearBgTranslate() {
      return (this.currentSlide) ? ((this.currentSlide-1) * (1/(slides.length-1) * 100)) : 0;
    },
    farBgTranslate() {
      return (this.currentSlide) ? ((this.currentSlide-1) * (1/(slides.length+4) * 100)) : 0;
    }
  },

//-----

  methods: {
    initializeSlider: function() {
      this.slideWidth = this.$el.offsetWidth;
      console.dir(this.$el);
    },
    nextSlide: function() {
      if (this.currentSlide+1 < this.slides.length) {
        this.currentSlide++;
      }
    },
    prevSlide: function() {
      if (this.currentSlide) {
        this.currentSlide--;
      }
    }
  },

//-----

  mounted: function() {
    this.initializeSlider();
  }
}

</script>



