
<template>

  <div class="view view-scp-devices">

    <transition name="app-banner">
      <router-link
        v-if="activeScheduleNumber" 
        to="/services/scp/active-schedules"
        class="app-alert-notification info-alert"
      >
        <span class="si si-circle-info-f"></span>
        <div class="alert-copy">
          You have {{ activeScheduleNumber }} {{ (activeScheduleNumber > 1) ? 'pausing schedules that are currently active' : 'pausing schedule that is currently active' }}.
          <div class="alert-cta-row">
            {{ (activeScheduleNumber > 1) ? 'View Schedules' : 'View Schedule' }}
            <span class="si si-chevron-right"></span>
          </div>
        </div>
      </router-link>
    </transition>

    <div class="full-card filter-card">
      <kite-select
        id="device-filter"
        label="Device Status"
        :onChange="onFilterChange"
        :value="filterVal"
      >
        <option value="" default>Show All</option>
        <option value="connected">Connected</option>
        <option value="paused">Paused</option>
        <option value="disconnected">Disconnected</option>
      </kite-select>
    </div>


    <div class="link-card devices-card">
      <template v-if="shownDevices.length">
        <swipe-row
          v-for="device in shownDevices"
          :key="device.id"
          :title="device.name"
          :details="device.model"
          :onClick="() => onRowClick(device)"
          :rowIcon="device.icon"
          :rightValue="(checkIfDevicePaused(device)) ? 'Paused' : (device.connected) ? 'Connected' : 'Disconnected'"
          :rightValueClass="(!device.connected) ? 'red' : (checkIfDevicePaused(device)) ? 'grey' : 'green'"
          primaryBtnLabel="Schedule"
          primaryBtnColor="#5CB8FC"
          :primaryBtnAction="() => onRowSchedule(device)"
          :secondaryBtnLabel="(checkIfDevicePaused(device)) ? 'Unpause' : 'Pause'"
          secondaryBtnColor="#63738a"
          :secondaryBtnAction="() => checkPauseUnpause(device)"
        />
      </template>
      <template v-else>
        <div class="link-card-item no-link null-item">
          <template v-if="filterVal === 'connected'">
            There are currently no devices connected to your network.
          </template>
          <template v-else-if="filterVal === 'disconnected'">
            None of your home devices are currently disconnected from your network.
          </template>
          <template v-else="filterVal === 'paused'">
            There are currently no paused devices on your network.
          </template>
        </div>
      </template>
    </div>


    <!-- Pause Action Sheet -->
    <!-- ---- -->
    <div
      class="pop-up-action-sheet"
      :class="{'show': canShowPauseActions}"
      @click="hideActionSheet"
    >
      <div class="pu-all-actions-container">
        <div class="pu-actions">
          <button
            class="pu-action-btn"
            @click="() => setDevicePause(0.5)"
          >
            For 30 Minutes
          </button>
          <button
            class="pu-action-btn"
            @click="() => setDevicePause(2)"
          >
            For 2 Hours
          </button>
          <button
            class="pu-action-btn"
            @click="() => setDevicePause(24)"
          >
            Until End of Day
          </button>
          <button
            class="pu-action-btn"
            @click="() => setDevicePause(0)"
          >
            Until I Unpause
          </button>
          <button
            class="pu-action-btn"
            @click="() => onRowSchedule(tempActionSheetDevice)"
          >
            Create a Pause Schedule
          </button>
        </div>
        <div class="pu-cancel">
          <button
            class="pu-action-btn pu-cancel"
            @click="hideActionSheet"
          >
            Cancel
          </button>
        </div>
      </div>
    </div>


  </div>

</template>



<script>

import { KiteSelect } from '@kite/vue-kite';
import SwipeRow from '../components/swipe-row.vue';

import { mapState } from 'vuex';
import devices from '../data/scp-devices';
import { days } from '../utils/time';


const now = new Date();



export default {
  name: 'scp-devices',
  components: {
    KiteSelect,
    SwipeRow
  },

  data: () => ({
    filterVal: '',
    currentDay: null,
    currentHour: null,

    // Action Sheet
    tempActionSheetDevice: null,
    canShowPauseActions: false,
  }),

//-----

  computed: {
    ...mapState([
      'connectedDevices',
      'focusedConnectedDeviceId',
    ]),
    scheduleNumber() {
      return this.connectedDevices.filter(device => !!device.schedule).length;
    },
    activeScheduleNumber() {
      return this.connectedDevices.filter(device => device.schedule && this.checkIfDevicePaused(device)).length;
    },
    shownDevices() {
      if (!this.filterVal) {
        return this.connectedDevices;

      } else {
        switch (this.filterVal) {
          case 'connected':
            return this.connectedDevices.filter((device) => device.connected);
            break;
          case 'paused':
            return this.connectedDevices.filter((device) => this.checkIfDevicePaused(device));
            break;
          case 'not-connected':
          default:
            return this.connectedDevices.filter((device) => !device.connected);
        }
      }
    },
  },

//-----

  methods: {
    onFilterChange(e) {
      this.filterVal = e.target.value;
    },
    onRowClick(device) {
      this.$store.commit('setFocusedDeviceId', device.id);
      this.$router.push({
        path: '/services/scp/device',
        query: {
          title: device.name
        },
      });
    },
    onRowSchedule(device) {
      this.$store.commit('setFocusedDeviceId', device.id);
      this.$router.push({
        path: '/services/scp/device',
        query: {
          title: device.name,
          schedule: 'show',
        },
      });
    },

    // Action Sheet
    //----
    checkPauseUnpause(device) {
      this.tempActionSheetDevice = device;
      if (this.checkIfDevicePaused(device)) {
        // Already paused, unpause
        this.unpauseDevice();

      } else {
        // Pause device
        this.showActionSheet();
      }
    },
    showActionSheet(device) {
      this.canShowPauseActions = true;
    },
    hideActionSheet() {
      this.canShowPauseActions = false;
    },
    setDevicePause(time) {
      let pauseLength;

      switch (time) {
        case 0.5:
          pauseLength = '30 minutes';
          break;
        case 2:
          pauseLength = '2 hours';
          break;
        case 24:
          pauseLength = this.getRestOfTodayTime();
          break;
        case 0:
        default:
          pauseLength = 1;
          break;
      }
      
      const pausePayload = {
        id: this.tempActionSheetDevice.id,
        length: pauseLength,
      };

      this.$store.commit('pauseDevice', pausePayload);
    },
    getRestOfTodayTime() {
      const hoursLeft = 23 - now.getHours();
      const minutesLeft = 60 - now.getMinutes();

      return this.formatTimeString(hoursLeft, minutesLeft);
    },
    formatTimeString(hoursLeft, minutesLeft) {
      let returnString = '';

      if (hoursLeft) {
        returnString += hoursLeft;
        
        if (hoursLeft > 1) {
          returnString += ' hours ';
        } else {
          returnString += ' hour ';
        }
      }

      if (minutesLeft) {
        returnString += minutesLeft;

        if (minutesLeft > 1) {
          returnString += ' minutes';
        } else {
          returnString += ' minute';
        }
      }

      return returnString;
    },

    unpauseDevice() {
      if (this.tempActionSheetDevice.tempPause) {
        this.$store.commit('unpauseDevice', this.tempActionSheetDevice.id);
      } else {
        const newSchedule = clone(this.tempActionSheetDevice.schedule);
        newSchedule.enabled = false;
        this.$store.commit('setDeviceSchedule', newSchedule);
      }
    },

    //-----

    saveCurrentTime() {
      this.currentDay = days[now.getDay()].toLowerCase();
      this.currentHour = now.getHours();
    },
    checkIfDevicePaused(deviceObj) {
      if (deviceObj.tempPause) {
        return true;

      } else if (deviceObj.schedule && deviceObj.schedule.enabled && deviceObj.schedule.days) {
        const todaySchedule = deviceObj.schedule.days.find((day) => day.name === this.currentDay);

        if (todaySchedule) {
          // convert times 0-23 time
          const startInt = this.convertTimeToInt(todaySchedule.start);
          const endInt = this.convertTimeToInt(todaySchedule.end);

          return ( (this.currentHour >= startInt) && (this.currentHour < endInt) );
        } else {
          return false;
        }

      } else {
        return false;
      }
    },
    convertTimeToInt(timeString) {
      const isPM = timeString.includes('pm');
      const splitString = timeString.split(':');
      const rawInt = parseInt(splitString[0]);

      if (rawInt === 12) {
        return (isPM) ? 12 : 0;
      } else {
        return (isPM) ? (rawInt + 12) : rawInt;
      }
    },
  },

//-----

  created() {
    this.saveCurrentTime();
  }
}

</script>



<style lang="scss">

.view.view-scp-devices {
  padding-bottom: 40px;

  .filter-card {
    margin-bottom: 8px;
  }

  .devices-card {
    overflow: hidden;

    .swipe-row {
      border-bottom: 1px solid $kite-gray-2;

      &:last-child {
        border: 0;
      }
    }
    .null-item {
      padding-top: 24px;
      padding-bottom: 24px;
      display: block;
      text-align: center;
    }
  }
}

</style>
