
// import { firestore } from 'firebase';
import localUser from './data/fake-user';
import scpDevices from './data/scp-devices';


function clone(x) {
  return JSON.parse(JSON.stringify(x));
}


const USE_LOCAL = true; // toggle to just use local data, rather than fetching from firebase, and stay logged in on refreshes. Useful for development


const defaultStore = {

  state: {
    useLocal: USE_LOCAL,

    // App state
    initialLoadDone: false,
    scrollLocked: false,
    allUsers: [],
    user: (USE_LOCAL) ? localUser : {},

    // Details
    detailsUser: {},

    // Action sheet
    actionSheetTitle: '',
    actionSheetItems: '',
    actionSheetOnSelect: '',
    canShowActionSheet: false,

    // Billing
    currentDetail: null,
    selectedBillId: null,
    selectedCompareBillId: null,
    currentBill: null,
    makePaymentStep: 0,

    // Advanced WiFi Settings
    currentDeviceId: null,
    currentPortId: null,

    // SCP
    connectedDevices: scpDevices,
    focusedConnectedDeviceId: null,
  },

//-----------

  mutations: {
    setInitialLoadDone: function(state) {
      state.initialLoadDone = true;
    },
    setAllUsers: function(state, val) {
      state.allUsers = val;
    },
    setUser: function(state, val) {
      state.user = val;
    },
    setDetailsUser: function(state, val) {
      state.detailsUser = val;
    },
    signOut: function(state) {
      state.user = {};
    },

    // Scroll
    lockScroll(state) {
      state.scrollLocked = true;
    },
    unlockScroll(state) {
      state.scrollLocked = false;
    },

    // Action sheet
    showActionSheet(state, sheetConfig) {
      state.actionSheetTitle = sheetConfig.title;
      state.actionSheetItems = sheetConfig.items;
      state.actionSheetOnSelect = sheetConfig.onSelect;
      state.canShowActionSheet = true;
      state.scrollLocked = true;
    },
    hideActionSheet(state) {
      state.canShowActionSheet = false;
      state.scrollLocked = false;
    },

    // Billing
    setCurrentDetail(state, detail) {
      state.currentDetail = detail;
    },
    setSelectedBillId(state, id) {
      state.selectedBillId = id;
    },
    setSelectedCompareBillId(state, id) {
      state.selectedCompareBillId = id;
    },
    setCurrentBill(state, bill) {
      state.currentBill = bill;
    },

    updateEmail(state, val) {
      state.user.contact.email.personal.address = val;
    },
    updatePhone(state, val) {
      state.user.contact.phone.number = val;
    },
    togglePaperless(state) {
      state.user.billing.usePaperless = !state.user.billing.usePaperless;
    },

    nextMakePaymentStep(state) {
      state.makePaymentStep++;
    },
    previousMakePaymentStep(state) {
      state.makePaymentStep--;
    },
    setMakePaymentStep(state, step) {
      state.makePaymentStep = step;
    },
    savePaymentMethod(state, method) {
      state.user.billing.savedMethods.push(method);
    },
    deletePaymentMethod(state, methodDeleted) {
      state.user.billing.savedMethods = state.user.billing.savedMethods.filter((method) => !(method.id === methodDeleted.id));
    },
    submitPayment(state, payment) {
      const isToday = (payment.date.getUTCDate() === new Date().getUTCDate());

      // Example Payment = {
      //   amount: 123.00,
      //   date: Date(),
      //   enrollInAutopay: false,
      //   paymentMethod: {type,accountNumbers,...}
      // }

      if (isToday) {
        state.user.billing.transactions.push(payment);
        state.user.billing.currentBalance -= payment.amount;
        state.user.billing.amountDue -= payment.amount;

        if (state.user.billing.isPastDue) {
          state.user.billing.pastDueAmount = (payment.amount > state.user.billing.pastDueAmount) ? 0 : (state.user.billing.pastDueAmount - payment.amount);
          
          if (!state.user.billing.pastDueAmount) {
            state.user.billing.isPastDue = false;
          }
        }
      } else {
        state.user.billing.scheduledPayments.push(payment);
      }

    },
    cancelScheduledPayment(state, i) {
      state.user.billing.scheduledPayments.splice(i, 1);
    },

    autopayEnroll(state, payment) {
      state.user.billing.autopay.enrolled = true;
      state.user.billing.autopay.method = payment; 
    },
    cancelAutopay(state) {
      state.user.billing.autopay.enrolled = false;
    },

    cancelOutageNotification(state) {
      state.user.outage.canShow = false;
    },

    // Simple WiFi Settings

    saveNetworkInfo(state, payload) {
      Object.assign(state.user.wifi, payload);
    },

    // Advanced Wifi settings

    // selectSubnet(state, selected) {
    //   state.user.advancedWifi.subnetting = state.user.advancedWifi.subnetting.map((address) => {
    //     address.selected = (selected.value === address.value);
    //     return address;
    //   });
    // },
    saveCustomDns(state, dnsConfig) {
      state.user.advancedWifi.customDns = dnsConfig;
    },
    saveUpnp(state, isOn) {
      state.user.advancedWifi.upnp.use = isOn;
    },
    setCurrentDeviceId(state, id) {
      state.currentDeviceId = id;
    },
    saveDevice(state, device) {
      const { ipReservations } = state.user.advancedWifi;
      const isEdit = ipReservations.find((ipReservation) => ipReservation.id === device.id);

      if (isEdit) {
        state.user.advancedWifi.ipReservations = ipReservations.map((ipReservation) => {
          return (ipReservation.id === device.id) ? device : ipReservation;
        });
      } else {
        state.user.advancedWifi.ipReservations.push(device);
      }
    },
    toggleReserveIp(state, isOn) {
      state.user.advancedWifi.ipReservations = state.user.advancedWifi.ipReservations.map((device) => {
        if (device.id === state.currentDeviceId) {
          device.ipReserved = isOn;
        }
        return device;
      });
    },
    setCurrentPortId(state, portId) {
      state.currentPortId = portId;
    },
    savePortChange(state, port) {
      state.user.advancedWifi.ipReservations = state.user.advancedWifi.ipReservations.map((device) => {
        if (device.id === state.currentDeviceId) {
          device.ports = device.ports.map((ogPort) => {
            return (ogPort.id === state.currentPortId) ? port : ogPort;
          });
        }

        return device;
      });
    },
    addPort(state, port) {
      state.user.advancedWifi.ipReservations = state.user.advancedWifi.ipReservations.map((device) => {
        if (device.id === state.currentDeviceId) {
          device.ports.push(port);
        }

        return device;
      });
    },
    deletePort(state) {
      state.user.advancedWifi.ipReservations = state.user.advancedWifi.ipReservations.map((device) => {
        if (device.id === state.currentDeviceId) {
          device.ports.splice(device.ports.findIndex((port) => {
            port.id === state.currentPortId;
          }));
        }

        return device;
      });
    },

    // User Contact Info
    setUserRole(state, newRole) {
      if (newRole === 'Primary' || newRole === 'Admin' || newRole === 'Standard') {
        state.user.role = newRole;
      } else {
        console.log('Failed to set user role to %s. Please set to either "Priamry", "Admin", or "Standard"', newRole);
      }
    },
    setContactRole(state, contactId, newRole) {
      const contact = state.user.users.find((contact) => contact.id === contactId);
      contact.role = newRole;
    },
    setFirstName(state, name) {
      state.user.firstName = name;
    },
    setLastName(state, name) {
      state.user.lastName = name;
    },
    setPrimaryEmail(state, email) {
      state.user.contact.email.personal.address = email;
    },
    setPrimaryPhone(state, phone) {
      state.user.contact.phone.number = phone;
    },
    setPrimaryPhoneType(state, isMobile) {
      state.user.contact.phone.isMobile = isMobile;
    },
    setAlternateEmails(state, emails) {
       state.user.additionalContacts.email = emails;
    },
    setAlternatePhones(state, phones) {
      state.user.additionalContacts.phone = phones;
    },
    purchaseTV(state) {
      state.user.services.tv = true;
    },

    // SCP
    setFocusedDeviceId(state, deviceId) {
      state.focusedConnectedDeviceId = deviceId;
    },
    setDeviceName(state, newName) {
      state.connectedDevices = state.connectedDevices.map((device) => {
        if (device.id === state.focusedConnectedDeviceId) {
          device.name = newName;
        }
        return device;
      });
    },
    setDeviceSchedule(state, newScheduleObj) {
      state.connectedDevices = state.connectedDevices.map((device) => {
        if (device.id === state.focusedConnectedDeviceId) {
          device.schedule = newScheduleObj;
        }
        return device;
      });
    },
    clearDeviceSchedule(state, deviceId) {
      state.connectedDevices = state.connectedDevices.map((device) => {
        if (device.id === state.focusedConnectedDeviceId) {
          device.schedule = null;
        }
        return device;
      });
    },
    pauseDevice(state, payload) { // payload: {id: 0, length: ''}
      state.connectedDevices = state.connectedDevices.map((device) => {
        if (device.id === payload.id) {
          device.tempPause = payload.length;
        }
        return device;
      });
    },
    unpauseDevice(state, deviceId) {
      state.connectedDevices = state.connectedDevices.map((device) => {
        if (device.id === deviceId) {
          device.tempPause = false;
        }
        return device;
      });
    },

  },

//-----------

  getters: {
    scpFocusedDevice(state) {
      return state.connectedDevices.find((device) => device.id === state.focusedConnectedDeviceId) || {};
    },
  },

//-----------

  actions: {
    updateUsers(context) {
      const users = [];

      firestore().collection('users').get().then((querySnapshot) => {
        querySnapshot.forEach((queryDoc) => {
          users.push(queryDoc.data());
        });

      });

      context.commit('setAllUsers', users);
    },
  },

};


export default defaultStore;