
<template>

  <div class="view view-users">
    <div class="link-card">
      <div
        class="link-card-item"
        v-for="user in allUsers"
        @click="showDetails(user)"
      >
        {{user.username}}
      </div>
    </div>
  </div>

</template>



<script>

import { 
  KiteButton,
  KiteTextInput,
} from '@kite/vue-kite';
import InputPasswordToggle from '../components/input-password-toggle.vue';
import { mapState } from 'vuex';


export default {
  name: 'users',
  components: {
    KiteButton,
    KiteTextInput,
    InputPasswordToggle,
  },

//-----

  data: function() {
    return {
    }
  },

//-----

  computed: {
    ...mapState([
      'allUsers'
    ]),
  },

//-----

  methods: {
    showDetails: function(user) {
      this.$store.commit('setDetailsUser', user);
      this.$router.push('/user-details');
    }
  },

}

</script>



<style lang="scss">

.view-users {

}

</style>
