
<template>

  <div class="view view-port-details">

    <section class="section-port-assignment">
      <h2 class="section-title">{{currentPort.name}}</h2>

      <div class="full-card">

        <div class="full-card-section">
          <div class="card-item">
            <div class="label">IP Address</div>
            {{currentDevice.ipAddress}}
          </div>
          <div class="card-item">
            <div class="label">MAC Address</div>
            {{currentDevice.macAddress}}
          </div>
        </div>

        <div class="full-card-section">
          <div class="card-item">
            <div class="label">Assignment Name</div>
            {{currentPort.name}}
          </div>
          <div class="card-item">
            <div class="label">External Port</div>
            {{currentPort.external}}
          </div>
          <div class="card-item">
            <div class="label">Protocol</div>
            {{currentPort.type}}
          </div>
        </div>

        <div class="port-action">
          <kite-button
            type="primary"
            size="full"
            :onClick="onShowEdit"
          >
            Manage Port Assignment
          </kite-button>
          <kite-button
            type="standalone-link"
            size="full"
            :onClick="onDeletePort"
          >
            Delete Port Assignment
          </kite-button>
        </div>

      </div>
    </section>


    <!-- Manage Port Modal -->

    <modal-full-page
      :canShow="canShowEdit"
      modalTitle="Port Assignment"
      :onBack="onEditCancel"
      :onSave="onEditSave"
      newClass="edit-port-modal"
    >
      <kite-text-input
        label="Assignment Name"
        id="name"
        :value="tempPort.name"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />
      <kite-text-input
        label="External Port"
        id="external"
        :value="tempPort.external"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />
      <kite-text-input
        label="Internal Port"
        id="internal"
        :value="tempPort.internal"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />

      <div class="full-width-select">
        <p class="select-card-title">Protocol</p>
        <kite-select
          label=""
          id="type"
          :value="tempPort.type"
          :onChange="onInputChange"
        >
          <option default value="tcp+udp">TCP + UDP</option>
          <option value="tcp">TCP</option>
          <option value="udp">UDP</option>
        </kite-select>
      </div>

    </modal-full-page>

  </div>

</template>



<script>

import { 
  KiteTextInput,
  KiteSwitch,
  KiteSelect,
  KiteButton,
} from '@kite/vue-kite';
import ModalFullPage from '../components/modal-full-page.vue';
import { mapState } from 'vuex';

function clone(obj) {
  return JSON.parse(JSON.stringify(obj));
}


export default {
  name: 'port-details',
  components: {
    KiteTextInput,
    KiteSwitch,
    KiteSelect,
    KiteButton,
    ModalFullPage,
  },

//-----

  data: () => ({
    currentDevice: null,
    currentPort: null,
    canShowEdit: false,

    tempPort: null,
  }),

//-----

  computed: {
    ...mapState([
      'user',
      'currentPortId',
      'currentDeviceId',
    ]),
  },

//-----

  methods: {
    getCurrentDevice() {
      if (this.currentDeviceId) {
        this.currentDevice = this.user.advancedWifi.ipReservations.find((device) => device.id === this.currentDeviceId);
      } else {
        this.currentDevice = this.user.advancedWifi.ipReservations[0];
      }
    },
    getCurrentPort() {
      this.currentPort = this.currentDevice.ports.find((port) => port.id === this.currentPortId);
    },
    setTempValues() {
      this.tempPort = clone(this.currentPort);
    },

    onInputChange(e) {
      this.tempPort[e.target.name] = e.target.value;
    },

    onShowEdit() {
      this.setTempValues();
      this.canShowEdit = true;
    },
    onEditSave() {
      this.$store.commit('savePortChange', this.tempPort);
      this.getCurrentPort();
      this.canShowEdit = false;
    },
    onEditCancel() {
      this.canShowEdit = false;
    },

    onDeletePort() {
      this.$store.commit('deletePort');
      this.$router.go(-1);
    },
  },

//-----

  created() {
    this.getCurrentDevice();
    this.getCurrentPort();
    this.setTempValues();
  },

}

</script>



<style lang="scss">

.view-port-details {
  padding-top: 40px;
  padding-bottom: 40px;

  .full-width-select {
    .select-card-title {
      margin-bottom: 8px;
    }
    .input-group {
      margin: 0;

      .label {
        display: none;
      }
      .kite-form-control {
        position: relative;
        left: -16px;
        width: calc(100% + 32px);
        padding-left: 16px;
        border-left: 0;
        border-right: 0;
        background-size: 1.3rem 1.3rem;
        background-position: calc(100% - 16px) 50%;

        &:focus {
          padding-top: 9px;
          padding-bottom: 9px;
        }
      }
    }
  }
}

</style>
