
<template>

  <div
    class="action-sheet"
    :class="{
      'show': canShowActionSheet
    }"
  >
    <div class="action-sheet-header app-header">
      <button
        class="back-button"
        @click.prevent="closeSheet"
      >
        <span class="si si-chevron-left"></span>
      </button>
      <div class="title show">{{ actionSheetTitle }}</div>
    </div>

    <div class="action-sheet-body">
      
      <div class="action-items">
        <div
          v-for="item in actionSheetItems"
          class="action-item"
          :class="{
            'selected': item.selected
          }"
          @click="onSelect(item)"
        >
          {{ item.value }}
        </div>
      </div>

    </div>
  </div>

</template>



<script>

import { mapState } from 'vuex';


export default {
  name: 'action-sheet',

//------

  data: function() {
    return {
    };
  },

//------

  computed: {
    ...mapState([
      'actionSheetTitle',
      'actionSheetItems',
      'actionSheetOnSelect',
      'canShowActionSheet',
    ]),
  },

//------

  methods: {
    onSelect: function(item) {
      this.actionSheetOnSelect(item);
      this.closeSheet();
    },
    closeSheet: function() {
      this.$store.commit('hideActionSheet');
    },
  },

}

</script>



<style lang="scss">

.action-sheet {
  position: fixed;
  opacity: 0;
  z-index: 7;
  top: 0;
  left: 120%;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  transition: transform 0.5s, opacity 0.5s;

  &.show {
    opacity: 1;
    transform: translateX(-120%);
  }

  .action-sheet-header {
    flex: 0 0 auto;
  }
  .action-sheet-body {
    flex: 1 0 0;
    background-color: $kite-gray-1;

    .action-items {
      padding-left: 44px;
      background-color: $kite-white;
      border-bottom: 1px solid $kite-gray-2;

      .action-item {
        position: relative;
        padding: 10px 16px 10px 0;
        border-bottom: 1px solid $kite-gray-2;

        &:last-child {
          border: 0;
        }
        &.selected {
          &:before {
            content: "\E661";
            font-family: 'Spectrum Icons';
            display: inline-block;
            position: absolute;
            right: calc(100% + 12px);
            top: 10px;
            color: $kite-blue-3;
          }
        }
      }
    }
  }
}

</style>
