
<template>

  <make-payment-base
    title="Payment Method"
    :show="show"
    :onClose="onClose"
    :onSubmit="onSaveMethod"
  >
    <div class="make-payment-method">
      <div class="method-tabs">
        <div
          class="tab"
          :class="{
            'selected': (paymentType === 'account')
          }"
          @click="paymentType = 'account'"
        >
          Checking/Savings
        </div>
        <div
          class="tab"
          :class="{
            'selected': (paymentType === 'card')
          }"
          @click="paymentType = 'card'"
        >
          Credit/Debit Card
        </div>
      </div>
      <div
        class="method-tab-window"
        :class="{
          'show-card-tab': (paymentType === 'card'),
        }"
      >
        <div class="method-tab-slider">

          <div class="method-tab account-tab">

            <kite-select
              id="accountType"
              label="Account Type"
              :value="accountType"
              :onChange="onInputChange"
            >
              <option value="checking">Checking</option>
              <option value="savings">Savings</option>
            </kite-select>
            <kite-text-input
              label="Routing Number"
              id="routingNumber"
              name="routingNumber"
              :value="routingNumber"
              :onChange="onInputChange"
            />
            <kite-text-input
              label="Account Number"
              id="accountNumber"
              name="accountNumber"
              :value="accountNumber"
              :onChange="onInputChange"
            />

            <div class="link-card no-border">
              <div class="link-card-item switch-item no-link">
                <div class="label-cell">Save for Future Payments</div>
                <div class="value-cell">
                  <kite-switch
                    id="saveMethodSwitch"
                    name="saveMethodSwitch"
                    :isOn="saveMethod"
                    :onChange="(e) => saveMethod = e.target.checked"
                  />
                </div>
              </div>
            </div>
          </div>

          <!--  -->

          <div class="method-tab card-tab">
            <div class="card-input">
              <kite-formatted-input
                formatType="credit-card"
                label="Card Number"
                id="payment-card"
                :value="cardVal"
                v-on:cleave-input="setCardVal"
                :onCreditCardTypeChange="setCardType"
              />
              <div class="card-icons">
                <transition name="fade" mode="out-in">
                  <img
                    v-if="!cardType"
                    class="camera-icon"
                    src="../assets/images/icon-camera.svg"
                    alt="camera-icon-button"
                  >
                  <card-logos
                    v-else
                    :cardType="cardType"
                  />
                </transition>
              </div>
            </div>
            <kite-formatted-input
              formatType="date"
              label="Expiration Date"
              id="payment-card"
              :value="cardExp"
              v-on:cleave-input="setCardExp"
              placeholder="MM/YY"
              className="exp-input"
            />

            <div class="link-card no-border">
              <div class="link-card-item switch-item no-link">
                <div class="label-cell">Save for Future Payments</div>
                <div class="value-cell">
                  <kite-switch
                    id="saveMethodSwitch"
                    name="saveMethodSwitch"
                    :isOn="saveMethod"
                    :onChange="(e) => saveMethod = e.target.checked"
                  />
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>

    </div>
  </make-payment-base>

</template>



<script>

import {
  KiteTextInput,
  KiteSelect,
  KiteFormattedInput,
  KiteSwitch,
} from '@kite/vue-kite';
import CardLogos from './card-logos.vue';
import MakePaymentBase from './make-payment-base.vue';


export default {
  name: 'make-payment-method',
  components: {
    KiteTextInput,
    KiteSelect,
    KiteFormattedInput,
    KiteSwitch,
    CardLogos,
    MakePaymentBase,
  },

//-----

  props: [
    'show',
    'onClose',
    'onSubmit',
    'method',
  ],

//-----

  data: function() {
    return {
      paymentType: 'account',

      cardVal: '',
      cardType: '',
      cardExp: '',

      accountType: 'checking',
      accountNumber: '',
      routingNumber: '',

      saveMethod: false,
    }
  },

//-----

  computed: {
    paymentMethod: function() {
      return (this.paymentType === 'card') ?
        {
          paymentType: 'card',
          cardVal: this.cardVal,
          cardExp: this.cardExp,
          cardType: this.cardType,
          saveMethod: this.saveMethod,
        }
        :
        {
          paymentType: this.accountType,
          accountNumber: this.accountNumber,
          routingNumber: this.routingNumber,
          saveMethod: this.saveMethod,
        };
    },
  },

//-----

  methods: {
    setCardVal: function(val) {
      this.cardVal = val;
    },
    setCardType: function(type) {
      this.cardType = type;
    },
    setCardExp: function(val) {
      this.cardExp = val;
    },
    onInputChange: function(e) {
      this[e.target.name] = e.target.value;
    },
    onAccountTypeChange(isChecking) {
      this.accountNumber = '';
      this.routingNumber = '';
    },
    resetValues() {
      this.paymentType = 'card';
      this.cardVal = '';
      this.cardType = '';
      this.cardExp = '';
      this.accountType = 'checking';
      this.accountNumber = '';
      this.routingNumber = '';
      this.saveMethod = false;
    },
    onSaveMethod() {
      this.onSubmit(this.paymentMethod);
      this.resetValues();
    }
  },

//-----

  mounted() {
    if (this.method) {
      Object.assign(this, this.method);
    }
  },

}

</script>



