
<template>

  <div class="si-view step-connecting si-step">

    <div class="self-install-content">
      <div class="connecting-loader">
        <loader-countdown
          :percent="timerPercentage"
          remainingString="0 Min."
        />
      </div>

      <div class="checkmark-list progress-list">
        <div
          class="checkmark-item"
          :class="{'active': (timerPercentage > 1)}"
        >
          Connecting Your Modem
        </div>
        <div
          class="checkmark-item"
          :class="{'active': (timerPercentage > 40)}"
        >
          Checking Signal Strength
        </div>
        <div
          class="checkmark-item"
          :class="{'active': (timerPercentage > 70)}"
        >
          Connecting to Your Modem
        </div>
      </div>
    </div>

    <div class="self-install-actions">
      <router-link
        to="/self-install/modem/success"
        class="kite-btn kite-btn-primary kite-btn-block"
        :class="{'disabled': (timerPercentage < 100)}"
      >
        Activate Modem
      </router-link>
    </div>
  </div>

</template>



<script>

import LoaderCountdown from '../components/loader-countdown.vue';


export default {
  name: 'modem-step-connecting',
  components: {
    LoaderCountdown,
  },

//----
  
  data: () => ({
    timerPercentage: 1,
    timerInterval: null,
  }),

//----

  methods: {
    startFakeTimer() {
      const TOTAL_TIME = 4000;
      const UDPATES = 30;
      const updateInterval = Math.round(TOTAL_TIME / UDPATES);
      const updateIncrement = Math.round(100 / UDPATES);

      this.timerInterval = window.setInterval(() => {
        this.timerPercentage += updateIncrement;
        
        if (this.timerPercentage >= 100) {
          window.clearInterval(this.timerInterval);
        }
      }, updateInterval);
    },
  },

//----

  mounted() {
    this.startFakeTimer();
  },

};

</script>



