
<template>

  <div
    class="article-accordion"
    :class="{'open': isOpen}"
  >
    <div
      class="accordion-title-row"
      @click="toggleDrawer"
    >
      <div class="accordion-title">{{title}}</div>
      <div class="accordion-arrow">
        <span class="si si-chevron-down"></span>
      </div>
    </div>
    <div
      class="accordion-drawer"
      :style="{'max-height': currentHeight+'px'}"
    >
      <div class="accordion-content">
        <slot></slot>
      </div>
    </div>
  </div>

</template>



<script>

export default {
  name: 'article-accordion',
  props: [
    'title',
  ],

//------

  data: function() {
    return {
      isOpen: false,
      openHeight: 0,
    };
  },

//------

  computed: {
    currentHeight: function() {
      return (this.isOpen) ? this.openHeight : 0;
    }
  },

//------

  methods: {
    setOpenHeight: function() {
      this.openHeight = this.contentEl.getBoundingClientRect().height;
    },
    toggleDrawer: function() {
      this.setOpenHeight();
      this.isOpen = !this.isOpen;
    }
  },

//------

  mounted: function() {
    this.contentEl = this.$el.querySelector('.accordion-content')
  }

}

</script>



<style lang="scss">

$drawer-curve: cubic-bezier(0.52, 0.08, 0.14, 0.88);


.article-accordion {
  position: relative;
  padding: 10px 16px 10px 0;
  border-bottom: 1px solid $kite-gray-2;

  &:first-child {
    border-top: 1px solid $kite-gray-2;
  }

  &:last-child {
    border-bottom: 0;
  }

  .accordion-title-row {
    position: relative;
  }
  .accordion-arrow {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    color: $kite-gray-2;
    transition: transform 0.4s;
  }
  .accordion-drawer {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    transition:
      max-height 0.3s 0.1s $drawer-curve,
      opacity 0.3s;

    .accordion-content {
      padding: 24px 0 14px;

      *:last-child {
        margin-bottom: 0;
      }
    }
  }

  &.open {
    .accordion-arrow {
      transform: translateY(-50%) rotate(-180deg);
    }
    .accordion-drawer {
      // max height set in js 
      opacity: 1;
      transition:
        max-height 0.4s $drawer-curve,
        opacity 0.3s 0.2s;
    }
  }
}

</style>
