
<template>

  <div class="view view-si-router">
  
    <transition :name="transitionName">
      <router-view></router-view>
    </transition>

  </div>

</template>



<script>

export default {
  name: 'self-install-router',

  //----

  data: () => ({
    transitionName: 'slide-right',
    routerRouteObj: null,
  }),

  //-----

  methods: {
    saveRouterRoutes() {
      const routePathBase = (this.$route.path.includes('modem')) ? '/self-install/modem' : '/self-install/router'
      this.routerRouteObj = this.$router.options.routes.find((route) => route.path === routePathBase);
      this.routerChildRoutes = this.routerRouteObj.children;
    },
    setTransition(to, from) {
      const currentRouteIndex = this.routerChildRoutes.findIndex(route => from.path.includes(route.path));
      const nextRouteIndex = this.routerChildRoutes.findIndex(route => to.path.includes(route.path));

      if (from.matched[0].path !== to.matched[0].path) {
        // Router to modem, or vice verse
        this.transitionName = (nextRouteIndex < currentRouteIndex) ? 'top-comes-from-right' : 'top-slides-to-right';

      } else {
        // Within router/modem view
        this.transitionName = (nextRouteIndex < currentRouteIndex) ? 'slide-left' : 'slide-right';
      }
    },
  },

  //----

  mounted: function() {
    this.saveRouterRoutes();

    this.$router.beforeEach((to, from, next) => {
      this.setTransition(to, from);
      next();
    });
  },
};

</script>



<style lang="scss">

.view-si-router {
  .si-view {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    // padding-bottom: 24px;
  }
}

</style>

