<script>

import { mapState } from 'vuex';


export default {
  name: 'tutorial-phone',
  props: [
    'showSlide'
  ],

//------

  computed: {
    translateValue() {
      return (this.showSlide) * 103.5; // width per slide
    },
    currentSlideClass() {
      return 'slide'+this.showSlide;
    },
  },

}

