<style lang="scss">

@keyframes spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes wifiAppear1 {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes wifiAppear2 {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes wifiAppear3 {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes addUserBtn {
  0% {
    transform: translate(-42px, -51px);
  }
  20% {
    transform: translate(0, -51px);
  }
  33% {
    transform: translate(0, -51px);
  }
  53% {
    transform: translate(-42px, 0);
  }
  66% {
    transform: translate(-42px, 0);
  }
  87% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}


.tour-phone {
  .tour-phone-screen {
    overflow: hidden;
  }
  #phone-slides {
    transition: transform 0.6s 0.1s cubic-bezier(0.55, 0.24, 0.24, 0.92);
  }
  #frame {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      bottom: -40px;
      width: 100%;
      height: 20px;
      background-color: red;
      border-radius: 50%;
    }
  }

  // Slide 1 - Autopay
  //---------
  #payment-arrows {
    position: relative;
    animation: spin 5s infinite linear;
    transform-origin: 12% 6%;
  }

  // Slide 2 - Wifi Alert
  //---------
  #screen-3 {
    #error-bar {
      transform: translateY(-15%);
      transition: transform 0.4s 0.7s cubic-bezier(0, 0.7, 0.31, 1.35);
    }
    #wifi-top,
    #wifi-mid,
    #wifi-bottom {
      opacity: 0;
      transition: 0.2s 0.6s;
    }
  }
  &.slide2 {
    #screen-3 {
      #error-bar {
        transform: translateY(0);
      }
      #wifi-bottom {
        animation: wifiAppear1 3s 1.3s alternate infinite ease-in;
        // animation: wifiAppear1 5s 1.3s infinite linear;
      }
      #wifi-mid {
        animation: wifiAppear2 3s 1.3s alternate infinite ease-in;
        // animation: wifiAppear2 5s 1.3s infinite linear;
      }
      #wifi-top {
        animation: wifiAppear3 3s 1.3s alternate infinite ease-in;
        // animation: wifiAppear3 5s 1.3s infinite linear;
      }
    }
  }

  // Slide 3 - Add Users
  //---------
  #screen-4 {
    #user-top-left,
    #user-top-right,
    #user-bottom-left {
      opacity: 0;
      transform: scale(0.8);
      transition:
        transform 0.1s 0.6s,
        opacity 0.1s 0.6s;
    }
    #user-plus-btn {
      transform: translate(-42px, -51px);
    }
  }
  &.slide3 {
    #screen-4 {
      #user-top-left,
      #user-top-right,
      #user-bottom-left {
        opacity: 1;
        transform: scale(1);
        transition:
          transform 0.4s cubic-bezier(0, 0.7, 0.31, 1.35),
          opacity 0.4s;
      }
      #user-top-left {
        transition-delay: 1s;
        transform-origin: 35px 45px;
      }
      #user-top-right {
        transition-delay: 2.0s;
