
<template>

  <div class="view view-port-forwarding">

    <div class="link-card about-card">
      <article-accordion
        title="About Port Forwarding & IP Reservations"
      >
        <p>IP reservations assign a consistent IP address to a device. This is commonly used for WiFi printers.</p>
        <p>Port forwarding enables external applications to access an assigned device on your network. This is commonly used for multi-player gaming, remote computer access or peer-to-peer file sharing.</p>
        <p>An IP reservation is required to set up port forwarding.</p>
      </article-accordion>
    </div>


    <section
      v-if="existingReservations.length"
      class="section-existing-reservations"
    >
      <h2 class="section-title">Manage Existing IP Reservations</h2>
      <div class="link-card">

        <div
          v-for="device in existingReservations"
          class="link-card-item"
          @click="editDevice(device)"
        >
          <div class="label-cell">
            {{device.name}}
          </div>
        </div>

      </div>
    </section>

    <section class="section-new-reservations">
      <h2 class="section-title">Create IP Reservation</h2>
      <p>Assign an IP reservation to a connected device.</p>

      <div class="link-card">
        <div
          v-for="device in otherDevices"
          class="link-card-item"
          @click="editDevice(device)"
        >
          <div class="label-cell">
            {{device.name}}
          </div>
        </div>

      </div>
    </section>



  </div>

</template>



<script>

import { 
  KiteButton,
} from '@kite/vue-kite';
import ArticleAccordion from '../components/article-accordion.vue';
import { mapState } from 'vuex';



export default {
  name: 'port-forwarding',
  components: {
    KiteButton,
    ArticleAccordion,
  },

//-----

  data: () => ({
    canShowEditDevice: false,
  }),

//-----

  computed: {
    ...mapState([
      'user',
    ]),
    existingReservations() {
      return this.user.advancedWifi.ipReservations.filter((device) => {
        return device.ipReserved;
      });
    },
    otherDevices() {
      return this.user.advancedWifi.ipReservations.filter((device) => {
        return !device.ipReserved;
      })
    },
  },

//-----

  methods: {
    editDevice(device) {
      this.$store.commit('setCurrentDeviceId', device.id);
      this.$router.push('/services/wifi-equipment/advanced-settings/port-forwarding/device?title=' + device.name);
    }
  },

}

</script>



