
<template>

  <div
    class="view view-login"
    :class="{
      'showingForm': initialLoadDone
    }"
  >
    
    <img
      :src="require('../assets/images/icon-person.svg')"
      alt="person"
      class="person-icon"
    >
    <h1 class="login-title">My Spectrum</h1>

    <form
      class="login-form"
      @submit.prevent="onSubmit"
    >
      <kite-text-input
        label="Username"
        id="username"
        :value="username"
        :onChange="onInputChange"
        :error="usernameError"
        :forceError="forceErrors"
      />
      <input-password-toggle
        label="Password"
        id="password"
        :value="password"
        :onChange="onInputChange"
        :error="passwordError"
        :forceError="forceErrors"
      />
      <button
        type="submit"
        class="kite-btn kite-btn-primary kite-btn-block"
      >
        Sign In
      </button>
    </form>

    <div class="forgot-links">
      <kite-button
        type="standalone-link"
        :onClick="() => {}"
      >
        Forgot Username
      </kite-button>
      <kite-button
        type="standalone-link"
        :onClick="() => {}"
      >
        Forgot Password
      </kite-button>
    </div>

    <router-link
      class="create-btn kite-btn kite-btn-sm kite-btn-standalone-link kite-btn-link"
      type="button"
      to="/users"
    >
      Create Username
    </router-link>

    <img
      :src="require('../assets/images/spectrum-logo.svg')"
      alt="Spectrum Logo"
      class="login-logo"
    >


    <transition name="fade-slow">
      <loading 
        v-if="showLoading"
        :onComplete="() => this.$router.push('/home')"
      />
    </transition>

  </div>

</template>



<script>

import { 
  KiteButton,
  KiteTextInput,
} from '@kite/vue-kite';
import InputPasswordToggle from '../components/input-password-toggle.vue';
import Loading from '../components/loading.vue';
import { mapState } from 'vuex';


export default {
  name: 'login',
  components: {
    KiteButton,
    KiteTextInput,
    InputPasswordToggle,
    Loading,
  },

//-----

  data: function() {
    return {
      username: '',
      password: '',
      forceErrors: false,
      showLoading: false,
    }
  },

//-----

  computed: {
    ...mapState([
      'allUsers',
      'initialLoadDone',
      'useLocal'
    ]),

    usernameError: function() {
      if (this.username === '') {
        return 'Username is required';

      } else if (!this.currentUser) {
        return 'Username is not recognized';

      } else {
        return '';
      }
    },
    passwordError: function() {
      if (this.password === '') {
        return 'Password is required';

      } else if (!this.passwordMatches) {
        return 'Password does not match username';

      } else {
        return '';
      }
    },

    passwordMatches: function() {
      if (this.currentUser) {
        return this.currentUser.password === this.password;
      } else {
        return true;
      }
    },
    currentUser: function() {
      if (this.username) {
        return this.getUser();
      } else {
        return null;
      }
    }
  },

//-----

  methods: {
    onInputChange: function(e) {
      this[e.target.name] = e.target.value;
    },
    getUser: function() {
      return this.allUsers.find((user) => {
        return (user.username === this.username);
      });
    },
    formComplete: function() {
      return (
        !this.usernameError &&
        !this.passwordError
      );
    },
    onSubmit: function() {
      // if (!this.useLocal) {
      //   if (this.formComplete()) {
      //     this.$store.commit('setUser', this.currentUser);
      //     this.showLoading = true;
      //   } else {
      //     this.forceErrors = true;
      //   }
      // } else {
        // this.showLoading = true; // previously just had this line uncommented
      // }

      this.$router.push('/home');
    },
  },

//-----

  mounted: function() {
    if (!this.initialLoadDone) {
      window.setTimeout(() => {
        this.$store.commit('setInitialLoadDone');
      }, 1500);
    }
  },

}

</script>



