
<template>

  <div class="view view-dns">
    
    <p class="section-intro">Edit the domain name system (DNS) setup for your home network. Custom DNS is commonly used for blocking selected websites from your network.</p>

    <div class="full-card">
      <div class="full-card-section">
        <div class="card-item">
          <div class="label">Primary DNS Server</div>
          {{user.advancedWifi.customDns.primaryIp}}
        </div>
        <div class="card-item">
          <div class="label">Secondary DNS Server</div>
          {{user.advancedWifi.customDns.secondaryIp}}
        </div>
      </div>
      <div class="full-card-section">
        <div class="card-item">
          <div class="label">Primary IPv6 Address</div>
          {{user.advancedWifi.customDns.primaryIpv6}}
        </div>
        <div class="card-item">
          <div class="label">Secondary IPv6 Address</div>
          {{user.advancedWifi.customDns.secondaryIpv6}}
        </div>
      </div>
      <div class="full-card-section dns-actions">
        <kite-button
          size="full"
          :onClick="() => canShowEdit = true"
        >
          Manage DNS
        </kite-button>
        <kite-button
          type="standalone-link"
          size="full"
          :onClick="() => {}"
        >
          Use Spectrum DNS
        </kite-button>
      </div>
    </div>


    <!-- Manage Port Modal -->

    <modal-full-page
      :canShow="canShowEdit"
      modalTitle="Manage DNS"
      :onBack="onEditCancel"
      :onSave="onEditSave"
      newClass="edit-port-modal"
    >
      <kite-text-input
        label="Primary DNS Server"
        id="primaryIp"
        :value="tempDns.primaryIp"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />
      <kite-text-input
        label="Secondary"
        id="secondaryIp"
        :value="tempDns.secondaryIp"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />
      <kite-text-input
        label="Primary IPv6 Address"
        id="primaryIpv6"
        :value="tempDns.primaryIpv6"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />
      <kite-text-input
        label="Secondary IPv6 Address"
        id="secondaryIpv6"
        :value="tempDns.secondaryIpv6"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />
    </modal-full-page>

  </div>

</template>



<script>

import {
  KiteButton,
  KiteTextInput,
} from '@kite/vue-kite';
import ModalFullPage from '../components/modal-full-page.vue';
import { mapState } from 'vuex';

function clone(obj) {
  return JSON.parse(JSON.stringify(obj));
}


export default {
  name: 'custom-dns',
  components: {
    KiteButton,
    KiteTextInput,
    ModalFullPage,
  },

//-----

  data: () => ({
    canShowEdit: false,

    tempDns: {
      primaryIp: '',
      secondaryIp: '',
      primaryIpv6: '',
      secondaryIpv6: '',
    }
  }),

//-----

  computed: {
    ...mapState([
      'user',
    ]),
  },

//-----

  methods: {
    resetTempDevice() {
      const clonedDns = clone(this.user.advancedWifi.customDns);
      this.tempDns.primaryIp = clonedDns.primaryIp;
      this.tempDns.secondaryIp = clonedDns.secondaryIp;
      this.tempDns.primaryIpv6 = clonedDns.primaryIpv6;
      this.tempDns.secondaryIpv6 = clonedDns.secondaryIpv6;
    },
    onInputChange(e) {
      this.tempDns[e.target.name] = e.target.value;
    },
    onEditSave() {
      const newCustomDns = Object.assign({}, this.user.advancedWifi.customDns, this.tempDns);
      this.$store.commit('saveCustomDns', newCustomDns);
      this.canShowEdit = false;
    },
    onEditCancel() {
      this.canShowEdit = false;
      this.resetTempDevice();
    },
  },

//-----

  created() {
    this.resetTempDevice();
  },

}

</script>



<style lang="scss">

.view-dns {
  padding-top: 40px;
  padding-bottom: 40px;

  .dns-actions {
    .kite-btn {
      &:first-child {
        margin-top: 0;
      }
    }
  }
}

</style>
