
<template>

  <div class="view view-bill-compare">

    <div class="bill-selections">
      <!-- <div class="selection-title">Select Statements</div> -->
      <div class="selection-inputs">
        <select
          :value="selectedBillId"
          @change="onBillChange"
          class="kite-form-control"
        >
          <option
            v-for="bill in bills"
            :value="bill.id"
            :key="bill.id"
          >
            {{bill.billDate}}
          </option>
        </select>
        <select
          :value="selectedCompareBillId"
          @change="onCompareBillChange"
          class="kite-form-control"
        >
          <option
            value=""
          ></option>
          <option
            v-for="bill in bills"
            :value="bill.id"
            :key="bill.id"
            :disabled="(selectedBillId === bill.id)"
          >
            {{bill.billDate}}
          </option>
        </select>
      </div>
    </div>


    <div class="bill-data-table">

      <div class="bill-dt-column">
        <div class="bill-dt-cell header-cell">
          <div class="cell-title">Balance</div>
          <div class="cell-value">${{selectedBill.balance.toFixed(2)}}</div>
        </div>
        <div class="data-cells">

          <div
            v-for="detail in selectedBill.details.serviceDetails"
            @click="showChargeDetails(detail)"
            class="bill-dt-cell"
            :class="{'flag': detail.flag, 'placeholder': detail.placeholder}"
          >
            <div class="cell-title">{{detail.title}}</div>
            <div class="cell-value">
              ${{detail.value.toFixed(2)}}
              <span
                class="si si-arrow-up-f"
                v-if="detail.flag"
              ></span>
            </div>
          </div>

        </div>
      </div>


      <div
        class="bill-dt-column"
        :class="{'empty': !selectedCompareBillId}"
      >
        <div class="bill-dt-cell header-cell">
          <div class="cell-title">Balance</div>
          <div class="cell-value" v-if="selectedCompareBill">${{selectedCompareBill.balance.toFixed(2)}}</div>
          <div class="cell-value" v-else >$0</div>
        </div>
        <div
          v-if="selectedCompareBill"
          class="data-cells"
        >

          <div
            v-for="detail in selectedCompareBill.details.serviceDetails"
            @click="showChargeDetails(detail)"
            class="bill-dt-cell"
            :class="{'flag': detail.flag, 'placeholder': detail.placeholder}"
          >
            <div class="cell-title">{{detail.title}}</div>
            <div class="cell-value">
              ${{detail.value.toFixed(2)}}
              <span
                class="si si-arrow-up-f"
                v-if="detail.flag"
              ></span>
            </div>
          </div>

        </div>

        <div
          v-if="!selectedCompareBill"
          class="data-cells"
        >
          <div class="no-data-cells">
            <div class="no-data-cell">
              <div class="nd-title"></div>    
              <div class="nd-value"></div>
            </div>
            <div class="no-data-cell">
              <div class="nd-title"></div>    
              <div class="nd-value"></div>
            </div>
            <div class="no-data-cell">
              <div class="nd-title"></div>    
              <div class="nd-value"></div>
            </div>
            <div class="no-data-cell">
              <div class="nd-title"></div>    
              <div class="nd-value"></div>
            </div>
            <div class="no-data-cell">
              <div class="nd-title"></div>    
              <div class="nd-value"></div>
            </div>
          </div>
        </div>
      
      </div> <!-- end .bill-dt-column -->
    </div>

  </div>

</template>



<script>

import Bills from '../data/bills';
import { mapState } from 'vuex';


export default {
  name: 'comparison',
  data: function() {
    return {
      selectedBill: Bills[0],
      selectedCompareBill: '',
      bills: Bills,
    }
  },

//-------

  computed: {
    ...mapState([
      'selectedBillId',
      'selectedCompareBillId'
    ]),
  },

  methods: {

    setBill: function(id) {
      this.selectedBill = Bills.find((bill) => bill.id === id);
    },

//-------

    setCompareBill: function(id) {
      this.selectedCompareBill = Bills.find((bill) => bill.id === id);
    },

//-------

    onBillChange: function(e) {
      const id = e.target.value;

      this.$store.commit('setSelectedBillId', id);
      this.setBill(id);

      if (id == this.selectedCompareBillId) {
        this.$store.commit('setSelectedCompareBillId', null);
        this.setCompareBill(null);
      }

      if (this.selectedCompareBillId) {
        this.clearCompareFlags();
        this.checkBillDifferences();
      } else {
        this.clearCompareFlags();
      }
    },

//-------

    onCompareBillChange: function(e) {
      const id = e.target.value;

      this.$store.commit('setSelectedCompareBillId', id);
      this.setCompareBill(id);

      if (id) {
        this.clearCompareFlags();
        this.checkBillDifferences();
      } else {
        this.clearCompareFlags();
      }
    },

//-------

    showChargeDetails: function(detail) {
      if (detail.details.length) {
        this.$store.commit('setCurrentDetail', detail);
        this.$router.push('/billing/charge-details');
      }
    },

//-------

    checkBillDifferences: function() {
      this.flagDifferencesBetweenBills();
      this.addPlaceholders();
    },

//-----

    flagDifferencesBetweenBills: function() {
      const mainDetails = this.selectedBill.details.serviceDetails;
      const compareDetails = this.selectedCompareBill.details.serviceDetails;

      mainDetails.forEach((detail, i) => {
        
        if (detail.placeholder) {
          return;
        }

        const compareDetail = compareDetails.find((compDetail) => compDetail.title === detail.title);

        if (compareDetail) {
          if (detail.value > compareDetail.value) {
            detail.flag = true;
            compareDetail.flag = false;

          } else if (detail.value < compareDetail.value) {
            detail.flag = false;
            compareDetail.flag = true;

          } else {
            detail.flag = false;
            compareDetail.flag = false;
          }
        }
      });
    },

//-----

    addPlaceholders: function() {
      let mainDetails = this.selectedBill.details.serviceDetails;
      let compareDetails = this.selectedCompareBill.details.serviceDetails;

      if (!mainDetails.length || !compareDetails.length) {
        return;
      }

      if (mainDetails.length > compareDetails.length) {
        compareDetails = this.fillShortList(mainDetails, compareDetails);

      } else if (compareDetails.length > mainDetails.length) {
        mainDetails = this.fillShortList(compareDetails, mainDetails);

      } else {
        mainDetails = this.fillShortList(compareDetails, mainDetails);
        compareDetails = this.fillShortList(mainDetails, compareDetails);
      }
    },

//-----

    fillShortList: function(longer, shorter) {
      longer.forEach((detail, i) => {
        if (detail.placeholder) {
          return;
        }

        const oppositeDetail = shorter.find((compDetail) => compDetail.title === detail.title);

        if (!oppositeDetail) {
          shorter.splice(i, 0, createPlaceholder(detail.title));
        }
      });

      return shorter;
    },

//-----

    clearCompareFlags: function() {
      this.clearMainPlaceholdersAndFlags();

      if (this.selectedCompareBill) {
        this.clearComparePlaceholdersAndFlags();
      }
    },

//-----

    clearMainPlaceholdersAndFlags: function() {
      const mainDetails = this.selectedBill.details.serviceDetails;

      mainDetails.forEach((detail, i) => {
        detail.flag = null;
      });

      mainDetails.forEach((detail, i) => {
        if (detail.placeholder) {
          mainDetails.splice(i, 1);
        }
      });
    },

//-----

    clearComparePlaceholdersAndFlags: function() {
      const compareDetails = this.selectedCompareBill.details.serviceDetails;

      compareDetails.forEach((detail, i) => {
        detail.flag = null;
      });

      compareDetails.forEach((detail, i) => {
        if (detail.placeholder) {
          compareDetails.splice(i, 1);
        }
      });
    }


  }, // end methods

//-----

  mounted: function() {
    if (this.selectedBillId) {
      this.setBill(this.selectedBillId);
    } else {
      this.$store.commit('setSelectedBillId', Bills[0].id);
    }

    if (this.selectedCompareBillId) {
      this.setCompareBill(this.selectedCompareBillId);
    }
  },
}



function createPlaceholder(title) {
  return {
    title: title,
    value: 0.00,
    details: [],
    placeholder: true,
  };
}



</script>



