
<template>

  <div class="view view-voicemail">

    <transition-group
      name="voicemail"
      class="link-card"
      tag="div"
    >
      <voicemail-accordion
        v-for="message in messages"
        :key="message.id"
        :unread="message.unread"
        :number="message.number"
        :date="message.date"
        :time="message.time"
        :duration="message.duration"

        :onOpen="onMessageOpen"
        :anotherMessageIsOpen="messageIsOpen"
        :forceAllMessagesClose="closeAllMessages"
        :forceClose="forceCloseMessages"
        :rawMessage="message"
        :onDelete="onMessageDelete"
        :onToggleRead="toggleMessageRead"
      />
    </transition-group>


    <transition name="fade">
      <modal
        v-if="canShowDeleteConfirmation"
        title="Delete Message"
        content="Are you sure you want to delete this message?"
        primaryActionTitle="Yes, Delete"
        :onPrimaryActionClick="deleteMessage"
        :onCancelClick="() => canShowDeleteConfirmation = false"
      />
    </transition>

  </div>

</template>



<script>

// import { mapState } from 'vuex';
import VoicemailAccordion from '../components/voicemail-accordion.vue';
import Modal from '../components/modal.vue';

const voicemailData = [
  {
    id: '01',
    unread: true,
    number: '(738) 999-1253',
    date: 'Sept 6',
    time: '11:03 AM',
    duration: 5,
  },
  {
    id: '02',
    unread: false,
    number: '(303) 171-4557',
    date: 'Sept 6',
    time: '1:43 PM',
    duration: 34,
  },
  {
    id: '03',
    unread: true,
    number: '(758) 323-1454',
    date: 'Sept 5',
    time: '7:01 AM',
    duration: 14,
  },
  {
    id: '04',
    unread: false,
    number: '(303) 171-4557',
    date: 'Sept 5',
    time: '11:26 AM',
    duration: 123,
  },
  {
    id: '05',
    unread: false,
    number: '(810) 173-3477',
    date: 'Sept 5',
    time: '3:23 PM',
    duration: 34,
  },
  {
    id: '06',
    unread: false,
    number: '(303) 579-0089',
    date: 'Sept 5',
    time: '4:14 PM',
    duration: 7,
  },
  {
    id: '07',
    unread: true,
    number: '(303) 422-7682',
    date: 'Sept 5',
    time: '7:19 PM',
    duration: 129,
  },
  {
    id: '08',
    unread: false,
    number: '(738) 999-1253',
    date: 'Sept 2',
    time: '11:03 AM',
    duration: 27,
  },
  {
    id: '09',
    unread: false,
    number: '(303) 171-3477',
    date: 'Aug 29',
    time: '1:43 PM',
    duration: 413,
  },
];


export default {
  name: 'voicemail',
  components: {
    VoicemailAccordion,
    Modal,
  },

  data: function() {
    return {
      messages: voicemailData,
      messageIsOpen: false,
      forceCloseMessages: false, // triggers components to close

      canShowDeleteConfirmation: false,
    }
  },

//-----

  computed: {
  },

//-----

  methods: {
    onMessageOpen: function(message) {
      this.messageIsOpen = true;
      if (message.unread) {
        this.toggleMessageRead(message);
      }
    },
    closeAllMessages: function() {
      this.forceCloseMessages = true;

      window.setTimeout(() => {
        this.forceCloseMessages = false;
      }, 1000)
    },
    onMessageDelete: function(message) {
      this.canShowDeleteConfirmation = true;
      this.messageToDelete = message;
    },
    deleteMessage: function() {
      let messageIdx = this.messages.findIndex((mes) => {
        return mes.id === this.messageToDelete.id;
      });
      this.messages.splice(messageIdx, 1);
      this.canShowDeleteConfirmation = false;
    },
    toggleMessageRead: function(message) {
      let messageRef = this.messages.find((mes) => {
        return mes.id === message.id;
      });
      messageRef.unread = !messageRef.unread;
    },
  }
}

</script>



