<script>
import AccountSetUp from '../components/account-set-up.vue';
import { KiteButton, KiteLinkButton } from '@kite/vue-kite';
import Throttle from 'lodash.throttle';
import { mapState } from 'vuex';
import { getMDY, getNextBillDueDateUTC } from '../utils/time';

const headerOffsetDistance = 112;

export default {
  name: 'home',
  components: {
    AccountSetUp,
    KiteButton,
    KiteLinkButton
  },

  //-----

  data: function() {
    return {
      // Cached values
      scrollEl: null,
      homeCardsEl: null,
      greetingsHeight: 0,

      mapElWindow: null,
      mapTop: 0,
      distanceToMap: 0,

      // State values
      greetingsTranslate: 0,
      greetingsRawScroll: 0,
      opacityVal: 1,
      mapTranslate: 0,

      billDueDate: getMDY(getNextBillDueDateUTC())
    };
  },

  //-----

  computed: {
    ...mapState(['user'])
  },

  //-----

  methods: {
    // goFullScreen: function() {
    //   const el = document.documentElement;

    //   if (el.webkitRequestFullscreen) {
    //     el.webkitRequestFullscreen();
    //   } else if (el.mozRequestFullScreen) {
    //     el.mozRequestFullScreen();
    //   } else if (el.requestFullscreen) {
    //     el.requestFullscreen();
    //   }
    // },

    initHeaderParallax: function() {
      this.scrollEl = document.querySelector('.view-home');
      this.homeCardsEl = this.$el.querySelector('.home-cards-container');
      const greetingsEl = this.$el.querySelector('.section-greeting');
      this.greetingsHeight = greetingsEl.scrollHeight;
    },
    watchHeaderScroll: function(e) {
      const percentScrolled = e.target.scrollTop / this.greetingsHeight;

      if (percentScrolled <= 1) {
        this.greetingsTranslate = percentScrolled * headerOffsetDistance;
        this.greetingsRawScroll = e.target.scrollTop;
        this.opacityVal = 1 - percentScrolled * 1.5;
      }
    },

    // initMapParallax: function() {
    //   this.mapElWindow = this.$el.querySelector('.nearest-store-map');
    //   this.mapTop = this.mapElWindow.offsetTop;
    //   this.distanceToMap = this.mapTop + this.greetingsHeight - this.scrollEl.offsetHeight;
    // },
    // watchMapScroll: function(e) {
    //   const windowPosition = e.target.scrollTop - this.distanceToMap;

    //   if (windowPosition > 0) {
    //     const positionRatio = windowPosition/this.scrollEl.offsetHeight;
    //     this.mapTranslate = (this.mapElWindow.offsetHeight * positionRatio)/4 + 10;
    //   }
    // },

    onScroll: function(e) {
      this.watchHeaderScroll(e);
      // this.watchMapScroll(e);
    },
    bindScrollWatchers: function() {
      this.scrollEl.addEventListener('scroll', Throttle(this.onScroll, 16));
    }
  },

  mounted: function() {
    this.initHeaderParallax();
    // this.initMapParallax();
    this.bindScrollWatchers();
  }
};
</script>

