
<template>

  <div class="view view-wifi-reset">
    <transition mode="out-in" name="fade-slow">
      <section
        v-if="!resetInProgress"
        key="landing"
        class="section-reset"
      >
        <span class="si si-circle-info-f"></span>
        <h1>Reset Your Router</h1>
        <p>A factory reset restores your router to its original default settings, including your WiFi name and password.</p>
        <kite-button
          type="primary"
          size="full"
          :onClick="() => canShowSpeedbump = true"
        >
          Reset Router
        </kite-button>
      </section>
      <section
        v-if="resetInProgress"
        key="resetting"
        class="section-in-progress"
      >
        <kite-progress
          :percent="resetStepItem.progress"
        />

        <transition mode="out-in" name="fade-slower">
          <div
            v-if="!resetComplete"
            key="in-progress"
          >
            <h1>Resetting Your Router</h1>
            <p
              :class="{'hide': hideResetCopy}"
            >
              {{resetStepItem.copy}}
            </p>
          </div>
          <div
            v-if="resetComplete"
            key="complete"
          >
            <h1>Router Reset Complete</h1>
            <p>Your router was reset to its factory settings.</p>
            <p>To reconnect every device to your home network faster, change the network name and password back to what was used before the reset.</p>
            <kite-button
              type="primary"
              size="full"
              :onClick="() => $router.push('/services/wifi-equipment/advanced-settings')"
            >
              Change WiFi Settings
            </kite-button>
            <kite-button
              type="standalone-link"
              size="full"
              :onClick="() => $router.push('/services/wifi-equipment')"
            >
              Use Default Settings
            </kite-button>
          </div>
        </transition>
        
      </section>
    </transition>

    <transition name="fade">
      <modal
        v-if="canShowSpeedbump"
        title="Reset Router?"
        content="This action will reset your router to its factory settings. You can customize your network name and password after the reset."
        :onPrimaryActionClick="startReset"
        primaryActionTitle="Reset"
        :onCancelClick="() => canShowSpeedbump = false"
      />
    </transition>
  </div>

</template>



<script>

import { 
  KiteButton,
  KiteProgress,
} from '@kite/vue-kite';
import Modal from '../components/modal.vue';
import { mapState } from 'vuex';

const resetSteps = [
  {
    progress: 0,
    copy: 'Your router is now resetting to its default factory settings.'
  },
  {
    progress: 10,
    copy: 'Your router is now resetting to its default factory settings.'
  },
  {
    progress: 25,
    copy: 'Your router will disconnect from your home network during the reset.'
  },
  {
    progress: 45,
    copy: 'If you have Spectrum Voice, you won\'t be able to make or receive calls from your home phone, including emergency 911 calls, until the router is back online. '
  },
  {
    progress: 60,
    copy: 'To secure your network after the reset, customize the network name and password.'
  },
  {
    progress: 80,
    copy: 'To reconnect your all of your devices to your home network faster, use the same network name and password that you had before the reset. '
  },
  {
    progress: 95,
    copy: 'Finishing up...'
  },
  {
    progress: 100,
    copy: ''
  },
];


export default {
  name: 'wifi-reset',
  components: {
    KiteButton,
    KiteProgress,
    Modal,
  },

//-----

  data: function() {
    return {
      canShowSpeedbump: false,

      resetInProgress: false,
      resetStep: 0,
      resetInterval: null,
      hideResetCopy: false,
    }
  },

//-----

  computed: {
    ...mapState([
      'user',
    ]),
    resetStepItem() {
      return resetSteps[this.resetStep];
    },
    resetComplete() {
      return (this.resetStepItem.progress >= 100);
    },
  },

//-----

  methods: {
    startReset() {
      this.canShowSpeedbump = false;
      this.resetInProgress = true;
      this.startFakeProgress();
    },
    startFakeProgress() {
      this.resetInterval = window.setInterval(() => {
        this.hideResetCopy = true;

        window.setTimeout(() => {
          this.resetStep++;
          this.hideResetCopy = false;
          if (this.resetComplete) {
            window.clearInterval(this.resetInterval);
          }
        }, 600);
      }, 3000);
    },
  },

}

</script>



<style lang="scss">

.view-wifi-reset {
  padding: 40px 16px;
  text-align: center;

  .si {
    margin-bottom: 24px;
    line-height: 1em;
    font-size: 48px;
    color: $kite-dark-red;
  }
  h1 {
    margin-bottom: 8px;
  }

  .section-in-progress {
    .kite-progress {
      padding-top: 0;
      padding-bottom: 16px;

      .progress-percent {
        font-size: 28px;
      }
    }
    p {
      margin-bottom: 8px;
      transition: opacity 0.6s;

      &.hide {
        opacity: 0;
      }
    }
  }

}

</style>
