
<template>

  <div id="app" class="kite-typography">

    <transition name="header">
      <app-header v-if="notLogin" />
    </transition>

    <transition name="outage">
      <router-link
        v-if="user.outage && user.outage.canShow && !$route.meta.level"
        to="/outage"
        class="app-alert-notification"
      >
        <span class="si si-circle-caution-f"></span>
        <div class="alert-copy">
          A Spectrum Internet outage was reported in your area.
        </div>
      </router-link>
    </transition>

    <main :class="{'lock': scrollLocked}">

      <transition :name="transitionName">
        <router-view></router-view>
      </transition>

    </main>

    <transition name="header">
      <app-footer v-if="notLogin && notUsers && notSelfInstall" />
    </transition>


  <!-- Action Sheet -->
    <action-sheet />


  </div>

</template>



<script>

import AppFooter from './components/footer.vue';
import AppHeader from './components/header.vue';
import ActionSheet from './components/action-sheet.vue';
import QuickTutorial from './components/quick-tutorial.vue';

import { mapState } from 'vuex';


export default {
  name: 'app',
  components: {
    AppFooter,
    AppHeader,
    ActionSheet,
    QuickTutorial
  },

//------

  data: function() {
    return {
      transitionName: 'page',
    };
  },

//------

  computed: {
    ...mapState([
      'scrollLocked',
      'useLocal',
      'user',
      'canShowOnboarding',
    ]),
    // notHome: function() {
    //   return this.$route.path !== '/home';
    // },
    notLogin: function() {
      return this.$route.path !== '/';
    },
    notUsers: function() {
      return !this.$route.path.includes('/user');
    },
    notSelfInstall: function() {
      return !this.$route.path.includes('/self-install');
    },
  },

//------

  methods: {
    createRoutes: function() {
      const newRoutes = [];

      this.$router.options.routes.forEach((route) => {
        if (route.name) {
          newRoutes.push(route.name);
        }
      });

      return newRoutes;
    },
    setTransition: function(to, from, routes) {
      // Set transition
      const currentIndex = routes.indexOf(from.name);
      const targetIndex = routes.indexOf(to.name);

      if (currentIndex >= 0 && targetIndex >= 0) {
        this.transitionName = (currentIndex > targetIndex) ? 'slide-left' : 'slide-right';

      } else {
        const currentLevel = from.meta.level;
        const targetLevel = to.meta.level;

        if (targetLevel > currentLevel) {
          this.transitionName = 'top-comes-from-right';

        } else if (targetLevel <= currentLevel) {
          this.transitionName = 'top-slides-to-right';
        }
      }
    },
  },

//------

  mounted: function() {
    const routes = this.createRoutes();

    this.$router.beforeEach((to, from, next) => {
      this.setTransition(to, from, routes);
      next();
    });
  },

//------

  created: function() {
    if (!this.useLocal) {
      this.$store.dispatch('updateUsers');
    }
  },

}

</script>



