<template>

  <div
    class="detail-row"
    :class="{'clickable': canClick}"
    @click="showChargeDetails()"
  >
    <span class="accordion-left">
      {{detail.title}}
      <span class="accordion-flag icon icon--yellow icon--small-xs sb_ic_arrow_up_f"></span>
    </span>
    <span class="accordion-right">${{detail.value.toFixed(2)}}</span>
    <span
      v-if="canClick"
      class="accordion-arrow"
    >
      <span class="si si-chevron-right"></span>
    </span>
  </div>

</template>



<script>

export default {
  name: 'detail-row',
  props: [
    'detail', // object - {title, value, details}
  ],

//-----

  data () {
    return {
    }
  },

//-----

  computed: {
    canClick: function() {
      return this.detail.details.length;
    }
  },

//-----

  methods: {
    showChargeDetails: function() {
      this.$store.commit('setCurrentDetail', this.detail);
      this.$router.push('/billing/charge-details');
    }
  }

//-----

}

</script>



