
<template>

  <div class="view view-ip-reservation">

    <section class="section-base-form">

      <h2 class="section-title">IP Reservation</h2>
      <p class="section-intro">An IP reservation is required for port forwarding.</p>

      <div
        class="full-card ip-card"
        :class="{'no-reserve': !currentDevice.ipReserved}"
      >
        <div class="full-card-section">
          <div class="card-item">
            <div class="label">IP Address</div>
            {{currentDevice.ipAddress}}
          </div>
          <div class="card-item">
            <div class="label">MAC Address</div>
            {{currentDevice.macAddress}}
          </div>
        </div>

        <div class="card-item switch-item">
          <div class="label-cell">
            Reserve IP Address
          </div>
          <div class="value-cell">
            <kite-switch
              id="ipIsSet"
              :isOn="currentDevice.ipReserved"
              name="ipIsSet"
              :onChange="onIpReserveChange"
            />
          </div>
        </div>

        <transition name="ip-reserve-warning">
          <div
            v-if="currentDevice.ipReserved"
            class="full-card-section"
          >
            Closing this IP reservation will delete all related port assignment info.
          </div>
        </transition>
      </div>
    </section>

    <section
      v-if="currentDevice.ipReserved"
      class="section-port-fields"
    >
      <h2 class="section-title">Port Forwarding</h2>
      <p class="section-intro">Open a designated port for a specific device.</p>
      <div class="link-card">

        <div
          v-for="port in currentDevice.ports"
          class="link-card-item"
          @click="editPort(port)"
        >
          {{port.name}}
        </div>

        <div class="link-card-item no-link action-row">
          <kite-button
            type="primary"
            size="full"
            :onClick="() => canShowNew = true"
          >
            Add Port Assignment
          </kite-button>
        </div>
      </div>
    </section>


    <!-- Manage Port Modal -->

    <modal-full-page
      :canShow="canShowNew"
      modalTitle="Port Assignment"
      :onBack="onNewCancel"
      :onSave="onNewSave"
      newClass="edit-port-modal"
    >
      <kite-text-input
        label="Assignment Name"
        id="name"
        :value="tempPort.name"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />
      <kite-text-input
        label="External Port"
        id="external"
        :value="tempPort.external"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />
      <kite-text-input
        label="Internal Port"
        id="internal"
        :value="tempPort.internal"
        :onChange="onInputChange"
        :onKeyup="onInputChange"
      />

      <div class="full-width-select">
        <p class="select-card-title">Protocol</p>
        <kite-select
          label=""
          id="type"
          :value="tempPort.type"
          :onChange="onInputChange"
        >
          <option default value="tcp+udp">TCP + UDP</option>
          <option value="tcp">TCP</option>
          <option value="udp">UDP</option>
        </kite-select>
      </div>

    </modal-full-page>

  </div>

</template>



<script>

import { 
  KiteTextInput,
  KiteSwitch,
  KiteSelect,
  KiteButton,
} from '@kite/vue-kite';
import ModalFullPage from '../components/modal-full-page.vue';
import { mapState } from 'vuex';


export default {
  name: 'device-ip',
  components: {
    KiteTextInput,
    KiteSwitch,
    KiteSelect,
    KiteButton,
    ModalFullPage,
  },

//-----

  data: () => ({
    currentDevice: null,

    canShowNew: false,
    tempPort: {
      name: '',
      internal: '',
      external: '',
      id: 0,
      type: '',
    },
  }),

//-----

  computed: {
    ...mapState([
      'user',
      'currentDeviceId',
    ]),
  },

//-----

  methods: {
    getCurrentDevice() {
      if (this.currentDeviceId) {
        this.currentDevice = this.user.advancedWifi.ipReservations.find((device) => device.id === this.currentDeviceId);
      } else {
        this.currentDevice = this.user.advancedWifi.ipReservations[0]
        this.$store.commit('setCurrentDeviceId', this.currentDevice.id)
      }
    },
    onInputChange(e) {
      this.tempPort[e.target.name] = e.target.value;
    },
    onIpReserveChange(e) {
      this.$store.commit('toggleReserveIp', e.target.checked);
      this.getCurrentDevice();
    },
    editPort(port) {
      this.$store.commit('setCurrentPortId', port.id);
      this.$router.push('/services/wifi-equipment/advanced-settings/port-forwarding/device/port');
    },

    getNextPortId() {
      let nextId = 0;
      this.currentDevice.ports.forEach((port) => {
        if (port.id > nextId) {
          nextId = port.id;
        }
      });
      return nextId + 1;
    },
    onNewSave() {
      this.tempPort.id = this.getNextPortId();
      this.$store.commit('addPort', this.tempPort);
      this.getCurrentDevice();
      this.canShowNew = false;
    },
    onNewCancel() {
      this.canShowNew = false;
      this.tempDevice = {
        name: '',
        internal: '',
        external: '',
        id: 0,
        type: '',
      };
    },
  },

//-----

  created() {
    this.getCurrentDevice();
    this.onNewCancel();
  },

}

</script>



<style lang="scss">

.view-ip-reservation {
  padding-top: 40px;
  padding-bottom: 40px;

  .section-port-fields {
    .action-row {
      padding-top: 24px;
      padding-bottom: 24px;

      .kite-btn {
        margin: 0;
      }
    }
  }

  .ip-card {
    transition: padding-bottom 0.5s ease-out;

    &.no-reserve {
      padding-bottom: 3px;
    }
  }
}

</style>
