
<template>

  <div class="view view-scp-active-schedules">

    <div class="full-card intro-card">
      <h2>Your Schedules</h2>
      <p>You currently have {{ schedules.length }} pausing {{ (schedules.length > 1) ? 'schedules' : 'schedule' }} on your network.</p>
    </div>

    <div class="link-card">

      <div
        v-for="device in schedules"
        class="link-card-item"
        @click="() => onScheduleClick(device)"
      >
        <div class="label-cell">
          {{ device.schedule.name || device.name }}
          <small
            v-if="device.schedule.name"
            class="label-cell-detail"
          >
            {{ device.name }}
          </small>
        </div>
      </div>

    </div>
  </div>

</template>



<script>

import { mapState } from 'vuex';
import { days } from '../utils/time';

const now = new Date();



export default {
  name: 'scp-active-schedules',
  components: {
  },

  data: () => ({
    schedules: [],
    currentDay: null,
    currentHour: null,
  }),

//-----

  computed: {
    ...mapState([
      'connectedDevices',
    ]),
  },

//-----

  methods: {
    filterSchedules() {
      this.schedules = this.connectedDevices.filter((device) => device.schedule && this.checkIfDevicePaused(device));
    },
    onScheduleClick(device) {
      this.$router.push({
        path: '/services/scp/device',
        query: {
          title: device.name
        },
      });
    },


    saveCurrentTime() {
      this.currentDay = days[now.getDay()].toLowerCase();
      this.currentHour = now.getHours();
    },
    checkIfDevicePaused(deviceObj) {
      if (deviceObj.tempPause) {
        return true;

      } else if (deviceObj.schedule && deviceObj.schedule.enabled && deviceObj.schedule.days) {
        const todaySchedule = deviceObj.schedule.days.find((day) => day.name === this.currentDay);

        if (todaySchedule) {
          // convert times 0-23 time
          const startInt = this.convertTimeToInt(todaySchedule.start);
          const endInt = this.convertTimeToInt(todaySchedule.end);

          return ( (this.currentHour >= startInt) && (this.currentHour < endInt) );
        } else {
          return false;
        }

      } else {
        return false;
      }
    },
    convertTimeToInt(timeString) {
      const isPM = timeString.includes('pm');
      const splitString = timeString.split(':');
      const rawInt = parseInt(splitString[0]);

      if (rawInt === 12) {
        return (isPM) ? 12 : 0;
      } else {
        return (isPM) ? (rawInt + 12) : rawInt;
      }
    },
    // createScheduleDetailCopy(device) {
    //   const pauseDays = device.schedule.days.map((day) => {
    //     const firstLetter = day.name.slice(0, 1).toUpperCase();

    //     if (firstLetter === 'T' || firstLetter === 'S') {
    //       const secondLetter = day.name.slice(1, 2);
    //       return firstLetter + secondLetter;
    //     } else {
    //       return firstLetter;
    //     }
    //   });

    //   if (device.schedule.isStandardSchedule) {
    //     const scheduleDay = device.schedule.days[0];
    //     return `${device.name} will pause from ${scheduleDay.start} - ${scheduleDay.end} ${pauseDays.join(',')}`
    //   } else {
    //     return `Lorem ipsum ${device.name} will pause at custom times on ${pauseDays.join(',')}`;
    //   }
    // },
  },

//-----

  mounted() {
    this.saveCurrentTime();
    this.filterSchedules();
  },

}

</script>



<style lang="scss">

.view.view-scp-active-schedules {
  padding: 40px 0;

  
}

</style>
