<script>

import {
  KiteButton,
  KiteTextInput,
  KiteFormattedInput,
  KiteSelect,
} from '@kite/vue-kite';
import ModalFullPage from '../components/modal-full-page.vue';
import { mapState } from 'vuex';

function clone(x) {
  return JSON.parse(JSON.stringify(x));
}


export default {
  name: 'your-info',
  components: {
    KiteButton,
    KiteTextInput,
    KiteFormattedInput,
    KiteSelect,
    ModalFullPage,
  },

  data: function() {
    return {
      canShowRoleChangeModal: false,
      canShowContactModal: false,

      // Manage contact info
      firstName: '',
      lastName: '',
      primaryEmail: '',
      primaryPhone: '',
      contactType: '',

      alternateEmails: [],
      alternatePhones: [],

      // Stopped here: Add email addresses/phone nummbers
      // - On save, update store
      // - Give warning on "back" action, if have edited anything

    }
  },

//-----

  computed: {
    ...mapState([
      'user'
    ]),
    isPrimary: function() {
      return this.user.role === 'Primary';
    }
  },

//-----

  methods: {
    setTempValues: function() {
      this.firstName = clone(this.user.firstName);
      this.lastName = clone(this.user.lastName);
      this.primaryEmail = clone(this.user.contact.email.personal.address);
      this.primaryPhone = clone(this.user.contact.phone.number);
      this.contactType = (this.user.contact.phone.isMobile) ? 'mobile' : 'non-mobile';

      this.alternateEmails = clone(this.user.additionalContacts.email);
      this.alternatePhones = clone(this.user.additionalContacts.phone);
    },

    onInputChange: function(e) {
      this[e.target.name] = e.target.value;
    },
    onPrimaryPhoneChange: function(val) {
      this.primaryPhone = val;
    },
    onChangeRole: function() {
      const sheetConfig = {
        title: 'Select Person',
        items: this.user.users.map((user) => ({
          value: `${user.name.fname} ${user.name.lname}`,
          raw: user,
        })),
        onSelect: this.onNewPrimarySelect,
      };
      this.$store.commit('showActionSheet', sheetConfig);
    },
    onNewPrimarySelect: function(user) {
      // Set own role as "admin"
      // Set other role as "primary"

      this.$store.commit('setUserRole', 'Admin');
      this.$store.commit('setContactRole', user.raw.id, 'Primary');
    },

    saveNames: function() {
      this.$store.commit('setFirstName', this.firstName);
      this.$store.commit('setLastName', this.lastName);
    },
    savePrimaryContact: function() {
      this.$store.commit('setPrimaryEmail', this.primaryEmail);
      this.$store.commit('setPrimaryPhone', this.primaryPhone);
      this.$store.commit('setPrimaryPhoneType', (this.contactType === 'mobile') );
    },

    addOtherEmail: function() {
      this.alternateEmails.push('');
    },
    onOtherEmailChange: function(i, e) {
      this.alternateEmails.splice(i, 1, e.target.value);
    },
    onDeleteEmail: function(i) {
      this.alternateEmails.splice(i, 1);
    },
    saveEmails: function() {
      this.$store.commit('setAlternateEmails', this.alternateEmails);
    },

    addOtherPhone: function() {
      this.alternatePhones.push('');
    },
    onOtherPhoneChange: function(i, val) {
      this.alternatePhones.splice(i, 1, val);
    },
    onDeletePhone: function(i) {
      this.alternatePhones.splice(i, 1);
    },
    savePhones: function() {
      this.$store.commit('setAlternatePhones', this.alternatePhones);
    },

    onSaveContactInfo: function() {
      this.saveNames();
      this.savePrimaryContact();
      this.saveEmails();
      this.savePhones();
      this.canShowContactModal = false;
    }
  },

//-----

  // mounted: function() {
  //   this.setTempValues();
  // },

//-----

  created: function() {
    this.setTempValues();
  }
}

</script>



