<template>

  <header class="app-header" :class="{ 'logo-showing': title === 'logo' }">

    <div class="phone-status-bar">
      <div class="status-bar-left">
        <img :src="require('../assets/images/icon-service-bars-white.svg')" alt="service bars">
        Spectrum Mobile
        <img :src="require('../assets/images/icon-wifi-white.svg')" alt="wifi bars">
      </div>
      <div class="status-bar-center">
        11:43 AM
      </div>
      <div class="status-bar-right">
        100%
        <img
          :src="require('../assets/images/icon-batter-full-white.png')"
          class="header-wifi-icon"
          alt="wifi bars"
        >
      </div>
    </div>

    <div class="content-row">
      <button
        v-if="$route.path != '/services/tv-upgrade'"
        v-show="!hideBack"
        @click="goBack()"
        class="back-button"
      >
        <span class="si si-chevron-left"></span>
      </button>

      <div :class="{ show: showTitle }" class="title">
        <img
          v-if="title === 'logo'"
          :src="require('../assets/images/logo-spectrum-white.svg')"
          alt="Spectrum"
        />
        <template v-else>
          {{ title }}
        </template>
      </div>

      <button
        v-if="$route.path == '/services/tv-upgrade'"
        @click="onDone"
        class="done-button"
      >
        Cancel
      </button>
    </div>
  </header>

</template>



<script>

import { mapState } from 'vuex';

export default {
  name: 'app-header',
  data() {
    return {
      showTitle: true,
      title:
        this.$route.meta.title === 'query'
          ? this.$route.query.title
          : this.$route.meta.title
    };
  },

  //-----

  computed: {
    ...mapState(['currentBill', 'currentDetail', 'makePaymentStep']),

    hideBack: function() {
      const { path } = this.$route;

      return (
        path === '/' ||
        path === '/home' ||
        path === '/billing' ||
        path === '/account' ||
        path === '/services' ||
        path === '/support' ||
        path === '/self-install'
      );
    }
  },

  //-----

  methods: {
    makePaymentGoBack() {
      if (this.makePaymentStep) {
        this.$store.commit('previousMakePaymentStep');
      } else {
        this.$router.go(-1);
      }
    },

    goBack() {
      if (this.$route.meta.title === 'Make Payment') {
        this.makePaymentGoBack();
      } else {
        this.$router.go(-1);
      }
    },

    // For random "Cancel" in buy flow
    onDone() {
      this.$router.push('/home');
    }
  },

  //-----

  mounted: function() {
    this.$router.beforeEach((to, from, next) => {
      // Set title
      this.showTitle = false;

      window.setTimeout(() => {
        this.title =
          to.meta.title === 'query' ? this.$route.query.title : to.meta.title;
        this.showTitle = true;
      }, 300);

      next();
    });
  }
};

</script>



