      .header-wifi-icon {
        margin-left: 2px;
      }
    }
    .status-bar-center {
      flex: 0 0 20%;
      text-align: center;
      font-weight: 500;
    }
    .status-bar-right {
      flex: 0 0 40%;
      text-align: right;
    }
    img {
      position: relative;
      bottom: 1px;
      display: inline-block;
      height: 0.9em;
      width: auto;
    }
  }

  .content-row {
    position: relative;
    padding: 9px 12px;
  }

  .back-button {
    position: absolute;
    top: 11px;
    left: 12px;
    font-weight: 500;
    color: $kite-white;
    background-color: transparent;
    border: 0;

    span.si {
      display: inline-block;
      margin-right: 8px;
    }
  }

  .title {
