
<template>

  <div class="view view-manage-payments">

    <div class="payment-description">
      <p>Payments are processed and posted to your account within 24 hours of your payment date.</p>
    </div>

    <div class="payment-list">
      <div
        v-for="(payment, i) in user.billing.scheduledPayments"
        class="payment-item"
      >
        <summary-card
          :paymentAmount="payment.amount"
          :paymentDate="payment.date"
          :paymentMethod="payment.paymentMethod"
        />
        <kite-button
          type="standalone-link"
          :onClick="(e) => onCancelPayment(i)"
        >
          Cancel Payment
        </kite-button>
      </div>
    </div>

    <transition name="fade-slow">
      <div
        v-if="cancelPayment.amount"
        class="confirm-cancel-modal"
      >
        <div class="cancel-modal">
          <h1>Cancel Scheduled Payment?</h1>
          <p>Are you sure you want to cancel your payment of ${{cancelPayment.amount}} scheduled for {{cancelPaymentDate}}?</p>
          <kite-button
            :onClick="onConfirmCancel"
            size="full"
          >
              Cancel Payment
          </kite-button>
          <kite-button
            :onClick="onKeepPayment"
            size="full"
            type="standalone-link"
          >
            Keep Payment
          </kite-button>
        </div>
      </div>
    </transition>

  </div>

</template>



<script>

import {
  KiteButton,
  KiteSpinner,
} from '@kite/vue-kite';
import SummaryCard from '../components/summary-card.vue';

import { mapState } from 'vuex';
import { getFullMDY } from '../utils/time';



export default {
  name: 'manage-payments',
  components: {
    KiteButton,
    KiteSpinner,
    SummaryCard,
  },

//-----

  data: function() {
    return {
      cancelPayment: {},
      cancelPaymentIndex: null,
    }
  },

//-----

  computed: {
    ...mapState([
      'user'
    ]),
    cancelPaymentDate: function() {
      if (this.cancelPayment.date) {
        return getFullMDY(this.cancelPayment.date);
      }
    },
  },

//-----

  methods: {
    resetCancelPayment: function() {
      this.cancelPayment = {};
      this.cancelPaymentIndex = null;
    },
    onCancelPayment: function(i) {
      this.cancelPayment = this.user.billing.scheduledPayments[i];
      this.cancelPaymentIndex = i;
    },

    onConfirmCancel: function() {
      this.$store.commit('cancelScheduledPayment', this.cancelPaymentIndex);
      this.resetCancelPayment();

      if (!this.user.billing.scheduledPayments.length) {
        this.$router.push('/billing');
      }
    },
    onKeepPayment: function() {
      this.resetCancelPayment();
    }
  },

}

</script>



