  .payment-description {
    padding: 24px 16px;
    border-bottom: 1px solid $kite-gray-2;

    p {
      margin: 0;
    }
  }
  .payment-list {
    padding: 24px 16px;

    .payment-item {
      margin: 0 0 32px;

      .summary-card {
        margin-bottom: 4px;
      }
      .kite-btn {
        margin: 0;
        font-weight: 500;

        &:focus {
          outline: 0;
        }
      }
    }
  }

  .confirm-cancel-modal {
    position: fixed;
    display: flex;
    align-items: center;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 24px 16px;
    background-color: rgba(0,0,0, 0.5);

    .cancel-modal {
      padding: 24px 16px;
      text-align: center;
      border-radius: 4px;
      background-color: $kite-white;
      box-shadow: 1px 2px 12px $kite-gray-3;
    }
  }
}

</style>
