
<template>

  <div class="view view-charge-details">

    <div v-if="currentDetail" class="detail-items">

      <div
        v-for="detail in currentDetail.details"
        @click="selectDetail(detail)"
        class="detail-item"
      >
        <div class="detail-title">{{detail.title}}</div>
        <div class="detail-value">{{detail.value.toFixed(2)}}</div>
      </div>

    </div>

    <div v-if="currentDetail" class="detail-total">
      <div class="detail-title">Total</div>
      <div class="detail-value">${{currentDetail.value.toFixed(2)}}</div>
    </div>

    <div
      class="detail-modal-bg"
      :class="{'show': showDetailModal}"
      @click="hideDetailModal()"
    >
      <div class="detail-modal-dialog">
        <div class="detail-modal-title">
          {{detailModalTitle}}
        </div>
        <div class="detail-modal-content">
          {{detailModalDescription}}
        </div>
        <button class="close-btn kite-btn kite-btn-primary kite-btn-block">
          Close
        </button>
      </div>
    </div>

  </div>

</template>



<script>

import { mapState } from 'vuex';


export default {
  name: 'charge-details',
  data: function() {
    return {
      showDetailModal: false,

      detailModalTitle: '',
      detailModalDescription: '',
    }
  },

//-----

  computed: {
    ...mapState([
      'currentDetail',
    ]),
  },

//-----

  methods: {
    selectDetail: function(detail) {
      if (detail.description) {
        this.showDetailModal = true;

        this.detailModalTitle = detail.title;
        this.detailModalDescription = detail.description;
      }
    },

//-----

    hideDetailModal: function() {
      this.showDetailModal = false;
    },

//-----

  },

  mounted: function() {
    if (!this.currentDetail) {
      this.$router.push('/bill-details');
    }
  }
}

</script>



<style lang="scss">

.view-charge-details {
  background-color: $kite-white;

  .detail-items {
    padding: 12px 16px 0;
  }
  .detail-item {
    display: flex;
    padding: 16px 0 14px;
    border-bottom: 1px solid $kite-gray-2;

    &:last-child {
      border-bottom: 0;
    }
  }
  .detail-title {
    flex: 1 1 0;
    padding-right: 8px;
    color: $kite-blue-2;
  }
  .detail-value {
    flex: 0 0 auto;
    font-weight: 700;
  }
  .detail-total {
    display: flex;
    padding: 22px 16px;
    border-top: 1px solid $kite-gray-2;

    .detail-title {
      color: $kite-black;
      font-weight: 700;
    }
  }

  .detail-modal-bg {
    opacity: 0;
    display: block;
    position: fixed;
    top: 100%;
    left: 100%;
    width: 0;
    height: 0;
    overflow: hidden;
    background-color: rgba(230,230,230, 0.8);
    transition:
      opacity 0.4s,
      top 0.1s 0.4s,
      left 0.1s 0.4s,
      height 0.1s 0.4s,
      width 0.1s 0.4s;

    &.show {
      opacity: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: opacity 0.4s;
    }

    .detail-modal-dialog {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100vw - 40px);
      height: auto;
      padding: 24px 20px 30px;
      text-align: center;
      background-color: $kite-white;
      border-radius: 4px;
    }
    .detail-modal-title {
      font-size: 24px;
      font-weight: 700;
      margin-bottom: 24px;
    }
    .detail-modal-content {
      font-size: 14px;
      line-height: 1.9em;
      margin-bottom: 30px;
    }
    .close-btn {
      margin: 0;
    }
  }
}

</style>
