
const months = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

const days = [
  'Sunday',
  'Monday',
  'Tuesday',
  'Wednesday',
  'Thursday',
  'Friday',
  'Saturday',
];

const today = new Date();
const billDay = new Date().setDate(26); // sets to 26th of current month
const billDate = new Date(billDay);


function getFullMonth(i) {
  return months[i];
}

function getFullDay(i) {
  return days[i];
}

function getTimeOfDay() {
  const hour = today.getHours();
  return (hour < 12) ? 'morning' : (hour < 17) ? 'afternoon' : 'evening';
}

function getMDY(utcDate) {
  return (utcDate.getUTCMonth()+1)+'/'+utcDate.getUTCDate()+'/'+utcDate.getUTCFullYear().toString().substring(2);
}

function getFullMDY(utcDate) {
  return `${getFullMonth(utcDate.getUTCMonth())} ${utcDate.getUTCDate()}, ${utcDate.getUTCFullYear()}`;
}

function getToday() {
  return `${getFullDay(today.getDay())}, ${getFullMonth(today.getUTCMonth())} ${today.getUTCDate()}`;
}

function isToday(utcDate) {
  return (utcDate.getUTCDate() === new Date().getUTCDate());
}

// TODO:
// - Rename the nextBilLDates to nextBillDueDates
// - Add better logic to make sure the dates are appropriate

function getNextBillDate() {
  return getMDY(billDate);
}

function getNextBillDateFull() {
  return getFullMDY(billDate);
}

function getNextBillDateUTC() {
  const nextDay = 4;
  const thisMonth = today.getUTCMonth();
  const thisYear = today.getUTCFullYear();
  const isDecember = (thisMonth === 11);

  const nextMonth = (isDecember) ? 0 : (thisMonth + 1);
  const nextYear = (isDecember) ? (thisYear + 1) : thisYear;

  return new Date(nextYear, nextMonth, nextDay);
}

function getNextBillDueDateUTC() {
  const nextDay = 14;
  const thisMonth = today.getUTCMonth();
  const thisYear = today.getUTCFullYear();
  const useThisMonth = (today.getDate() < nextDay);
  const isDecember = (thisMonth === 11);

  const dueMonth = (useThisMonth) ? thisMonth : (isDecember) ? 0 : (thisMonth + 1);
  const dueYear = (!useThisMonth && isDecember) ? (thisYear + 1) : thisYear;

  return new Date(dueYear, dueMonth, nextDay);
}

function getNextAutoPayDateUTC() {
  const nextDay = 25;
  const thisMonth = today.getUTCMonth();
  const thisYear = today.getUTCFullYear();
  const useThisMonth = (today.getDate() < nextDay);
  const isDecember = (thisMonth === 11);
  const isNovember = (thisMonth === 10);

  if (useThisMonth) {
    var dueMonth = (isDecember) ? 0 : (thisMonth + 1);
    var dueYear = (isDecember) ? (thisYear + 1) : thisYear;
  } else {
    var dueMonth = (isNovember) ? 0 : (isDecember) ? 1 : (thisMonth + 2);
    var dueYear = (isNovember || isDecember) ? (thisYear + 1) : thisYear;
  }

  return new Date(dueYear, dueMonth, nextDay);
}



export {
  days,
  getFullMonth,
  getFullDay,
  getTimeOfDay,
  getMDY,
  getFullMDY,
  getToday,
  isToday,

  getNextBillDate,
  getNextBillDateFull,
  getNextBillDateUTC,
  getNextBillDueDateUTC,
  getNextAutoPayDateUTC
};