
<template>

  <div class="view view-autopay-manage">

    <div class="autopay-status-bar">
      <div class="status-title">Auto Pay</div>
      <div class="status-value">Enrolled</div>
    </div>

    <div class="autopay-manage-body">
      <div class="manage-autopay-summary summary-card">
        <div class="success-row">
          <div class="success-label">Next Payment Date</div>
          <div class="success-value">{{nextPaymentDate}}</div>
        </div>
        <div class="success-row">
          <div class="success-label">Payment Method</div>
          <div
            class="success-value"
            :class="{'card-value': (user.billing.autopay.method.paymentType === 'card')}"
          >
            <span
              v-if="user.billing.autopay.method.paymentType === 'card'"
              class="card-logo"
            >
              <card-logos
                :cardType="user.billing.autopay.method.cardType"
                :cardValid="true"
                :useGrey="true"
              />
            </span>
            {{methodValue}}
          </div>
        </div>
      </div>

      <div class="autopay-actions">
        <kite-button
          size="full"
          :onClick="() => canShowPaymentMethod = true"
        >
          Edit Auto Pay Method
        </kite-button>
        <kite-button
          type="standalone-link"
          size="full"
          :onClick="cancelAutoPay"
        >
          Cancel Auto Pay
        </kite-button>
      </div>
    </div>


<!-- Modals -->

    <make-payment-method
      :show="canShowPaymentMethod"
      :onClose="() => canShowPaymentMethod = false"
      :onSubmit="submitUpdate"
    />

    <transition name="fade">
      <div
        v-if="submitting"
        class="payment-loader"
      >
        <div class="payment-spinner-container">
          <kite-spinner />
        </div>
      </div>
    </transition>

 
    <transition name="fade-slow">
      <make-payment-success
        v-if="showSuccessUpdate"
        :isAutopay="true"
        headerTitle="Manage Auto Pay"
        contentTitle="Payment Method Updated"
        contentCopy="Thanks for your enrollment in Auto Pay. We sent a confirmation to the email address on file."
        :paymentAmount="paymentAmount"
        :paymentMethod="paymentMethod"
      />
      <make-payment-success
        v-else-if="showSuccessCancel"
        :isAutopay="true"
        headerTitle="Manage Auto Pay"
        contentTitle="Auto Pay Canceled"
        contentCopy="Line under that: You've canceled your Auto Pay enrollment. Remember to make one-time payments for any balance due on your account."
        :hideSummary="true"
      />
    </transition>

  </div>

</template>



<script>

import {
  KiteButton,
  KiteSpinner,
} from '@kite/vue-kite';
import MakePaymentMethod from '../components/make-payment-method.vue';
import MakePaymentSuccess from '../components/make-payment-success.vue';
import CardLogos from '../components/card-logos.vue';

import { mapState } from 'vuex';
import {
  getMDY,
  getNextBillDueDateUTC,
} from '../utils/time';


export default {
  name: 'autopay-manage',
  components: {
    KiteButton,
    KiteSpinner,
    MakePaymentMethod,
    MakePaymentSuccess,
    CardLogos
  },

  data: function() {
    return {
      submitting: false,
      showSuccessUpdate: false,
      showSuccessCancel: false,
      canShowPaymentMethod: false,

      paymentMethod: '',
      paymentAmount: '',
      nextPaymentDate: '12/26/18',
    }
  },

//-----

  computed: {
    ...mapState([
      'user'
    ]),

    methodValue: function() {
      const { method } = this.user.billing.autopay;

      if (method.paymentType === 'card') {
        const { cardVal } = method;
        return cardVal.substring(cardVal.length-4);

      } else {
        const {
          paymentType,
          accountNumber
        } = method;
        return `${paymentType.charAt(0).toUpperCase() + paymentType.slice(1)} x${accountNumber.substring(accountNumber.length-4)}`;
      }
    },
  },

//-----

  methods: {
    submitUpdate: function(payment) {
      this.paymentMethod = payment;
      this.$store.commit('autopayEnroll', payment);
      this.submitting = true;

      window.setTimeout(() => {
        this.showSuccessUpdate = true;
        this.submitting = false;
      }, 2000);
    },
    cancelAutoPay: function() {
      this.submitting = true;

      window.setTimeout(() => {
        this.showSuccessCancel = true;
        this.submitting = false;
        this.$store.commit('cancelAutopay');
      }, 2000);
    },
  },

//-----

  mounted: function() {
    if (this.user) {
      this.nextPaymentDate = getMDY(getNextBillDueDateUTC());
      this.paymentAmount = this.user.billing.currentBalance.toFixed(2);
    }
  }

}

</script>



<style lang="scss">

.view-autopay-manage {
  padding-top: 24px;

  .autopay-status-bar {
    display: flex;
    justify-content: space-between;
    margin-bottom: 32px;
    padding: 12px 16px 11px;
    background-color: $kite-white;
    border-top: 1px solid $kite-gray-2;    
    border-bottom: 1px solid $kite-gray-2;

    div {
      display: inline-block;
      flex: 0 0 auto;
      font-weight: 500;
    }
    .status-value {
      color: $kite-gray-3;
    }
  }
  .autopay-manage-body {
    padding: 0 16px 24px;
  }
  .autopay-actions {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 0 16px 12px;

    .kite-btn-standalone-link {
      font-weight: 500;
    }
    .kite-btn-block {
      margin-bottom: 12px;

      &:last-child {
        margin: 0;
      }
    }
  }

  .payment-loader {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: rgba(255,255,255, 0.3);

    .payment-spinner-container {
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      height: 50px;
      width: 50px;
    }
  }
}

</style>
