<script>

import {
  KiteButton,
  KiteSwitch
} from '@kite/vue-kite';
import CompareCard from '../components/compare-card.vue';

// import Bills from '../data/bills';
import { mapState } from 'vuex';
import {
  // getNextBillDateFull,
  getNextBillDueDateUTC,
  getMDY,
  isToday,
} from '../utils/time';


export default {
  name: 'billing',
  components: {
    KiteButton,
    KiteSwitch,
    CompareCard,
  },

//-----

  data: function() {
    return {
      // bills: Bills,
      // latestBill: Bills[0],
      nextBillDate: getMDY(getNextBillDueDateUTC()),
    }
  },

//-----

  computed: {
    ...mapState([
      'user'
    ]),
    recentPayment: function() {
      const {transactions} = this.user.billing;

      if (transactions.length) {
        return transactions.find((payment) => {
          return isToday(payment.date);
        });
      } else {
        return false;
      }
    },
  },

//-----

  methods: {
    goToDetails: function() {
      this.$router.push('/billing/details');
    },
    onPaperlessChange: function(e) {
      console.log('paperless toggle: ', e);
      this.$store.commit('togglePaperless');
    }
  }

}

</script>



